@import '../variables.scss';

/******************************
Fixed Bottom Menu
*******************************/

.bottom-menu{
  position: fixed;
  width: calc(100% - var(--bottom-menu-margin, 0px) * 2);
  left: 50%;
  bottom: 0;
  background-color: white;
  z-index: 100;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  border-width: 1px 0 0 0;
  transition: transform 400ms 350ms ease-out;
  //padding: 0 8px;
  scrollbar-width: none;
  &::-webkit-scrollbar{
    display: none;
  }
  ul, li {
    all: unset;
    margin: 0;
    padding: 0;
    list-style: none;
  }
  > ul {
    display: flex;
    align-items: center;
    > li {
      all: unset;
      flex-grow: 1;
      margin: 0;
      > a {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 100%;
        padding: 0 10px;
        min-width: 50px;
        height: var(--bottom-menu-height, 55px);
        transition: all 0.075s ease-out;
        position: relative;
        &::before {
          width: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          line-height: 1;
          margin: 2px 0;
        }
      }
      + li {
        border-width: 0 0 0 1px;
      }
      .links-text {
        white-space: nowrap;
        position: relative;
        top: -2px;
      }
      .count-badge{
        position: absolute;
        top: 50%;
        transform: translate3d(calc(75% + var(--x, 0px)), calc(-100% + var(--y, 0px)), 0);
      }
    }
  }
}

.firefox{
  .side-menu{
    > ul {
      > li{
        min-width: 30px;
      }
    }
  }
}

.side-menu{
  position: fixed;
  z-index: 200;
  right: 0;
  overflow: hidden;
  ul, li {
    margin: 0;
    padding: 0;
    list-style: none;
  }
  > ul {
    display: flex;
    flex-direction: column;
    justify-content: center;
    border-radius:inherit;
    > li {
      flex-grow: 1;
      > a {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 100%;
        padding: 5px 10px;
        transition: all 0.075s ease-out;
        position: relative;
        background-color: white;
        &::before {
          //width: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          line-height: 1.1;
          margin: 2px 4px;
          transition: all 0.075s ease-out;
        }
      }
      &:first-of-type{
        border-top-left-radius: inherit;
        border-top-right-radius: inherit;
      }
      &:last-of-type{
        border-bottom-left-radius: inherit;
        border-bottom-right-radius: inherit;
      }
      &:only-of-type{
        border-radius:inherit;
      }
      + li {
        border-width: 1px 0 0 0;
      }
      .links-text {
        position: relative;
        top: -1px;
        transition: all 0.05s ease-out;
      }
      .count-badge{
        position: absolute;
        right: 0;
        top: 0;
      }
    }
  }
}

.popup-open .side-menu{
  margin-inline-end: var(--scrollW, 0);
}



