@import '../variables.scss';

/******************************
 Links Menu
*******************************/

.links-menu {
  //overflow: hidden; //why?
  .module-body {
    --display:block;
    --column-width: auto;
    display: var(--display);
    flex-wrap: wrap;
    border-radius: inherit;
    width: -moz-available;
    width: -webkit-fill-available;
    width: stretch;
    align-items: baseline;
    //margin: calc(0px - var(--item-gap, 0px));
    break-inside: avoid;
    -moz-column-fill: balance;
    column-fill: balance;
  }

  ul, li {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  .menu-item {
    break-inside: avoid;
    -moz-column-fill: balance;
    column-fill: balance;
    position: relative;
    transition: all 0.08s ease-out;
    display: flex;
    align-items: var(--align);
    max-width: max-content;
    margin: calc(var(--item-gap, 0px));
    flex-direction: column;

    &::before{
      content: "";
      min-height: 0;
      min-width: 1px;
      transition: all 75ms ease;
      border-width: 1px 0 0 0;
      margin: calc(var(--item-gap,0px)/2) 0 calc(var(--item-gap,0px)/2) 0;
    }

    &:first-child{
      &::before{
        visibility: hidden;
        margin: 0;
      }
    }

    &.dropdown > a::after {
      display: none;
    }

    a {
      display: inline-flex;
      align-items: center;
      position: relative;
      flex-grow: 1;
      .links-text{
        transition: all 75ms ease;
      }

      .menu-label {
        //right: auto;
        //left: auto;
        //top: auto;
        //margin:0 5px;
        //transform: none !important;
        //position: relative;
        //&::before{
        //  border-right-color: currentColor;
        //}
        //&::after{
        //  border-top-color: transparent;
        //}
      }

      &::before {
        line-height: 1;
      }

      &::after {
        display: none !important;
      }

      &:not([href]) {
        cursor: default;
      }
    }
  }
}

footer .links-menu .module-title {
  &::before {
    position: absolute;
    right: 0;
    content: "\eb85";
    display: none;
    font-size: 18px;
    font-family: icomoon, sans-serif;
    top: 50%;
    transform: translateY(-50%);
  }
  &.closed{
    &::before{
      content: "\eba0";
    }
  }
}

//Fader

.links-menu{
  .module-body{
    //&::before,
    &::after {
      content: "";
      display: none;
      opacity: 1;
      position: absolute;
      pointer-events: none;
      top: 0;
      height: 100%;
      transition: opacity 200ms ease-out;
      --fader-color: hsla(var(--j-color-scheme-body-background-h), var(--j-color-scheme-body-background-s), calc(var(--j-color-scheme-body-background-l) - 0%), calc(var(--j-color-scheme-body-background-a) * 1));
      width: 80px;
      z-index: 2;
    }

    //&::before {
    //  inset-inline-start: 0;
    //  background-image: linear-gradient(-90deg, rgba(255, 255, 255, 0) 0, var(--fader-color, white) 90%, var(--fader-color, white));
    //  border-start-start-radius: inherit;
    //  border-end-start-radius: inherit;
    //}

    &::after {
      inset-inline-end: 0;
      background-image: linear-gradient(90deg, rgba(255, 255, 255, 0) 0, var(--fader-color, white) 90%, var(--fader-color, white));
      border-start-end-radius: inherit;
      border-end-end-radius: inherit;
    }

    &:not([style]),
    &[style*="--scroll-offset: 0px;"]{
      &::before{
        display: none;
      }
    }
    &.no-scroll-end{
      &::after{
        display: none;
      }
    }
  }
}


