@import 'variables.scss';

/******************************
 Description
*******************************/

.route-product-category {
  #content {
    display: flex;
    flex-direction: column;
  }
}

.category-description {
  position: relative;

  &::after {
    content: "";
    display: table;
    clear: both;
  }

  + p {
    order: 40;
  }
}

.category-image {
  float: left;
  position: relative;

  img {
    max-width: 100%;
    height: auto;
  }
}

.category-text > p:last-of-type {
  margin-bottom: 0;
}

/******************************
 Subcategories
*******************************/

.refine-title {
  margin: 0;
}

.refine-items {
  display: flex;
  flex-wrap: wrap;
}

.refine-item {
  display: flex;

  a {
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 100%;
    min-width: 0;

    img {
      max-width: 100%;
      width: 100%;
      height: auto;
      align-self: center;
      border-radius: inherit;
      min-width: 0;
      transition: all 60ms ease-out;

      &:hover{
        transform: scale(var(--scale-hover, 1))
      }
      &:active{
        transform: scale(var(--scale-active, 1));
        transition: all 30ms ease;
      }
    }
  }
}

.refine-name {
  min-width: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  width: 100%;
  padding: 3px;
  transition: all 0.05s ease-out;
  flex-grow: 1;
  line-height: 1.2;

  .count-badge {
    transform: none;
  }
}

.refine-grid .refine-items.ipr-grid {
  .refine-item {
    padding: 10px;
  }
}

.refine-links {
  .refine-items:not(.auto-grid) {
    margin: calc(var(--refine-spacing, 10px) / 2 * -1);
    .refine-item {
      margin: calc(var(--refine-spacing, 10px) / 2);
      width: auto !important;
      padding: 0;

      a {
        //background-color: transparent;
        //padding: 0;
        img {
          display: none;
        }
      }

      &:not(:last-of-type) {
        &::after {
          margin: 0 7px;
        }
      }
    }
  }
}

/******************************
 Products Sort Bar
*******************************/

.products-filter {
  display: flex;
  flex-wrap: wrap;
  align-items: center;

  .select-group {
    display: flex;
    margin-inline-start: auto;

    label, select {
      border-width: 0;
    }

    .input-group {
      display: inline-flex;
      align-items: center;
      height: auto;

      .input-group-addon {
        width: auto;
        display: flex;
        align-items: center;
        border-radius: 0;
        height: 100%;
      }
    }

    .form-control {
      width: auto;
      min-width: 50px;
      //padding: 4px //why?
    }

    label {
      margin-bottom: 0;
    }
  }
}

.grid-list {
  display: flex;
  align-items: center;
  flex-wrap: nowrap;

  button, button::before {
    line-height: 1;
  }
}

.view-btn {
  background-color: transparent;
  position: relative;
}

.compare-btn {
  display: flex;
  align-items: center;
  position: relative;
}

.sort-by {
  margin-inline-end: 10px;
}

@media only screen and (max-width: 600px) {
  .products-filter {
    .select-group .input-group {
      .input-group-addon {
        display: none;
      }

      .form-control {
        max-width: 100px;
      }
    }
  }
}

.mac.webkit {
  .select-group {
    .input-group-addon {
      position: relative;
      z-index: 2;
    }

    .form-control {
      position: relative;
      left: -3px;
      z-index: 1;
    }
  }
}

/******************************
 Pagination
*******************************/

.pagination-results {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;

  &::before,
  &::after {
    display: none;
  }

  > div {
    width: auto;
    padding: 0;
    margin: 4px;
  }

  > div:first-child:not(:empty) + div {
    //margin: 5px 0;
  }

  .phone & {
    justify-content: center;

    > div {
      text-align: center;
      width: 100%;
    }

    .pagination {
      justify-content: center;

      > li {
        margin-bottom: 5px;
      }
    }
  }
}

.pagination {
  border-radius: 0;
  display: flex;
  flex-wrap: wrap;
  margin: calc(0px - var(--pagination-gap, 0px) / 2);

  > li {
    max-width: 50px;
    transition: all 0.08s ease-out;
    position: relative;
    display: inline-flex !important;

    > a,
    > span {
      border: none !important;
      line-height: 1;
      padding: 0;
      transition: all 0.05s ease-out;
      background: transparent;
      border-radius: inherit;
      width: 2em;
      height: 2em;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      margin: calc(var(--pagination-gap, 0px) / 2) !important;

      &::before {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: inherit;
        background: inherit;
      }
    }

    &:hover {
      z-index: 2;
    }

    &.active {
      a, span {
        background: transparent;
        pointer-events: none;
      }
    }

    &::before {
      content: "";
      border-width: 0 0 0 1px;
      border-color: transparent;
      border-style: solid;
      height: calc(100% - var(--pagination-gap, 0px));
      top: calc(var(--pagination-gap, 0px) / 2);
      position: relative;
      min-width: 1px;

      html[dir="rtl"] & {
        border-width: 0 1px 0 0;
      }
    }

    &:first-child, &:last-child {
      a, span {
        border-radius: 0;
      }
    }

    &:first-child {
      &::before {
        display: none;
      }
    }

    + li {
      margin-top: 0;
    }
  }

  > li:first-child a,
  > li:last-child a,
  a.prev,
  a.next {
    visibility: hidden;

    &::before {
      visibility: visible;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) scaleX(1);
    }
  }

  > li:last-child a, a.next {
    &::before {
      transform: translate(-50%, -50%) scaleX(-1);
    }
  }
}

/******************************
 Empty
*******************************/

.text-empty {
  padding: 10px;
}

/******************************
 Infinite Scroll
*******************************/

.ias-trigger,
.ias-spinner,
.ias-noneleft {
  width: 100%;
  text-align: center;

  em::before {
    font-size: 22px;
  }

  .auto-grid & {
    grid-column: 1/-1;
  }
}

.ias-trigger a {
  display: flex;
  align-items: center;
  justify-content: center;
}
