@import 'variables.scss';

/******************************
 Product Grid
*******************************/

.product-grid:not(.swiper-wrapper):not(.auto-grid),
.isotope-grid,
.module-products-list .product-list:not(.swiper-wrapper):not(.auto-grid) {
  display: flex;
  flex-wrap: wrap;
}

.product-layout {
  display: flex;
  position: relative;

  &::after {
    content: "";
    position: absolute;
    pointer-events: none;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    inset: 0;
    z-index: 0;
    --layout-scale: 1;
    --layout-scale-duration: 150ms;
    --layout-scale-proportion: .99;
    transform: translate3d(0, 0, 0) scale3d(1, 1, 1);
    transition: transform var(--layout-scale-duration) ease-out;
  }

  &:hover {
    z-index: 2;

    &::after {
      transform: translate3d(0, 0, 0) scale3d(var(--layout-scale), calc(var(--layout-scale) * var(--layout-scale-proportion)), 1);
    }
  }
}

.product-thumb {
  display: block;
  flex-wrap: wrap;
  flex-grow: 1;
  min-width: 0;
  z-index: 1;
  position: relative;
  transition: all 0.075s ease-out;
  //overflow: hidden; // why?
  transform-style: preserve-3d;
  --perspective: 800px;
  --flip-duration: 450ms;
  --flip-ease: ease-out;

  --fade-duration: 200ms;
  --fade-ease: ease;

  --scale-duration: 200ms;
  --scale-ease: ease-out;
  --scale: 1.1;

  .image {
    position: relative;
    overflow: hidden;

    &::before {
      content: "";
      position: absolute;
      width: 100%;
      max-height: 100%;
      left: 0;
      right: 0;
      bottom: 0;
      height: 100%;
      pointer-events: none;
      z-index: 1;
      transition: all 0.2s ease-out;

      .is-dragging & {
        pointer-events: auto;
      }
    }
  }

  .product-img {
    display: block;

    > div {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      transition: transform var(--scale-duration) var(--scale-ease);
    }

    img {
      pointer-events: none;
      transition: all var(--fade-duration) var(--fade-ease);
      width: 100%;
      height: auto;
      --drop-shadow-x: 0px;
      --drop-shadow-y: 0px;
      --drop-shadow-blur: 0px;
      --drop-shadow-color: transparent;
      --drop-shadow-x-hover: var(--drop-shadow-x);
      --drop-shadow-y-hover: var(--drop-shadow-y);
      --drop-shadow-blur-hover: var(--drop-shadow-blur);
      --drop-shadow-color-hover: var(--drop-shadow-color);
      filter: drop-shadow(var(--drop-shadow-x) var(--drop-shadow-y) var(--drop-shadow-blur) var(--drop-shadow-color));


      &.img-second {
        visibility: hidden;
        opacity: 0;
        position: absolute;
      }
    }
  }

  .product-labels {
    z-index: 3;
  }

  &:hover {
    z-index: 1;

    .has-second-image {
      img {
        visibility: hidden;

        &.img-second {
          visibility: visible;
          opacity: 1;
        }
      }
    }

    .product-img {
      img {
        filter: drop-shadow(var(--drop-shadow-x-hover) var(--drop-shadow-y-hover) var(--drop-shadow-blur-hover) var(--drop-shadow-color-hover));
      }
    }
  }

  .caption {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    position: relative;
  }

  .name {
    font-weight: normal;
    line-height: 1.3;
    margin: 0 0 7px 0;
    display: flex;
    align-items: center;
    max-width: 100%;
    //flex-grow: 1;

    a {
      display: block;
      text-align: center;
      padding-bottom: 1px;
    }
  }

  .description {
    margin: 0 0 auto 0;
    flex-grow: 1;
  }

  .price {
    margin-top: auto;
    display: flex;
    flex-direction: column;

    > div {
      flex-grow: 1;
      display: inline-flex;
      align-items: stretch;
      flex-wrap: wrap;
      max-width: 100%;
      margin: calc(0px - var(--price-gap, 0) / 2);

      span {
        line-height: 1;
        white-space: nowrap;
        display: flex;
        align-items: center;
        margin: calc(var(--price-gap, 0) / 2);
        flex-basis: calc((var(--price-stack, 0px) - 100%) * 9999);
      }
    }

    .price-tax {
      line-height: 1.5;
    }
  }

  .product-labels-price {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: var(--label-gap, 5px);
    max-width: max-content;

    .product-label {
      margin: 0;
    }
  }

  .price-tax {
    color: #999;
    display: block;
    font-size: 85%;
    margin-top: var(--price-gap, 0);
  }

  .countdown {
    position: absolute;
    left: 50%;
    transform: translateX(var(--transform-50-rtl, -50%));
    bottom: var(--countdown-bottom-offset, 0);
    pointer-events: none;
    z-index: 2;
    width: 100%;
  }

  .stats-group {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    flex-grow: 1;
    margin: calc(0px - var(--stats-gap, 0px) / 2);

    > span {
      margin: calc(var(--stats-gap, 0) / 2);
      flex-basis: calc((var(--stats-stack, 0px) - 100%) * 9999);
      display: flex;
      align-items: center;

      a {
        //white-space: normal;
        display: block;
        word-break: break-all;
      }
    }

    .stats-label + span {
      white-space: nowrap;
    }
  }
}

.stats-label {
  margin-inline-end: 4px;
  white-space: nowrap;

  html[dir="rtl"] & {
    margin-inline-start: 4px;
  }
}

.safari {
  .product-thumb {
    .product-img,
    .product-labels {
      //transform: translateZ(1px); // why // interferes with quickview btn ios
      transform: translate3d(0, 0, 0);
    }
  }
}

//Quickview
.product-layout {
  .quickview-button {
    position: absolute;
    z-index: 10;
    //overflow: hidden; //why?
    transform: translate3d(0, 0, 0) scale(1);
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    inset: 0;
    align-items: center;
    justify-content: center;
    --display: flex;
    display: var(--display);
    pointer-events: none;

    .btn-quickview {
      pointer-events: auto;
      visibility: var(--visibility);
      opacity: var(--opacity);
    }

    span {
      display: none;
    }
  }

  &:hover {
    .quickview-button {
      .btn-quickview {
        visibility: visible;
        opacity: 1;
        transform: scale(1) !important;
      }
    }
  }
}

.touchevents .product-layout .quickview-button {
  display: var(--touch-display, var(--display)) !important;

  .btn-quickview {
    visibility: visible !important;
    opacity: 1 !important;
    transform: scale(1) !important;
  }
}


//Buttons
.product-layout {
  .button-group,
  .wish-group,
  .extra-group {
    display: flex;
  }

  .btn-text {
    display: none;
  }

  .buttons-wrapper {
    z-index: 2;

    .button-group {
      position: static;
      transition: transform 0.25s ease-out;
    }

    a::before {
      line-height: 1;
    }
  }

  .rating {
    width: 100%;
    align-items: center;
    pointer-events: none;
    transition: all 0.1s ease-in-out;
    z-index: 2;
  }

  .rating-stars {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.1s ease-in-out;
  }

  .wish-group,
  .cart-group,
  .extra-group {
    position: relative;
    align-items: center;
    flex-wrap: wrap;
  }

  .image-group {
    display: block;
    //display:flex; // issues with auto-grid on safari mobile, image on the side
    //flex-direction:column;
  }

  .button-group {
    margin: calc(0px - var(--buttons-gap, 0px) / 2);

    > div {
      margin: 0;
    }
  }

  .cart-group {
    align-items: stretch;
    max-width: 100%;
    margin: calc(0px - var(--buttons-gap, 0px) / 2);

    > * {
      margin: calc(var(--buttons-gap, 0px) / 2);
    }
  }

  .wish-group {
    transition: all 0.1s ease-out;
    margin: calc(0px - var(--wishlist-gap, var(--buttons-gap, 0px)) / 2);

    .btn {
      flex-grow: 1;
      margin: calc(var(--wishlist-gap, var(--buttons-gap, 0px)) / 2);
    }
  }

  .extra-group {
    z-index: 10;

    > div {
      margin: calc(0px - var(--extra-gap, 0px) / 2);

      .btn {
        min-width: min-content;
        margin: calc(var(--extra-gap, 0px) / 2);
        flex-basis: calc((var(--extra-stack, 0) - 100%) * 9999);
      }
    }
  }
}

.product-grid {

  .image-group {
    overflow: hidden;
    min-width: 30%;
    border-top-right-radius: inherit;
    border-top-left-radius: inherit;
  }

  .caption {
    border-bottom-right-radius: inherit;
    border-bottom-left-radius: inherit;
  }

  .extra-group {
    > div {
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      justify-content: space-between;
      transition: all 0.2s ease-out;
    }
  }

  .buttons-wrapper {
    margin-top: auto;
  }

  .button-group {
    align-items: center;
    flex-wrap: wrap;
  }

  .wish-group {
    pointer-events: none;

    a {
      pointer-events: auto;
    }
  }

  &.flex-grid {
    --flex-grid-width: 250px;
  }
}


/******************************
Product List
*******************************/

.no-flexbox-gap {
  .product-list {
    .product-thumb {
      margin: calc(0px - var(--product-list-gap, 0px) / 2);

      > div {
        margin: calc(var(--product-list-gap, 0px) / 2);
      }
    }
  }
}

.product-list {
  .product-thumb {
    display: flex;
    flex-wrap: wrap;
    gap: var(--product-list-gap, 0px);
    --product-list-image-width: 30%;

    .image-group {
      overflow: hidden;
      width: var(--product-list-image-width);
    }

    .caption {
      width: calc(100% - var(--product-list-image-width) - var(--product-list-gap, 0px));

      > * {
        justify-content: flex-start;
      }

      .price {
        margin-top: 0;
        text-align: left;
      }
    }

    .rating {
      margin: 0;
      order: -1;
    }

    .name {
      margin: 0;
      padding-inline-start: 0;
      padding-top: 0;
      flex-grow: 0;

      a {
        text-align: left;
      }
    }

    .description {
      display: flex;
      padding-top: 0;
      padding-inline-start: 0;
      text-align: left;
      margin-bottom: 0;
      margin-top: 0;
    }

    .button-group {
      justify-content: flex-start;
      flex: 1;
    }

    .cart-group {
      justify-content: flex-start;
    }

    .caption {
      .buttons-wrapper {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-between;
        margin-top: auto;
        width: auto;
      }
    }

    .extra-group {
      width: auto;
      pointer-events: none;

      .btn {
        pointer-events: auto;
      }

      > div {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
      }
    }
  }

  .stats {
    order: -2;

    > span {
      padding-inline-end: 1em;
    }
  }
}

/******************************
 Side Products
*******************************/

.side-products {
  &:not(.swiper-wrapper) {
    display: flex;
    flex-wrap: wrap;
  }

  &.auto-grid {
    height: 100%;

    .product-layout {
      margin-top: 0;
    }
  }

  counter-reset: top-counters;

  .product-layout {
    display: block;
    counter-increment: top-counters;
  }
}

.side-product {
  &::before {
    content: counter(top-counters);
    font-size: 300%;
    line-height: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: inherit;
    height: 100%;
    padding: 8px;
    z-index: 2;
    top: 5px;
    width: var(--min-width, min-content);
  }
}

.side-product {
  height: 100%;

  .ipr-grid & {
    height: 100%;
  }

  position: relative;
  z-index: 1;
  display: flex;
  flex-wrap: wrap;

  gap: var(--mini-product-gap, 8px);

  .image {
    position: relative;
    max-width: 100%;
    flex-basis: max-content;

    &::before {
      position: absolute;
      z-index: 3;
      inset: 0;
      line-height: 1;
      font-size: 2em;
      pointer-events: none;
      display: flex;
      align-items: start;
      justify-content: start;
    }

    a {
      display: flex;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    .quickview-button {
      pointer-events: none;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      inset: 0;
      display: flex;
      align-items: center;
      justify-content: center;

      .btn-quickview {
        visibility: var(--visibility);
        opacity: var(--opacity);
        transform: scale(1);
        pointer-events: auto;
      }
    }
  }

  .name {
    line-height: 1.1;

    a {
      display: block;
    }
  }

  .caption {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    min-width: 0;
    flex-basis: 0;
  }

  .button-group {
    order: 20;
  }

  .rating-stars {
    display: inline-flex;
  }

  .price {
    display: flex;
    flex-wrap: wrap;
    gap: 5px;
    > span {
      line-height: 1.35;
    }
  }

  &:hover {
    .image .quickview-button .btn-quickview {
      visibility: visible;
      opacity: 1;
      transform: scale(1) !important;
    }
  }
}

.touchevents .side-product .image .quickview-button {
  display: var(--display);

  .btn-quickview {
    visibility: visible !important;
    opacity: 1 !important;
    transform: scale(1) !important;
  }
}

.side-products-blocks {
  display: grid;
  gap: var(--blocks-gap, 0px);
  grid-template-columns: repeat(auto-fill, minmax(min(var(--blocks-grid-width, 100%), 100%), 1fr));

  > div {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
  }
}

.mobile {
  .product-thumb .product-img > div,
  .module-gallery .module-item > a img,
  .post-thumb .image img,
  .manufacturer-thumb .image img {
    transform: scale(1) !important;
  }

  input {
    box-shadow: none;
  }
}

.module-products .product-list:not(.product-grid).auto-grid .product-layout:not(.swiper-slide) + .product-layout:not(.swiper-slide) {
  border: none;
  padding-top: 0;
  margin-top: 0;
}


