@import 'variables.scss';

/******************************
 General
*******************************/

@keyframes fade-in {
  from {
    opacity: 0;
    transform: translate3d(0, -5px, 0) scale(.95);
  }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0) scale(1);
  }
}

@keyframes scale-in {
  from {
    opacity: 0;
    transform: translate3d(0, 0, 0) scale(.85);
  }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0) scale(1);
  }
}

@keyframes slide-in {
  from {
    opacity: 0;
    transform: translate3d(0, 20px, 0);
  }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

.notification-wrapper {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 15000;
  align-items: center;
  pointer-events: none;
  display:flex;
  flex-direction:column;
}

.notification-wrapper-tc {
  align-items: center;
}

.notification-wrapper-tr {
  align-items: flex-end;
}

.notification-wrapper-center {
  align-items: center;
  justify-content: center;
  .notification{
    transform-origin: center;
    animation: scale-in .15s ease-out;
    &.fade-out {
      opacity: 0;
      margin: 0;
      visibility: hidden;
      pointer-events: none;
      transform: translate3d(0, 0, 0) scale(.9);
    }
  }
}

.notification-wrapper-bottom {
  justify-content: flex-end;
  align-items: center;
  .notification{
    animation: slide-in .2s ease-out;
    transition: all 0.2s ease-out;
    margin: 0;
    &.fade-out {
      margin: 0;
      visibility: hidden;
      pointer-events: none;
      opacity: 0;
      transform: translate3d(0, -5px, 0) scale(.95);
    }
  }
}

.notification {
  position: relative;
  background-color: white;
  pointer-events: auto;
  transition: all 0.3s ease-out;
  animation: fade-in .25s ease-out;
  transform-origin: top;
  border-radius: inherit;
  max-width: 400px;
  margin: 20px;
  margin-bottom: 0;
  padding: 20px;
  box-shadow: 0 15px 90px -10px rgb(0 0 0 / 20%);

  &.fade-out {
    visibility: hidden;
    pointer-events: none;
    opacity: 0;
    transform: translate3d(0, -7px, 0) scale(.95);
  }
  &.removed {
    transition: margin-top 0s;
  }
}

.notification-content {
  display: flex;
  align-items: flex-start;
  img{
    margin-inline-end: 10px;
  }
  &::after{
    content: "";
    display: table;
    clear: both;
  }
}

.notification-title {
  font-weight: 600;
  margin-bottom: 5px;
  &:empty{
    margin:0;
    display: none;
  }
}

.notification-buttons {
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom-left-radius: inherit;
  border-bottom-right-radius: inherit;
  a::before {
    margin-inline-end: 5px;
  }
  a::after {
    margin-inline-start: 5px;
  }
}

.notification-close {
  position: absolute;
  top: 0;
  right: 0;
  transition: all 0.075s ease-out;
  &::before {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
html[dir="rtl"]{
  .notification-close {
    right: auto;
    left: 0;
  }
}

.module-notification{
  width: 100%;
  max-width: 100%;
  display:flex;
  flex-direction:column;
  transform-origin: bottom;
  .notification-content{
    display: block;
  }
  .notification-close{
    align-items: center;
    justify-content: center;
    &::before{
      position: relative;
      top: initial;
      left: initial;
      transform: none
    }
  }
}

.notification-wrapper-message{
  &::before{
    content:"";
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,.6);
    position: absolute;
    pointer-events: auto;
  }
  &:empty::before{
    display: none;
  }
  .notification{
    display: flex;
    align-items: flex-start;
    border-radius: 5px;
    .notification-content{
      min-width: 200px;
      order: -1;
    }
  }
  .notification-text{
    color: #444;
  }
}


