@import 'variables.scss';

/******************************
 Forms
*******************************/
label {
  font-weight: normal;
  position: relative;
}

legend {
  border: none;
  color: inherit;
  width: auto;
  font-size: initial;
}

// From-control
.form-control,
.select-control,
.form-select{
  //border-radius: 0 !important; //why?
  border-color: transparent;
  line-height: 1.3;
  min-width: 36px;
  min-height: 36px;
  color: unset;
  padding: 5px 8px;
  background-color: unset;
  transition: all 0.07s ease-out;
  box-sizing: border-box;
  box-shadow: none;
  font-size: unset;
}

select {
  appearance: none;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  padding-inline-end: var(--element-padding-inline-end, 20px) !important;
  max-width: 100%;
  background-image: linear-gradient(120deg, rgba(255,255,255,0) 63%, var(--background-color, #fff) 63%), linear-gradient(-120deg, currentColor 63%, rgba(255,255,255,0) 63%) !important;
  background-repeat: no-repeat !important;
  background-size: 15px 6px !important;
  background-position: calc(100% - 4px) 50% !important;
}

html[dir="rtl"]{
  select{
    background-position: 4px 50% !important;
  }
}

// Input group
.input-group,
.input-group-btn{
  gap: var(--input-group-gap, 5px);
}

.input-group {
  display: flex;
  align-items: center;

  .input-group-btn{
    display: flex;
  }

  label {
    height: 100%;
  }

  .form-control {
    width: auto;
    //height: 100%; // not ok coupon code cart page
    flex-grow: 1;
  }

  .input-group-btn {
    align-self: stretch;
    width: auto;
  }

  .input-group-btn:last-child > .btn,
  .input-group-btn:last-child > .btn-group {
    margin: 0;
    flex-grow: 1;
    width: 100%;
    height: 100%;
  }

  &.date, &.time, &.datetime {
    .input-group-btn {
      max-width: max-content;
      flex-basis: auto;
    }
  }

  .btn {
    i {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
}


//Radio-checkbox remove absolute
.radio,
.checkbox,
.form-check {
  input[type='checkbox'],
  input[type='radio'] {
    position: relative;
    margin-inline-end: 5px;
    margin-inline-start: 0;
  }
}

.form-check{
  label{
    margin-bottom: 0;
  }
}

// Radio checkbox
input[type='checkbox'],
input[type='radio'] {
  min-width: 15px;
  min-height: 15px;
  margin-top: 0;
  margin-bottom: 0;

  + img {
    margin-inline-start: 5px;
  }
}

.radio,
.checkbox,
.form-check {
  padding: 0;
  margin: 0;
  display: inline-flex;
  align-items: center;
  width: 100%;

  .form-horizontal & {
    padding: 0;
  }

  label {
    padding: 0;
    display: inline-block;

    input[type="checkbox"],
    input[type="radio"] {
      position: relative;
      margin-inline-start: 0;
      margin-inline-end: 5px;
      vertical-align: middle;
      transform: translate3d(0, -1px, 0);

      html[dir="rtl"] & {
        margin-inline-end: 0;
        margin-inline-start: 5px;
      }
    }
  }

  a {
    padding: 0 4px;
  }

  + .radio, + .checkbox {
    margin-top: 0;
  }
}

.checkbox-inline,
.radio-inline,
.form-check-inline{
  display: flex;
  align-items: center;
  padding: 0;

  input {
    position: relative;
    margin-inline-start: 0;
    margin-inline-end: 5px;

    html[dir="rtl"] & {
      margin-inline-end: 0;
      margin-inline-start: 5px;
    }
  }

  .form-horizontal & {
    padding: 0;

    input {
      position: relative;
      margin-inline-start: 0;
      margin-inline-end: 5px;

      html[dir="rtl"] & {
        margin-inline-end: 0;
        margin-inline-start: 5px;
      }
    }
  }
}

.form-group {
  &.form-radio {
    > [class*=col-] {
      gap: var(--radio-gap, 0px);

      .radio > label, .radio-inline {
        margin: 0;
      }
    }
  }

  &.form-checkbox {
    > [class*=col-] {
      gap: var(--checkbox-gap, var(--radio-gap, 0px));

      .checkbox > label, .checkbox-inline {
        margin: 0;
      }
    }
  }
}

// Form group

.no-flexbox-gap {
  .form-group {
    .control-label {
      margin-top: var(--label-gap, 0px);
      margin-bottom: var(--label-gap, 0px);
    }
  }
}

// Control label
.control-label,
.form-label{
  padding: 0;
  margin: 0;
  line-height: 1.3;
  text-align: start;
  float: none;
  border-width: 0 0 1px 0;
  word-break: break-word;
  display: flex;
  align-self: stretch;
  align-items: flex-start;
  flex-wrap: nowrap;
  max-width: 100%;
  min-width: min(100%, max(var(--form-label-width), var(--form-label-min-width)));
  flex-basis: calc((var(--form-label-stack, 400px) - 100%) * 999);

  + * {
    max-width: 100%;
    min-width: 30px;
    flex-grow: 1;
    flex-basis: calc((var(--form-label-stack, 400px) - 100%) * 999);
  }

  + .input-group {
    .input-group-btn {
      flex-basis: calc((var(--button-group-stack, 350px) - 100%) * 999);
    }
  }
}

:not(.form-group) > label{
  margin-bottom: var(--label-gap, 0px);
}

.form-group {
  display: flex;
  flex-wrap: wrap;
  position: relative;
  margin: 0;
  transition: all 0.05s ease-out;
  gap: var(--label-gap, 0px);
  --form-label-min-width: 80px;
  --form-label-width: 20%;

  .form-horizontal & {
    margin: 0;
  }

  .control-label,
  .form-label {
    text-align: start;
  }

  &:empty {
    display: none !important;
  }

  > div {
    display: flex;
    flex-wrap: wrap;

    &.date, &.time, &.datetime {
      flex-wrap: nowrap;
    }
  }

  [class*="col-"] {
    padding: 0;

    > input[type=checkbox] {
      align-self: center;
      margin: 0;
    }
  }

  // Fieldset
  fieldset {
    width: 100%;

    + fieldset {
      margin-top: 20px;
    }
  }

  .label-info {
    display: inline-block;
  }

  &.push-option {
    .control-label {
      + [id*='input'],
      + [class*='input'] {
        //width: auto;
        //min-width: calc(100% - var(--form-label-width, 25%));
      }
    }
  }

  &::before,
  &::after {
    display: none !important;
  }
}

fieldset {
  &:not(:first-child) {
    margin-top: var(--fieldset-spacing, 0);
  }
}

//Required field
.required {
  .control-label,
  .form-label{
    &::before,
    &::after {
      content: "*";
      display: none;
      position: relative;
      font-family: inherit;
      line-height: 1.2;
    }

    &::after {
      display: block;
      margin-inline-start: 3px;
    }

    + div {
      &::before {
        display: none;
        content: "*";
        position: absolute;
        transform: translateX(-150%);
        font-family: inherit;
        line-height: 1.2;
        max-height: 10px;
      }
    }
  }
}

// Field error
.text-danger,
.text-success,
.invalid-feedback{
  width: 100%;
  margin-top: 5px;
}

.has-error {
  .form-control {
    box-shadow: none;
  }

  .control-label,
  .text-danger {
    color: inherit;
  }
}


/******************************
 Captcha
*******************************/

#input-captcha {
  max-width: 140px;

  + img {
    margin: 0 7px;
  }
}

.form-horizontal {
  .captcha {
    .form-group {
      margin: 0;
    }

    legend {
      display: none;
    }
  }

  .g-recaptcha {
    transform: scale(.92);
    transform-origin: 0 0;
  }
}

/******************************
 Date Time
*******************************/

.bootstrap-datetimepicker-widget {
  width: auto;
  --size: 32px;

  a[data-action] {
    padding: 0;
  }

  .timepicker-picker {
    td {
      width: var(--size);
      height: var(--size);
      line-height: var(--size);
      padding: 0 !important;

      span {
        width: var(--size);
        height: var(--size);
        line-height: var(--size);
      }
    }
  }

  tr td {
    position: relative;

    span {
      position: relative;

      &:hover {
        background: transparent;
        z-index: 2;
      }

      &.active {
        background-color: inherit;
        color: inherit;
        text-shadow: inherit;
        z-index: 3;
      }
    }

    &:hover, &.active {
      z-index: 2;
    }

    &.separator {
      color: inherit;
      background-color: transparent;
      box-shadow: none;

      &:hover {
        color: inherit !important;
        background-color: transparent !important;
        box-shadow: none !important;
      }
    }
  }

  .datepicker-months,
  .datepicker-years,
  .timepicker-picker {
    tr td {
      color: inherit !important;
      background-color: transparent !important;
      box-shadow: none !important;
    }
  }

  .picker-switch + .collapse {
    .timepicker-hours,
    .timepicker-minutes {
      margin-top: 10px;
    }
  }

  &::before {
    display: none !important;
  }

  .timepicker-picker .btn {
    margin: 10px 0;
  }

  .picker-switch .btn {
    margin-top: 10px;
  }
}

/******************************
 Stepper
*******************************/

.stepper {
  display: inline-flex;
  align-items: center;
  position: relative;
  overflow: hidden;
  border-width: 1px;
  transition: all 100ms ease-out;
  min-width: var(--stepper-min-width, 60px);
  height: var(--stepper-height, 36px);

  button {
    transition: inherit;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 2;
    top: 0;
    left: auto;
    right: 0;
    border-style: inherit;
    border-color: inherit;

    &.fa-angle-down {
      z-index: 3;
      top: 50%;
    }

    &::before {
      transition: inherit;
      top: -1px;
    }

    &:active {
      opacity: .8;
      background-color: #e8e8e8;
    }

    .no-touchevents & {
      &:hover {
        background-color: #e8e8e8;
      }
    }
  }

  input.form-control {
    width: 100%;
    padding: 0 !important;
    align-self: stretch;
    min-height: 100%;
    min-width: 25px;
    height: initial !important;
    border-radius: 0 !important;
    border: none !important;
    text-align: center;
    transition: all 0.05s ease-out;
  }

  .input-group & {
    + .input-group-btn {
      .btn {
        width: max-content;
      }
    }
  }
}

