@import 'variables.scss';

/******************************
 Wishlist
*******************************/

.route-account-wishlist {
  .table {
    margin-bottom: 0;
  }

  .table-responsive {
    table tr td {
      .btn {
        width: 36px;
        height: 36px;

        + .btn {
          margin-inline-start: 2px;
        }
      }

      &:nth-of-type(2) {
        min-width: 150px;
      }

      &:last-of-type {
        white-space: nowrap;
      }
    }
  }
}

.route-account-wishlist {
  .table-responsive {
    td {
      .btn {
        padding: .5em !important;
        font-size: 95%;

        span {
          display: none;
        }
      }
    }
  }
}

.route-account-transaction,
.route-account-reward,
.route-account-order,
.route-account-order-info {
  #content table:not(.table-order) > tbody > tr {
    td {
      padding: 18px;
    }
  }
}

.route-checkout-checkout #content .quick-checkout-wrapper .right .cart-section table tfoot td,
.route-checkout-cart .cart-total .table > tbody > tr > td {
    padding: 14px;
}

.route-account-address,
.route-account-address-delete {
  .table-responsive {
    td {
      padding: 18px;
      line-height: 1.8;

      .btn {
        + .btn {
          left: -8px;
        }
      }
    }
  }
}

[class*="route-account-"]:not(.route-account-address),
[class*="route-account-"]:not(.route-account-address-delete) {
  table .text-right {
    .btn {
      min-width: 35px;
      min-height: 35px;
      margin: 2px;

      i {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        &::before {
          font-size: 16px;
        }
      }
    }
  }

  h1 + p {
    margin-bottom: 15px;
  }
}

/******************************
 Cart Page
*******************************/

.panels-total {
  display: flex;
  justify-content: space-between;
}

.cart-total {
  display: flex;
  justify-content: flex-end;

  .table-responsive {
    width: 100%;
  }

  .table {
    margin: 0 !important;

    td + td {
      width: 15%;
      min-width: 130px;
      white-space: nowrap !important;
    }
  }
}

.cart-page .cart-bottom {
  margin: 0;
  min-width: min(100%, 300px);
  align-self: flex-start;
}

.route-checkout-cart {
  #shopping-cart {
    table {
      tbody {
        .td-qty {
          .input-group {
            display: flex;
            align-items: center;
            justify-content: center;
            margin-bottom: 0 !important;
          }

          .input-group-btn {
            width: auto;
            --input-group-gap: 8px;
          }
        }

        .td-name {
          min-width: 150px;
          white-space: normal;

          a {
            margin-bottom: 4px;
            display: inline-block;
          }

          small:first-of-type {
            display: inline-block;
            margin-top: 3px;
          }
        }
      }

      tr td {
        min-width: 90px;
        vertical-align: middle;
      }
    }
  }

  .cart-panels {
    flex: 1;

    p {
      margin-bottom: 16px;

      + * {
        margin-top: 0;
      }
    }
  }

  #error-not-found {
    #content {
      flex-direction: column;
    }
  }

  .cart-page {
    display: flex;
    flex-wrap: wrap;
    max-width: 100%;

    > * {
      max-width: 100%;
      flex-grow: 1;
    }
  }
}


/******************************
 Sitemap
*******************************/

.route-information-sitemap {
  #content > .row {
    display: flex;
    flex-wrap: wrap;
    margin: calc(0px - var(--sitemap-gap, 0px) / 2);

    &::before {
      display: none;
    }

    > div {
      flex-grow: 1;
      padding: calc(var(--sitemap-gap, 0px) / 2);

      > ul {
        padding: 0;

        ul {
          padding-inline-start: 20px;
        }

        li {
          list-style: none;
          margin-top: 6px;

          a {
            &::before {
              margin: 0 5px;
              display: inline-block;
            }
          }
        }
      }
    }
  }
}

/******************************
 Contact
*******************************/

.route-information-contact {
  .our-location {
    a > i {
      margin-inline-end: 5px;
    }

    background: transparent;
    border: none;
    box-shadow: none;
    margin: 0;

    .panel-body {
      padding: 0;
    }
  }

  .other-stores {
    margin-bottom: 20px;
  }
}

/******************************
 Manufacturer
*******************************/

.route-product-manufacturer {
  scroll-behavior: smooth;

  .brand-index {
    margin-bottom: 25px;
    position: sticky;
    z-index: 97;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    background: white;
    padding: 5px;

    p {
      margin: 0;
      overflow-x: auto;
      white-space: nowrap;
      scrollbar-width: none;

      &::-webkit-scrollbar {
        display: none;
      }
    }

    strong {
      white-space: nowrap;
      margin: 4px 20px 4px 0;
    }

    a {
      margin-inline-end: 20px;
    }
  }

  .manufacturer {
    margin-bottom: 25px;
    display: grid;
    gap: 20px;
    grid-template-columns: repeat(auto-fill, minmax(130px, 1fr));
  }

  &.desktop-header-active.header-sticky {
    &.sticky-default {
      .brand-index {
        top: calc(var(--sticky-top-distance, var(--desktop-header, 60px)) + var(--desktop-main-menu, 40px));
      }

      .manufacturer-letter {
        scroll-margin-top: calc(var(--desktop-sticky, var(--desktop-header, 60px)) + var(--desktop-main-menu, 40px) + var(--brand-index-height, 50px));
      }
    }

    &.sticky-full {
      .brand-index {
        top: calc(var(--desktop-top-bar, 35px) + var(--desktop-sticky, var(--desktop-header, 60px)) + var(--desktop-main-menu, 40px));
      }

      .manufacturer-letter {
        scroll-margin-top: calc(var(--desktop-top-bar, 35px) + var(--desktop-sticky, var(--desktop-header, 60px)) + var(--desktop-main-menu, 40px) + var(--brand-index-height, 50px));
      }
    }

    &.sticky-menu {
      .brand-index {
        top: var(--desktop-main-menu, 40px);
      }

      .manufacturer-letter {
        scroll-margin-top: calc(var(--desktop-main-menu, 40px) + var(--brand-index-height, 50px));
      }
    }
  }

  &.desktop-header-active:not(.header-sticky) {
    &.sticky-default {
      .brand-index {
        top: calc(var(--desktop-header, 60px) + var(--desktop-main-menu, 40px));
      }

      .manufacturer-letter {
        scroll-margin-top: calc(var(--desktop-header, 60px) + var(--desktop-main-menu, 40px) + var(--brand-index-height, 50px));
      }
    }

    &.sticky-full {
      .brand-index {
        top: calc(var(--desktop-top-bar, 35px) + var(--desktop-header, 60px) + var(--desktop-main-menu, 40px));
      }

      .manufacturer-letter {
        scroll-margin-top: calc(var(--desktop-top-bar, 35px) + var(--desktop-header, 60px) + var(--desktop-main-menu, 40px) + var(--brand-index-height, 50px));
      }
    }

    &.sticky-menu {
      .brand-index {
        top: var(--desktop-main-menu, 40px);
      }

      .manufacturer-letter {
        scroll-margin-top: calc(var(--desktop-main-menu, 40px) + var(--brand-index-height, 50px));
      }
    }
  }

  &.mobile-header-active.mobile-sticky-full:not(.header-mobile-1) {
    .manufacturer-letter {
      scroll-margin-top: calc(var(--mobile-top-bar, 35px) + var(--mobile-bar, 60px) + var(--mobile-search-bar, 0px) + (var(--mobile-search-padding, 7px) * 2));
    }
  }

  &.mobile-header-active.mobile-sticky-default:not(.header-mobile-1) {
    .manufacturer-letter {
      scroll-margin-top: calc(var(--mobile-bar, 60px) + var(--mobile-search-bar, 0px) + (var(--mobile-search-padding, 7px) * 2));
    }
  }

  &.mobile-header-active.mobile-sticky-full.header-mobile-1 {
    .manufacturer-letter {
      scroll-margin-top: calc(var(--mobile-top-bar, 35px) + var(--mobile-bar, 60px));
    }
  }

  &.mobile-header-active.mobile-sticky-default.header-mobile-1 {
    .manufacturer-letter {
      scroll-margin-top: calc(var(--mobile-bar, 60px));
    }
  }
}

.image-card {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  transition: all 0.15s ease;
  text-align: center;
  line-height: 1.15;

  img {
    width: 100%;
    margin-bottom: 6px;
  }

  span {
    display: block;
  }
}

.route-account-return-info {
  #content > * + * {
    margin-top: 20px;
  }
}

/******************************
 Compare
*******************************/

.route-product-compare {
  .compare-buttons {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    padding: 7px 0;

    .btn {
      flex-grow: 1;
      margin: 4px;

      &::before {
        display: none;
      }
    }
  }

  table #content {
    table {
      td {
        width: 20%;
        min-width: 150px;
        white-space: normal;
      }

      td:not(:first-of-type) {
        min-width: 200px;
      }
    }

    tbody:last-of-type tr:last-child td {
      border-top-width: 1px;
      padding: 0;
    }

    .table-bordered > tfoot > tr > td {
      border: 0;
    }
  }
}

.route-product-compare #content table {
  > tbody > tr > td:first-child {
    white-space: nowrap;
  }

  > tfoot > tr > td {
    border-left: 0;
    border-right: 0;
  }
}

/******************************
 Search
*******************************/

.route-product-search {
  #content {
    .search-form {
      .inputs {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        --split: 70%;
        --gap: 20px;
        --min-width: 200px;
        gap: var(--gap, 20px);
        margin-bottom: 10px;
      }

      input[type='text'] {
        flex-grow: 1;
        flex-basis: clamp(var(--min-width, 250px), calc(var(--split, 60%) - var(--gap, 20px) / 2), 100%);
        max-width: 100%;
      }

      select {
        max-width: 100%;
        flex-grow: 1;
        flex-basis: clamp(var(--min-width, 250px), calc(100% - var(--split, 60%) - var(--gap, 20px) / 2), 100%);
      }
    }

    > h2:last-child {
      display: none !important;
    }

    > h2:last-of-type {
      margin-top: 20px
    }

    > .checkbox {
      margin-bottom: 5px;
    }
  }
}

.route-account-order-info {
  .text-left:first-child {
    min-width: 150px;
  }

  table tr:first-of-type td {
    line-height: 1.7;
  }

  .text-right {
    b {
      white-space: nowrap;
    }
  }
}

.route-information-information {
  #content {
    display: flex;
    flex-direction: column;
  }

  .content {
    flex: 1;
  }
}
