@import '../variables.scss';
@import '../easings.scss';

/******************************
 Mobile Wrapper
*******************************/

.mobile-container {
  display: none;
  flex-direction: column;
  position: fixed;
  z-index: 1300;
  max-width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  transform: translate3d(-100%, 0, 0);
  transition: all .4s $easeInOutCubic;
  will-change: transform;
  background-color: white;
  overflow-y: auto;

  .mobile-wrapper-header {
    order: -2;

    //+ div {
    //  //overflow-y: auto;
    //  overflow-x: hidden;
    //  //height: calc(100% - var(--mobile-wrapper-header, 40px));
    //}
  }

  .bottom-menu {
    transform: translate3d(0, 0, 0);
    margin: 0 var(--bottom-menu-margin-mobile-menu, 0px) var(--bottom-menu-margin-mobile-menu, 0px) var(--bottom-menu-margin-mobile-menu, 0px);

    ~ .mobile-main-menu-wrapper > .main-menu {
      margin-bottom: calc(var(--bottom-menu-height, 55px) + var(--bottom-menu-margin-mobile-menu, var(--bottom-menu-margin, 0px)));
    }
  }

  .desktop-mobile-main-menu-wrapper:not(:empty),
  .mobile-main-menu-wrapper:not(:empty) {
    //height: 100%;
  }
  .menu-item > .collapse > .j-menu > .menu-item.dropdown{
    flex-wrap: wrap;
    > div{
      width: 100%;
    }
  }
}

html[class*="-container-open"] {
  .mobile-container {
    display: flex;

    &.animating {
      transform: translate3d(0, 0, 0) !important;
    }

    &.mobile-cart-content-container {
      transform: translate3d(100%, 0, 0);
      left: auto;
      right: 0;
    }
  }
}

// Site Overlay

.site-wrapper {
  &::before {
    content: "";
    position: fixed;
    z-index: 1299;
    left: 0;
    top: -200px;
    width: 100%;
    height: calc(100vh + 200px);
    pointer-events: none;
    opacity: 0;
    visibility: hidden;
    will-change: opacity;
    transition: all 0.2s;
    display: none;
  }
}

.mobile-menu-active,
.mobile-header-active,
.mobile-filter-active {
  .site-wrapper {
    &::before {
      display: block;
    }
  }
}


// Close Overlay
html:not(.popup).mobile-overlay {
  body {
    overflow-y: hidden;
    width: calc(100vw - var(--scrollW, 0px));
  }

  .site-wrapper {
    pointer-events: none;
    touch-action: none;
  }

  .site-wrapper::before {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
    background: rgba(0, 0, 0, .5);
  }
}

.android.chrome41 {
  &.mobile-header-active .site-wrapper {
    &::before {
      display: none;
    }
  }

  &.mobile-header-active.mobile-overlay .site-wrapper {
    &::before {
      display: block;
    }
  }
}

/******************************
 Mobile Wrapper Header
*******************************/

.mobile-wrapper-header {
  position: relative;
  width: 100%;
  min-height: var(--mobile-wrapper-header, 40px);
  //z-index: 1; why?
  display: flex;
  align-items: center;
  min-width: 0;

  > span {
    height: 100%;
    align-items: center;
    font-size: 17px;
    display: flex;

    &:not(:empty) {
      padding-inline-start: calc(12px + var(--offset-left, 0px));
      padding-inline-end: calc(12px + var(--offset-right, 0px));
    }

    span {
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }

    &::after {
      font: inherit;
      margin-inline-start: calc(12px + var(--offset-left, 0px));
      margin-inline-end: calc(12px + var(--offset-right, 0px));
    }
  }

  > a {
    height: 100%;
    min-width: var(--mobile-wrapper-header-close, var(--mobile-wrapper-header, 30px));
    flex-grow: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 25px;
    cursor: pointer;
    transition: transform 80ms ease;
    margin-inline-start: auto;

    &::before {
      content: "\e5cd";
      font-family: $icons;
      line-height: 1;
      aspect-ratio: 1;
    }

    &:active {
      transform: scale(.95);
    }
  }

  .mobile-wrapper-top-menu {
    min-width: 0;
    display: flex;
    align-items: center;
    position: relative;

    div.top-menu {
      padding-inline-end: 0;

      > .j-menu {
        > .dropdown {
          > .j-dropdown {
            top: calc(50% + var(--element-height, 0px) / 2);
          }

          &:last-child {
            padding-inline-end: var(--element-padding-inline-end, 0px);
          }
        }
      }
    }

    &::after {
      content: "";
      display: none;
      position: absolute;
      pointer-events: none;
      --element-padding-inline-end: inherit;
      --background-color: inherit;
      width: 30px;
      background-image: linear-gradient(90deg, rgba(255, 255, 255, 0) 0, var(--fader-color, var(--background-color)) 70%, var(--fader-color, var(--background-color)));
      height: 100%;
      top: 0;
      right: 0;
      z-index: 2;
      transition: opacity 200ms ease-out;
    }

    &.has-scroll {
      &::after {
        display: block;
      }
    }

    &.no-scroll {
      &::after {
        display: none;
      }
    }

    &:empty {
      display: none;
    }
  }

  .mobile-wrapper-top-menu,
  .language-currency, {
    height: 100%;
  }

  .language-currency {
    flex-grow: 1;
  }
}

html[dir="rtl"] {
  .mobile-wrapper-header {
    > span {
      &:not(:empty) {
        padding-inline-end: calc(12px + var(--offset-left, 0px));
        padding-inline-start: calc(12px + var(--offset-right, 0px));
      }

      &::after {
        margin-inline-end: calc(12px + var(--offset-left, 0px));
        margin-inline-start: calc(12px + var(--offset-right, 0px));
      }
    }

    > a {
      margin-inline-start: 0;
      margin-inline-end: auto;
    }

    .mobile-wrapper-top-menu {
      div.top-menu {
        padding-inline-start: 0;
        padding-inline-end: var(--element-padding-inline-end, 0px);

        > .j-menu {
          > .dropdown {
            &:last-child {
              padding-inline-end: 0;
              padding-inline-start: var(--element-padding-inline-end, 0px);
            }
          }
        }
      }
    }
  }
}

/******************************
 Mobile Filter
*******************************/

.mobile-filter-wrapper {
  height: calc(100% - var(--mobile-wrapper-header, 40px));
  overflow-y: auto;
  overflow-x: hidden;

  .module-filter {
    padding: 15px;

    .module-title {
      display: none;
      padding: 0;
      margin: 0;
    }
  }
}

.mobile-filter-container {
  .mobile-wrapper-header {
    .reset-filter.btn {
      position: absolute;
      left: 50%;
      transform: translate3d(-50%, 0, 0);
    }
  }
}

.mobile-filter-container-open {
  .journal-loading-overlay {
    position: fixed;
    top: 0;
    left: 0;
  }
}


/******************************
 Mobile Main Menu
*******************************/

.desktop-mobile-main-menu-wrapper,
.mobile-main-menu-wrapper {
  .main-menu {

    border-width: 0;
    background-color: transparent;

    .j-menu > * {
      box-shadow: none !important;
    }

    .dropdown::after {
      display: none !important;
    }

    .j-menu {
      list-style: none;
      margin: 0;
      padding: 0;
      box-shadow: none !important;

      > .menu-item {
        //padding: 0;

        .open-menu {
          min-width: 40px;
          height: 100%;
          right: 0;
          border-radius: 0
        }

        > a {
          position: relative;
          justify-content: flex-start;
          padding: 0 10px;
          display: flex;
          align-items: center;

          &::after {
            display: none;
          }
        }
      }
    }

    .dropdown-menu {
      padding: 0;
      margin: 0;
      position: relative;
      opacity: 1;
      visibility: visible;
      transform: translateY(0) scale(1);
      width: 100%;
      display: none;

      > ul {
        box-shadow: none;

        li {
          > a {
            margin-bottom: 10px;
          }

          .dropdown-menu {
            left: 0;
            box-shadow: none;
          }
        }
      }

      &::before {
        display: none;
      }
    }

    .open > .dropdown-menu {
      display: block;
    }

    .multi-level .dropdown-menu ul {
      box-shadow: none;

      li.dropdown {
        display: table;
        width: 100%;
      }
    }

    .mega-menu .main-menu-item > a + div {
      border-width: 1px 0 0 0;
      border-color: inherit;
      border-style: inherit;
    }

    > .main-menu {
      > div {
        &:first-of-type {
          border-top-width: 0;
        }

        &:last-of-type {
          border-bottom-width: 1px;
        }
      }
    }

    .j-menu {
      border-top-width: 0 !important;
      border-bottom-width: 0 !important;
    }
  }
}

/******************************
 Desktop menu off canvas
*******************************/

.desktop-mobile-main-menu-wrapper {
  .dropdown > a::after {
    display: none !important;
  }

  .j-menu {
    box-shadow: none !important;
  }
}

.mobile-main-menu-wrapper,
.desktop-mobile-main-menu-wrapper {
  &:empty {
    display: none;

    + .module-grid {
      height: 100%;
      overflow-y: auto;
      overflow-x: hidden;

      > .grid-row {
        //height: 100%;

        > .grid-cols-wrapper {
          height: 100%;

          > .grid-cols {
            > .grid-col {
              > .grid-items {
                > .grid-item {

                }
              }
            }
          }
        }
      }
    }
  }

  .tab-container,
  .tab-content {
    &::before {
      display: none !important;
    }
  }
}


/******************************
 Mobile Cart
*******************************/

.mobile-cart-content-container {
  .mobile-cart-content-wrapper.cart-content {
    max-height: 100%;
    height: calc(100% - var(--mobile-wrapper-header, 40px));

    ul {
      height: 100%;
      border-radius: 0;
      background: transparent;
    }

    .cart-products {
      max-height: 100%;
      flex-grow: 1;

      td {
        min-width: min-content;
        width: 100%;

        &.td-image {
          width: 20%;
        }

        &.td-qty {
          padding: 0 !important;
          min-width: initial;
        }
      }
    }

    .cart-totals {
      position: relative;
    }
  }
}

