@import '../variables.scss';


.category-grid:not(.auto-grid){
  display: flex;
  &:not(.swiper-wrapper){
    flex-wrap: wrap;
  }
}

.manufacturer-grid:not(.auto-grid){
  display: flex;
  &:not(.swiper-wrapper){
    flex-wrap: wrap;
  }
}

.category-thumb,
.manufacturer-thumb{
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  height: 100%;
  //overflow-y: hidden; // name over image
  transition: all 0.075s ease-out;
  position: relative;
  .caption{
    width: 100%;
    display:flex;
    flex-direction:column;
    z-index: 2;
    position: relative;
  }
  .description{
    //flex-grow: 1; //use on name
  }
  .button-group{
    //margin-top: auto; // why? used flex-grow on name
  }
  .image{
    width: 100%;
    position: relative;
    a{
      display: flex;
      align-items: center;
      justify-content: center;
      //overflow: hidden; // why?
      -webkit-backface-visibility: hidden;
      backface-visibility: hidden;
      transition: all 60ms ease-out;

      &:hover{
        transform: scale(var(--scale-hover, 1))
      }
      &:active{
        transform: scale(var(--scale-active, 1));
        transition: all 30ms ease;
      }
    }
    img{
      width: 100%;
      height: auto;
      transition: all 0.18s ease;
      border-radius: inherit;
    }
  }
  .name{
    max-width: 100%;
    top: 0;
    transition: all 0.075s ease-out;
    a{
      //width: stretch; why? interferes with count name position
      display: block;
    }
  }
}

.category-thumb{
  --image-width: 100%;
  --stack: 300px;
  --gap: 0;
  gap: var(--gap);
  flex-direction: column;
  flex-wrap: nowrap;

  > div{
    flex-basis: calc((var(--stack) - 100%) * 9999);
  }
  .image{
    min-width: min(100%, (var(--image-width)));
  }
  .caption {
    justify-content: center;
    flex-grow: 1;
    min-width: min(100%, (100% - var(--image-width) - var(--gap)));
    .name{
      align-items: center;
    }
  }
  .product-count{
    line-height: 1;
    align-items: center;

    span{
      display: inline-flex;
      align-items: center;
      justify-content: center;
      line-height: 1;
      min-width: 1em;
      min-height: 1em;
      aspect-ratio: 1;
      margin-inline-end: calc(var(--category-count-offset, 0) / 2);
    }
    &.product-count-below {
      margin: var(--category-count-offset, 0) 0;
    }
    &.product-count-image{
      position: absolute;
      inset-inline-end: var(--category-count-offset, 0);
      inset-block-start: var(--category-count-offset, 0);
      z-index: 2;
    }
    &.product-count-name{
      margin: 0 var(--category-count-offset, 0);
    }
  }
}

.manufacturer-grid:not(.auto-grid){
  display: flex;
  &:not(.swiper-wrapper){
    flex-wrap: wrap;
  }
}
.manufacturer-thumb{
  display: flex;
  flex-wrap: wrap;
  --image-width: 100%;
  --image-stack: 30px;
  --details-gap: 0;
  gap: var(--details-gap);

  .image{
    min-width: min(100%, max(1%, var(--image-width)));
    flex-basis: calc((var(--image-stack) - 100%) * 9999);
    display: flex;
    align-items: center;
    flex-grow: 1;
    width: 100%;
    border-radius: inherit;
    a{
      border-radius: inherit;
    }
  }
  .caption {
    //margin-top: auto; // why? auto-list align issue
    border-radius: inherit;
    flex-grow: 1;
    min-width: calc(max(1%, min(100%, 100% - var(--image-width))) - var(--details-gap));
    flex-basis: calc((var(--image-stack) - 100%) * 9999);
    align-items: center;
    justify-content: center;
  }
  .name{
    //width: 100%;
    //left: 50%;
    //transform: translate3d(-50%,0,0);
    border-radius: inherit;
  }
}

.module-manufacturers,
.module-categories{
  display: flex;
  flex-wrap: wrap;
  --title-width: 100%;
  --title-stack: 900px;

  > .module-title{
    min-width: min(100%, max(1%, var(--title-width)));
    flex-basis: calc((var(--title-stack) + 1px - (100 * var(--unit, 1%)))*9999);
    //display: flex;
    //align-items: center;
    //flex-direction: column;
    flex-grow: 1;
    width: 100%;
  }
  > .module-body {
    flex-grow: 1;
    min-width: max(1%, min(100%, 100% - var(--title-width)));
    flex-basis: calc((var(--title-stack) + 1px - (100 * var(--unit, 1%)))*9999);
  }
}
