@import 'variables.scss';

/******************************
 Journal Builder
*******************************/
.grid-rows {
  display: flex;
  flex-direction: column;
}

.grid-row {
  position: relative;
  --col-gap: 0px;
}

.grid-cols-wrapper {
  margin: 0 auto;
  position: relative;
  z-index: 1; // 2 or more interferes with Flyout in side column over #content
  transform: translate3d(0,0,0);
}

/******************************
 Grid gap
*******************************/

.grid-cols:not(.single-item){
  margin: calc(var(--col-gap, 0px) / 2 * -1);
  > .grid-col:not(:only-child){
    padding: calc(var(--col-gap, 0px) / 2);
  }
}

.grid-cols {
  display: flex;
  flex-wrap: wrap;
  height: 100%;
}

.grid-col {
  width: 100%;
  position: relative;
  flex-basis: 0;
}

.grid-items {
  height: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
  &::before{
    content: "";
    display: none;
    position: absolute;
    pointer-events: none;
    --unit: 1%;
    width: calc(100 * var(--unit));
    inset: 0;
  }
}

.grid-item {
  position: relative;
  width: stretch;
  display:flex;
  flex-direction:column;
  align-items: inherit;
  > .module:not(.module-background_slider) {
    position: relative;
    max-width: 100%;
    align-self: inherit;

    > .module-body{
      > div{
        // height: inherit; why? interferes with newsletter popup
      }
    }

    &:empty{
      display: none;
    }
  }
  &:empty{
    height: 100%;
  }
}

.module-faq,
.module-blocks{
  width: 100%;
}

.grid-row{
  > .module-background_slider{
    position: absolute;
    inset: 0;
  }
}

.row-bg{
  position: absolute;
  top:0;
  right:0;
  bottom:0;
  left:0;
  margin-top: var(--row-bg-offset, 0px);
  height: var(--row-bg-height, 100%);
  z-index: 0;
}

/******************************
 Boxed Layout
*******************************/
.boxed-layout {
  .grid-row {
    &::before {
      width: 100%
    }
  }
}

/******************************
 Side Column
*******************************/
.side-column {
  .grid-row {
    padding: 0;
    margin: 0;
  }

  .row-bg {
    background: none;
  }

  > .grid-rows {
    > .grid-row > .grid-cols-wrapper > .grid-cols {
      margin: 0;

      > .grid-col {
        min-width: 100%;
        padding-top: 0;
        padding-inline-start: 0;
      }
    }
  }
}

/******************************
 Row Overlay
*******************************/
.grid-row {
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;

  &::before {
    width: 100%;
    height: 100%;
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    pointer-events: none;
    z-index: 1;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    outline: 1px solid transparent;
    --content-gutter: inherit;
    top: calc(0px + var(--overlay-top, 0px));
    transform: translate3d(0,0,0) rotate(var(--overlay-rotate, 0deg)) skew(var(--overlay-rotate, 0deg));
  }
}

/******************************
 Row Wave
*******************************/
.grid-row {
  .wave {
    pointer-events: none;
    position: absolute;
    width: 100%;
  }

  .wave-top {
    top: 0;
    left: 0;
    transform: translateY(-63%);
    z-index: 1;
  }

  .wave-bottom {
    bottom: 0;
    left: 0;
    transform: translateY(63%) scaleY(-1);
    z-index: 1;
  }
}

/******************************
 Row video background
*******************************/
.video-row {
  position: absolute;
  z-index: 0;
  width: 100%;
  height: 100%;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  overflow: hidden;
  margin:var(--align-v, 0) var(--align-h, 0);
}

.video-bg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: -100;
}

.video-col-bg {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
}


/******************************
 Column Dimensions
*******************************/
.desktop.is-admin {
  #top,
  #content-top,
  #bottom {
    .grid-col {
      &::before {
        pointer-events: none;
        position: absolute;
        padding: 0 4px;
        font-size: 12px;
        top: 1px;
        right: 1px;
        box-shadow: -1px 1px 1px 0 rgb(0 0 0 / 20%);
        background-color: #26AB5E;
        color: white;
        z-index: 10;
        content: attr(data-dimensions);
        visibility: hidden;
        opacity: 0;
      }

      &:hover::before {
        visibility: visible;
        opacity: 1;
      }
    }
  }

  #top .grid-cols > div:only-of-type::before {
    display: none;
  }
}
