@import '../variables.scss';

.module-newsletter{
  display:flex;
  flex-direction:column;
  .module-body{
    display: flex;
    width: 100%;
    justify-content: inherit;
    flex-wrap: wrap;
    gap: var(--text-input-gap, 0px);
     > div:not(.newsletter-text){
       display: flex;
       justify-content: inherit;
     }
    .newsletter-form{
      max-width: 100%;
      &::before{
        display: flex;
        align-items: center;
        line-height: 1;
        height: var(--input-height, max-content);
        margin-inline-end: 8px;
        font-size: 28px;
        float: left;
      }
      form{
        max-width: 100%;
        min-width: 0;
        input{
          max-width: 100%;
        }
      }
      .checkbox{
        padding:0
      }
    }
  }
  .btn {
    position: absolute;
    right: 0;
    z-index: 2;
    &::before{
      content: "\f0e0";
      font-family: $icons;
    }
  }
  .input-group{
    gap: 0;
    padding: 0;
    height: var(--input-height, auto);
    .input-group-btn{
      display: flex;
      align-items: center;
      z-index: 3;
      .btn{
        width: min-content;
        height: auto;
        min-height: initial;
      }
    }
    input{
      height: 100%;
    }
    + .checkbox label{
      margin-top: 5px;
      width: 100%;
      background: transparent;
      input{
        min-width: 14px;
        min-height: 14px;
        width: calc(1em - 1px);
        height: calc(1em - 1px);
      }
    }
  }
}

.module-newsletter .module-body .newsletter-form form>div:not(.buttons):not(.checkout-section):not(.dropdown):not(#review){
  &.input-group,
  &.checkbox{
    margin-bottom: 0 !important;
  }
}

.newsletter-text::before{
  line-height: 1;
  margin-inline-end: 8px;
}

.agree b{
  font-weight: inherit;
}

.side-column{
  .module-newsletter{
    .module-body{
      display: block;
    }
    .btn {
      span {
        display: none;
      }
    }
  }
  .newsletter-text{
    margin-bottom:10px;
  }
}

