/******************************
 Journal Editor
*******************************/

.is-editor {
  --green: #26AB5E;
  --orange: #ED5149;
  --dark: #37485E;
  --blue: #238CD2;
  --top-border: 4px;
  --bar-height: 55px;
  transition: padding-bottom 150ms ease-out;
}

.admin-bar {
  position: fixed;
  bottom: 0;
  z-index: 999999;
  width: 100%;
  font-family: sans-serif;
  font-size: 14px;
  min-height: var(--bar-height);
  transform: translate3d(0, calc(100% + var(--top-border)), 0);
  transition: transform .15s ease-out, background-color .15s ease-out;
  background: var(--dark);
  border-top: var(--top-border) solid var(--orange);
  box-shadow: 1px -5px 30px -5px rgb(0 0 0 / 10%);

  display: none;
  align-items: center;

  .admin-close-bar {
    position: absolute;
    left: 50%;
    top: calc(0px - var(--top-border));
    transform: translate3d(-50%, -100%, 0);
    display: flex;
    align-items: center;
    justify-content: center;
    width: 34px;
    height: 34px;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;

    span {
      position: absolute;
      top: 48%;
      left: 50%;
      font-size: 24px;
      transform: translate(-43%, -50%) rotate(45deg);
      transform-origin: center;
    }

    html[dir="rtl"] & {
      left: auto;
      right: 50%;
    }
  }

  .j-logo {
    width: 100px;
    height: 45px;
    color: white;
    position: absolute;
    display: flex;
    align-items: center;
    padding: 8px;

    span {
      margin: 5px;
      opacity: .7;
      font-size: 13px;
    }

    svg {
      width: auto;
      fill: white;
    }
  }
}

.admin-bar-buttons {
  padding: 10px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  gap: 8px;

  button {
    background-color: var(--blue);
    color: white;
    padding: 5px 10px;
    border-radius: 3px;
    font-size: 13px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 100ms;

    &:hover {
      background-color: var(--green);
    }
  }

  .is-editor-open & {
    opacity: .4;
    pointer-events: none;
  }
}

.admin-edit {
  display: none;
  cursor: pointer;
  position: absolute;
  top: 1px;
  left: 1px;
  color: white;
  font-size: 12px;
  background-color: var(--green);
  border-radius: 2px;
  z-index: 100;
  box-shadow: 1px 1px 0 0 rgb(0 0 0 / 20%);

  html[dir="rtl"] & {
    left: auto;
    right: 1px;
  }

  em {
    margin: 2px 6px;
  }

  transition: opacity .15s ease-out;
  opacity: 0;
  height: 0;
  overflow: hidden;

  #main-menu-2 & {
    left: auto;
    right: 1px;
  }
}

.admin-edit-tooltip {
  pointer-events: none;
  z-index: 9999999;

  &.top .tooltip-arrow {
    border-top-color: var(--green) !important;
  }

  &.bottom .tooltip-arrow {
    border-bottom-color: var(--green) !important;
  }

  &.left .tooltip-arrow {
    border-left-color: var(--green) !important;
  }

  &.right .tooltip-arrow {
    border-right-color: var(--green) !important;
  }

  .tooltip-arrow {
    filter: drop-shadow(1px 1px 0 rgb(0 0 0 / 20%));
    z-index: 1;
  }

  .tooltip-inner {
    background: var(--green) !important;
    color: white !important;
    box-shadow: 1px 1px 0 0 rgb(0 0 0 / 20%);
  }
}

.admin-close-bar,
.admin-close-edit {
  right: 10px;
  position: absolute;
  top: 10px;
  width: 31px;
  height: 31px;
  border-radius: 3px;
  color: white;
  background: var(--orange);
  font-size: 21px;

  &:hover {
    background: var(--green);
  }
}

.admin-edit-popup {
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 999999;
  padding: 40px 20px 30px;
  background: rgba(0, 0, 0, .75);
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  align-items: center;
  justify-content: center;

  iframe {
    width: 100%;
    height: 100%;
    max-width: 1280px;
    border-radius: 5px;
    resize: both;
    max-height: 90vh;
  }
}

.is-editor {
  .admin-edit {
    display: block;
  }

  .admin-bar {
    display: flex;
  }

  &.admin-bar-visible {
    padding-bottom: var(--bar-height);

    .notification-wrapper-bottom .notification {
      bottom: 70px;
    }

    .scroll-top {
      bottom: var(--bar-height);
    }

    .admin-bar {
      transform: translate3d(0, 0, 0);

      .admin-close-bar span {
        transform: translate(-50%, -50%) rotate(0deg);
      }
    }
  }

  &.popup-open {
    .admin-bar {
      z-index: 1399;
    }
  }

  .lg-on {
    .admin-bar {
      display: none;
    }
  }

  .grid-row,
  .popup-inner-body,
  .module-header_notice,
  .module-notification,
  .top-menu,
  .main-menu {
    &:hover .admin-edit {
      opacity: 1;
      height: auto;
    }
  }
}

@media only screen and (max-width: 767px) {
  .admin-bar, .admin-edit {
    display: none !important;
  }
}

/* oc4 */
.oc4 .invalid-feedback:not(.d-block) {
  display: none;
}

.d-none {
  display: none;
}
