@import '../variables.scss';

/******************************
 Main Menu
*******************************/

.desktop-main-menu-wrapper {
  max-width: 100%;

  &.menu-bar {
    position: relative;
  }

  .main-menu {
    display: inline-flex;
    align-items: center;
    flex-wrap: wrap;
    scrollbar-width: none;

    &::-webkit-scrollbar {
      display: none;
    }

    .main-menu-item {
      display: flex;
      flex-direction: column;
      justify-content: center;
      height: 100%;
      padding: 0 calc(var(--menu-item-gap, 0px) / 2);

      > a {
        border-width: 0 0 0 1px;
        justify-content: center;
      }

      &:first-of-type {
        border-top-left-radius: inherit;
        border-bottom-left-radius: inherit;
        padding-inline-start: 0;

        > a {
          border-left-width: 0;
          border-radius: inherit;
        }
      }

      &:last-of-type {
        border-top-right-radius: inherit;
        border-bottom-right-radius: inherit;
        padding-inline-end: 0;

        > a {
          border-right-width: 0;
          border-radius: inherit;
        }
      }
    }

    > .j-menu {
      border-radius: inherit;
      flex-wrap: nowrap;
      z-index: 1;

      > li > a {
        height: 100%;

        .links-text {
          width: 100%;
          display: flex;
          align-items: center;
        }
      }
    }
  }

  &.menu-stretch {
    .main-menu {
      flex-wrap: nowrap;
      flex-grow: 1;

      > ul {
        width: 100%;

        > li {
          flex-grow: 1;
        }
      }
    }
  }

  &.menu-center {
    text-align: center;

    .main-menu {
      margin: 0 auto;
      display: inline-flex;
    }
  }

  &.has-menu-2 {
    display: flex;
    //align-items: center;
  }
}

html[dir='rtl'] {
  .desktop-main-menu-wrapper {
    .main-menu .main-menu-item {
      &:last-child {
        padding-inline-start: 0;
        padding-inline-end: calc(var(--menu-item-gap, 0px) / 2);
      }

      > a {
        border-width: 0 1px 0 0;
      }

      &:first-child {
        padding-inline-end: 0;
        padding-inline-start: calc(var(--menu-item-gap, 0px) / 2);

        > a {
          border-right-width: 0;
        }
      }
    }
  }
}

/******************************
 Flyout
*******************************/

.flyout {
  .flyout-menu-item:first-child,
  .flyout-menu-item:first-child > a {
    border-top-left-radius: inherit;
    border-top-right-radius: inherit;
  }

  .flyout-menu-item:last-child,
  .flyout-menu-item:last-child > a {
    border-bottom-left-radius: inherit;
    border-bottom-right-radius: inherit;
  }

  > .dropdown-menu {
    min-width: var(--element-width, auto);

    > .flyout-menu {
      min-width: 100%;
      max-width: var(--dropdown-width-main-menu, var(--dropdown-width, 100%));
    }
  }
}

/******************************
 Mega Menu
*******************************/

.mega-menu-content{
  &::before{
    --triangle-size-overlay: calc(var(--triangle-size, 0px) + var(--triangle-top-distance, 0px));
    content: "";
    inset: 0;
    top: var(--triangle-size-overlay, 0px);
    height: calc(100% - var(--triangle-size-overlay, 0px));
    position: absolute;
    border-radius: inherit;
    pointer-events: none;
    display: none;
  }
}

.desktop-main-menu-wrapper {
  &.menu-center #main-menu {
    margin: 0 auto;
  }

  [id*="main-menu"] {
    > .j-menu > .mega-menu > .j-dropdown > .mega-menu-content {
      overflow-x: hidden;
      overflow-y: auto;
      max-height: calc(100vh - (var(--mega-menu-top-offset, 0px) + var(--element-height, 0px) + var(--dropdown-triangle, 0px)));
    }
  }

  .main-menu {
    .mega-menu {
      .mega-menu-content {
        width: 100%;
        margin: 0 auto;
      }

      .grid-cols-wrapper {
        max-width: 100%;
        //overflow: hidden; // why? use row overflow opt
      }
    }

    .mega-fullwidth {
      position: static;

      > .j-dropdown {
        &::before {
          --element-offset: inherit;
          --scroll-offset: inherit;
          --element-width: inherit;
          --mega-menu-full-offset: inherit;
          --gutter: 0px;
          transform: translate3d(-50%, 0, 0);
          left: calc((var(--element-offset, 0px) + (var(--element-width, 0px) / 2)) - var(--scroll-offset, 0px) - var(--gutter, 0px) + var(--triangle-left-offset, 0px));
        }
      }

      &:first-child {
        > .j-dropdown {
          &::before {
            left: calc((var(--element-offset, 0px) + (var(--element-width, 0px) / 2)) - var(--scroll-offset, 0px) - var(--gutter, 0px) - var(--menu-item-gap, 0px) / 4 + var(--triangle-left-offset, 0px));
          }
        }
      }

      &:last-child {
        > .j-dropdown {
          &::before {
            left: calc((var(--element-offset, 0px) + (var(--element-width, 0px) / 2)) - var(--scroll-offset, 0px) - var(--gutter, 0px) + var(--menu-item-gap, 0px) / 4 + var(--triangle-left-offset, 0px));
          }
        }
      }
    }

    .menu-fullwidth {
      .mega-menu-content {
        max-width: 100%;

        > .grid-rows > .grid-row > .grid-cols-wrapper {
          max-width: min(calc(100% - var(--gutter, 0px) * 2), calc(var(--max-width, var(--content-max-width, 100%)) - var(--gutter, 0px) * 2))
        }
      }

      > .j-dropdown {
        padding-top: var(--padding, 0);

        &::before {
          left: calc((var(--mega-menu-full-offset, 0px) + (var(--element-width, 0px) / 2)) - var(--scroll-offset, 0px) - var(--gutter, 0px) + var(--triangle-left-offset, 0px));
        }
      }

      &:first-child {
        > .j-dropdown {
          &::before {
            left: calc((var(--mega-menu-full-offset, 0px) + (var(--element-width, 0px) / 2)) - var(--scroll-offset, 0px) - var(--gutter, 0px) - var(--menu-item-gap, 0px) / 4 + var(--triangle-left-offset, 0px));
          }
        }
      }

      &:last-child {
        > .j-dropdown {
          &::before {
            left: calc((var(--mega-menu-full-offset, 0px) + (var(--element-width, 0px) / 2)) - var(--scroll-offset, 0px) - var(--gutter, 0px) + var(--menu-item-gap, 0px) / 4 + var(--triangle-left-offset, 0px));
          }
        }
      }
    }

    .mega-custom {
      position: relative;

      > .j-dropdown {
        width: var(--width, 500px);
      }
    }
  }
}

html[dir="rtl"] {
  .desktop-main-menu-wrapper {
    .main-menu {
      .mega-fullwidth {
        > .j-dropdown {
          &::before {
            transform: translate3d(100%, 0, 0);
            left: auto;
            right: calc((var(--element-offset, 0px) + (var(--element-width, 0px) / 2)) - var(--scroll-offset, 0px) - var(--gutter, 0px));
          }
        }
      }

      .menu-fullwidth {
        .mega-menu-content {
          > .grid-rows > .grid-row > .grid-cols-wrapper {
            left: auto;
            right: calc(var(--scrollW, 0px) / 2);
          }
        }

        > .j-dropdown {
          &::before {
            --mega-menu-full-offset: inherit;
            left: auto;
            right: calc((var(--mega-menu-full-offset, 0px) + (var(--element-width, 0px) / 2)) - var(--scroll-offset, 0px) - var(--gutter, 0px) - var(--scrollW, 0px));
          }
        }
      }
    }
  }
}

/******************************
 Menu hover extenders
*******************************/

.no-touchevents {
  .main-menu {
    .main-menu-item.mega-menu:hover {
      &:first-child::before {
        content: "";
        --element-width: inherit;
        position: absolute;
        width: calc(var(--element-width, 50px));
        height: 100%;
        transform: translate3d(-100%, 0, 0);
      }

      &:last-child::after {
        content: "";
        --element-width: inherit;
        position: absolute;
        width: calc(var(--element-width, 50px));
        height: 100%;
        transform: translate3d(calc(var(--element-width, 50px)), 0, 0);
      }
    }
  }
}

html[dir="rtl"].no-touchevents {
  .header-sm {
    .menu-bar {
      .main-menu-item.mega-menu {
        &:first-child::before {
          transform: translate3d(100%, 0, 0);
        }

        &:last-child::after {
          transform: translate3d(calc(var(--element-width, 50px) * -1), 0, 0);
        }
      }
    }
  }
}

.header-sm .menu-bar > .main-menu-1{
  background: inherit;
}

/******************************
 Menu Open Site Overlay
*******************************/

@keyframes overlay {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.site-overlay {
  z-index: -10;
  position: fixed;
  width: 100%;
  height: 100vh;
  inset:0;
  top: calc(var(--header-notice-height, 0px) + var(--site-overlay-offset, 0px));
  pointer-events: none;
  background-color:#000;
  animation: overlay .3s ease;
  transition: opacity .15s .10s ease-in-out;
  opacity: 0;
  visibility: hidden;
  //display: none;

  &.boxed-layout {
    .site-overlay {
      display: none !important;
    }
  }
}

.desktop-header-active{
  .menu-hover .site-overlay {
    opacity: 0;
    visibility: hidden;
    display: none;
  }
}

/******************************
 Mobile Menu Desktop
*******************************/

.desktop-header-active {
  .menu-trigger {
    display: none !important;
    transition: all 50ms ease-out;
    z-index: 2;

    ul, li {
      height: 100%;
    }

    a {
      cursor: pointer !important;
      justify-content: flex-start;
      border-width: 0;
      height: 100%;
      display: flex;
      align-items: center;
      padding: 5px 15px;
      text-decoration: none !important;
      transition: all 80ms ease-out;
      white-space: nowrap;
    }

    .links-text {
      transition: inherit;
    }
  }

  .desktop-main-menu-wrapper.menu-top{
    align-items: center;
    > .menu-trigger {
      padding-inline-start: 0;
      padding-inline-end: 0;
    }
  }

  &.mobile-menu-active {
    .menu-trigger {
      display: inline-flex !important;
      min-height: 100%;
      margin-inline-end: auto;
      margin-inline-start: 0;

      html[dir="rtl"] & {
        margin-inline-start: auto;
        margin-inline-end: 0;
      }
    }
  }

  &.mobile-menu-1-active .global-wrapper #main-menu {
    display: none !important;
  }

  &.mobile-menu-2-active .global-wrapper #main-menu-2 {
    display: none !important;
  }

  &.mobile-menu-2-active:not(.mobile-menu-1-active) {
    .has-menu-2 {
      .menu-trigger {
        order: 2;
        margin: 0 0 0 auto;
      }
    }
  }
}

.main-menu-1 {
  display: inline-flex;
  align-items: center;
  max-width: 100%;
  flex-grow: 1;
  min-width: 0;

  > .main-menu > ul {
    flex-grow: 1;
  }
}

.main-menu-2 {
  display: inline-flex;
  align-items: center;
  min-width: 0;
}

.desktop-main-menu-wrapper {
  .main-menu > .j-menu > li > a .count-badge {
    --menu-badge-y: -100%;
  }

  .menu-bar > div > .main-menu > .j-menu > li > a .count-badge {
    --count-badge-y: -100%;
  }
}

/******************************
 Main Menu Dropdowns Align or Scroll
*******************************/

.desktop-main-menu-wrapper .main-menu > .j-menu > .main-menu-item.dropdown {
  > .j-dropdown {
    top: calc(50% + var(--element-height, 0px) / 2);

    &::before {
      --menu-item-gap: inherit;
    }
  }
}

