@import '../variables.scss';

/******************************
 Accordion
*******************************/

.accordion-heading {
  div {
    span {
      font-size: 90%;
      opacity: .7;
      margin-inline-start: 5px;
    }
  }
}

.panel-group {
  margin-bottom: 0;

  .panel {
    border-radius: 0;
    box-shadow: none;
    border: none;
    background: transparent;
    position: relative;
    z-index: 1;
    transition: all 75ms ease;

    &:hover {
      z-index: 2;
    }

    + .panel {
      margin-top: var(--panel-spacing, 0px);
    }

    &:first-child {
      border-top-left-radius: inherit;
      border-top-right-radius: inherit;
    }

    &:last-child {
      border-bottom-left-radius: inherit;
      border-bottom-right-radius: inherit;
    }

    &:not(:only-child):not(:last-child)::after {
      content: "";
      --panel-spacing: inherit;
      border-width: 0.5px;
      display: block;
      margin-top: var(--panel-spacing, 0px);
    }
  }

  .panel-heading {
    padding: 0;
    transition: all 75ms ease, border-radius 0s;
    border-radius: inherit;
    position: relative;
    z-index: 2;
    background: transparent;

    .panel-title {
      font-size: inherit;
      font-weight: inherit;
      position: relative;
      border-radius: inherit;
      transition: all 75ms ease;
      padding: 0;
      border: none;
      width: 100%;
    }

    a {
      display: flex;
      align-items: center;

      i, em {
        display: none;
      }

      &::before {
        margin-inline-end: 5px;
        line-height: 1;
        overflow: hidden;
        min-width: 24px;
        transition: all 75ms ease;
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }

    + .panel-collapse {
      position: relative;
      z-index: 1;

      > .panel-body {
        border-top: 0;
      }
    }
  }

  .panel-active {
    .panel-heading {
      //transition: all .5s;
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;

      a {
        cursor: default;
      }
    }

    + .panel .panel-heading {
      border-top-width: 1px;
    }
  }

  .panel-body {
    padding: 0;
  }
}

.product_accordion > .panel > .panel-heading {
  display: flex;
  align-items: center;

  &::before {
    line-height: 1;
  }
}

