 @import 'variables.scss';

/********************e**********
 Post Grid/List
*******************************/

.post-grid:not(.swiper-wrapper):not(.auto-grid) {
  display: flex;
  flex-wrap: wrap;
}

.post-layout {
  display: flex;
  position: relative;
}

.post-grid {
  .image {
    container-type: inline-size;
  }
}

.post-thumb {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  min-width: 0;
  position: relative;
  transition: all 0.075s ease-out;
  //overflow: hidden; //interferes with image and details shadow
  //overflow: hidden; // for border radius

  .image {
    position: relative;

    a {
      display: flex;
      align-items: center;
      justify-content: center;
      -webkit-backface-visibility: hidden;
      backface-visibility: hidden;
      overflow: hidden;
      position: relative;

      &::before {
        content: "";
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        pointer-events: none;
        z-index: 2;
      }

      img {
        width: 100%;
        height: auto;
        pointer-events: none;
        --scale-speed: 500ms;
        transition: all var(--scale-speed) $easeOutExpo;
      }
    }

    .p-date {
      z-index: 1;
    }
  }

  &:hover {
    z-index: 2;
  }

  .caption {
    display: flex;
    flex-direction: column;
    position: relative;
    max-width: 100%;
    container-type: inline-size;

    > * {
      order: 50;
    }
  }

  .name {
    font-weight: normal;
    line-height: 1.2;
    margin: 0;
    display: flex;
    max-width: 100%;
    //flex-grow: 1; //interferes with auto list and chips
    align-items: flex-start;
    a {
      display: block;
    }
  }

  .post-stats {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    font-size: 12px;
    span {
      display: inline-flex;
    }
  }
}

.post-stats {
  ::before {
    line-height: 0;
  }
}

.p-date {
  em {
    padding: 0 4px;
    margin: 0;
    font-style: normal;
  }
}

.p-date-image {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: absolute;
  --p-date-size: 40px;
  width: var(--p-date-size, auto);
  height: var(--p-date-size, auto);
  padding: 5px;
  pointer-events: none;
  line-height: 1;
  border-radius: inherit;
  container-type: inline-size;

  em {
    font-size: 80%;
  }

  &::before {
    display: none;
  }
}

//Buttons
.post-layout {
  .button-group {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }

  .btn-text {
    display: none;
  }
}

.post-grid {
  .post-thumb{
    --image-width: 100%;
    --stack: 300px;
    --details-gap: 0px;
    flex-direction: column;
    flex-wrap: nowrap;

    > div{
      flex-basis: calc((var(--stack) - 100%) * 9999);
    }
    .image{
      min-width: min(100%, (var(--image-width)));
      a{
        height: 100%;
        img{
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
    }
    .caption {
      // justify-content: center; //why?
      flex-grow: 1;
      min-width: min(100%, (max(1%, 100% - var(--image-width)) - var(--details-gap)));
    }
  }
}

.post-grid, .post-list {
  .post-thumb {
    gap: var(--details-gap, 0);
  }
}

.post-list {
  .post-thumb {
    flex-direction: row;
    flex-wrap: wrap;

    > div {
      width: 100%;
      flex-basis: calc((var(--details-stack, 200px) - 100%) * 9999);
    }

    .image {
      min-width: min(100%, var(--list-image, 40%));
    }

    .caption {
      flex-grow: 1;
    }
  }
}

.main-posts.post-list{
  .post-layout{
    width: 100%;
  }
}

.post-list:not(.auto-grid),
.main-posts.post-list{
  .post-layout:not(.swiper-slide) {
    margin-bottom: var(--post-list-spacing, 0);
  }
}

/******************************
 Post Page
*******************************/

.post-image {
  position: relative;
  break-inside: avoid;

  img {
    height: auto;
  }
}

.post-stats {
  --post-stats-gap: 8px;
  gap: var(--post-stats-gap, 4px);

  span {
    align-items: center;
    flex-wrap: wrap;
    display: inline-flex;
    line-height: 1;
  }
}

.post-details {
  column-fill: balance;
}

.post-content::after {
  content: "";
  display: table;
  width: 100%;
  clear: both;
}

/******************************
 Comments
*******************************/

.blog-post {
  .comment {
    p {
      margin-top: 5px;
    }
  }

  .success {
    &:last-of-type {
      margin-bottom: 0;
    }
  }
}

.post-comment {
  position: relative;

  .user-name {
    padding-inline-end: 50px;
    margin-bottom: 5px;
  }
}

.user-data {
  margin-top: 3px;

  ::before {
    margin-inline-end: 5px;
  }

  span {
    display: inline-flex;
    align-items: center;
    max-width: 100%;
  }

  div span {
    margin-inline-end: 5px;
  }

  a {
    display: block;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 100%;
  }
}

.user-avatar {
  float: left;
  position: relative;
  z-index: 2;
}

.reply-form {
  margin-top: 20px;
}

.reply-btn {
  position: absolute;
  top: 0;
  right: 0;
  left: auto;

  html[dir="rtl"] & {
    right: auto;
    left: 0;
  }
}

/******************************
 Feed
*******************************/

.blog-feed {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translate3d(0,-50%,0) scale(var(--scale-hover, 1));
  font-size: 1.4rem;
  font-weight: normal;
  display: flex;
  align-items: center;
  justify-content: center;

  &:hover{
    transform: translate3d(0,-50%,0) scale(var(--scale-hover, 1));
  }

  span {
    margin: 0 5px;
  }
}

html[dir='rtl'] {
  .blog-feed {
    right: auto;
    left: 10px;
  }
}

/******************************
 Modules
*******************************/

.module-blog_categories {
  .module-body {
    display: flex;
    flex-direction: column;
  }

  a {
    display: flex;
    align-items: center;
    transition: all 0.07s ease-out;

    + a {
      border-width: 1px 0 0 0;
    }
  }
}

/******************************
 Search
*******************************/

.box-search {
  display: flex;
  position: relative;

  .btn {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    height: 95%;
    min-width: 32px;

    .fa {
      line-height: 0;
    }
  }
}

html[dir='rtl'] {
  .box-search {
    .btn {
      right: auto;
      left: 0;
    }
  }
}

/******************************
 Comments
*******************************/

.module-blog_comments {
  .module-body > div + div {
    border-width: 1px 0 0 0;
  }

  .module-item {
    display: flex;
    align-items: center;

    &:first-child {
      padding-top: 0;
    }

    > div {
      flex: 1;
      line-height: 1.3;
    }

    .side-subtitle {
      margin-top: 3px;
    }
  }
}

/******************************
 Side Posts
*******************************/

.side-posts.swiper-wrapper {
  flex-wrap: nowrap;
}

.side-details{
  min-width: 0;
}

.side-posts {
  display: flex;
  flex-wrap: wrap;

  .post-thumb {
    width: 100%;
    flex-direction: row;
    flex-wrap: wrap;
    overflow: initial;
    align-items: center;

    .image {
      position: relative;
      margin: 0 auto;
      max-width: 100%;

      a {
        display: block;
        overflow: initial;
      }

      img {
        width: 100%;
      }
    }

    .post-stats {
      justify-content: flex-start;
    }

    .name {
      line-height: 1.1;
      margin-bottom: 3px;
    }

    .caption {
      padding: 0 8px;
      min-width: 0;
      flex: 10 2 100px;
      width: 100%;
    }
  }
}
