@import '../variables.scss';

.module-gallery {
  .ipr-grid {
    display: flex;
    flex-wrap: wrap;
  }

  container-type: inline-size;

  .module-item {
    > a {
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      width: 100%;
      -webkit-backface-visibility: hidden;
      backface-visibility: hidden;
      overflow: hidden;
      //container-type: inline-size;
      inset-inline-start: var(--item-offset-x, initial);
      inset-block-start: var(--item-offset-y, initial);

      img {
        transition: all 0.15s ease-out;
        width: 100%;
        height: auto;
      }

      //Title thumb
      &::before {
        content: "";
        position: absolute;
        pointer-events: none;
        left: 0;
        right: 0;
        bottom: 0;
        padding: clamp(8px, 10%, 20px);
        transform: translate3d(0, 10px, 0) scale(1);
        transition: all 0.1s ease-out;
        opacity: 0;
        font-size: clamp(11px, 6cqw, 14px);
        color: white;
        line-height: 1.3;
        z-index: 2;
      }

      //Icon
      &::after {
        content: "";
        position: absolute;
        display: var(--display);
        width: 100%;
        height: 100%;
        align-items: center;
        justify-content: center;
        transition: all 0.15s ease-out;
        --opacity: 0;
        transform: translate3d(0, 0, 0) scale(0);
        opacity: var(--opacity);
        pointer-events: none;
        font-size: 35px;
        z-index: 2;
      }

      > span {
        width: 100%;
        height: 100%;
        display: block;
        //Overlay
        &::before {
          content: "";
          width: 100%;
          height: 100%;
          position: absolute;
          pointer-events: none;
          transition: opacity 0.15s ease-out;
          z-index: 1;
        }
      }
    }
    + .module-item{
      z-index: 2;
    }
  }
}

.gallery-image-caption {
  display: none;
  font-size: clamp(12px, 4cqw, 15px);
  line-height: 1.3;
  margin-top: 4px;
}

.no-touchevents .module-gallery .module-item > a:hover {
  .gallery-image::before {
    opacity: 1;
  }

  &::after {
    opacity: 1;
    transform: translate3d(0, 0, 0) scale(1);
  }

  &::before {
    transform: translate3d(0, 0, 0) scale(1);
    opacity: 1;
  }
}

/******************************
 Gallery popup
*******************************/

.lg-backdrop {
  z-index: 100000;
  background-color: rgba(0, 0, 0, .85);
}

.lg-outer {
  z-index: 200000;
}

.lg-outer .lg-item {
  background: none;

  &::after {
    content: "";
    position: absolute;
    top: 46.5%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1000;
  }

  &.lg-complete::after {
    display: none;
  }
}

.lg-content {
  .lg-next, .lg-prev {
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;

    &::after {
      display: none;
    }
  }
}

.lg-content {
  .lg-icon {
    &::before,
    &::after {
      left: 0 !important;
    }
  }

  .lg-video-play-button {
    svg {
      stroke: currentColor;

      polygon {
        fill: currentColor;
      }
    }
  }
}

.lg-progress-bar .lg-progress {
  height: 3px;
}


.lg-toolbar {
  background: transparent;
  display: flex;
  align-items: center;
  padding-inline: 20px 12px;
  height: 50px;

  .lg-icon {
    padding: 0;
    width: 44px;
    height: 100%;
    //background: pink;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.lg-close {
  order: 99;
}

.lg-outer {
  display: flex;
  flex-direction: column;

  .lg-thumb-outer {
    background: transparent;
  }

  div.lg-thumb{
    background: rgba(0,0,0,.8);
    padding: var(--thumbs-padding, 0px);
  }

  .lg-thumb-outer {
    .lg-thumb-item {
      transition: all 100ms ease-out;
      z-index: 1;
      &:hover,
      &:active{
        z-index: 2;
      }
      img{
        border-radius: calc(var(--element-border-radius, inherit) - 2px);
      }
    }
  }

  .lg-thumb-item.active,
  .lg-thumb-item:hover{
    border-color: transparent;
  }

  .lg-toggle-thumb {
    position: absolute;
    z-index: 10000;
    padding: .2em .4em;
  }

  &.lg-components-open {
    .lg-components {
      transform: translate3d(0, calc(0px - var(--thumbs-bottom-offset, 0px)), 0) !important;
    }
    .lg-sub-html{
      margin-bottom: calc(var(--thumb-height, 0px) + (var(--thumbs-padding, 0px) * 2));
    }
  }

  .lg-components {
    //height: 100%;
    //bottom: var(--thumbs-bottom-offset, 0px);
    pointer-events: none;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }

  .lg-counter {
    order: -1;
    height: auto;
    padding: 0;
    margin: 0;
    margin-inline-end: auto;
  }

  .lg-sub-html {
    pointer-events: auto;
    background: transparent;
    opacity: 1;
    padding-inline: var(--content-gutter, 24px);
    margin-inline: auto;
    width: 100%;
    height: auto;
    transition: margin 0.35s ease-out !important;
  }

  .lg-actions {
    .lg-icon {
      color: white;
      margin: 0;
      transform: translateY(-50%);
      border-radius: 0;

      &::after {
        display: none;
      }
    }
  }

  .lg-zoom-in {
    font-size: 24px;

    &::after {
      content: "\e312";
    }
  }

  .lg-thumb-outer {
    pointer-events: auto;
    height: auto !important;
    transition: transform .25s ease-out;
  }

  .lg-thumb {
    padding: 0;
    margin: 0 auto;
    background: transparent;
    align-items: center;
    display: flex;
  }

  .lg-thumb-item {
    border: none;
    border-radius: 0;
    margin-bottom: 0;
    overflow: visible;
    position: relative;

    &:hover, &.active {
      z-index: 2;
    }

    img {
      display: block;
      transition: all 100ms ease-out;
    }
  }

  .lg-item:not(.lg-start-end-progress) {
    background: none;
  }
}


