@import 'variables.scss';

/******************************
 Auto grid
*******************************/

.auto-grid {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  --scroll-by: 1;
  --grid-gap: 0px;
  --auto-grow-type: auto-fill;
  --grid-max-col: initial;
  --grid-min-col: 1; //why 0?
  --grid-min-width: 240px;
  --fr: 1fr;
  --item-fit: 1fr;
  --item-fill: var(--grid-min-width);

  &::after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    background-image: linear-gradient(to right, rgba(255, 255, 255, 0), rgba(255, 255, 255, 1));
    width: 70px;
    z-index: 1;
    pointer-events: none;
    display: none;
  }

  &.no-scroll,
  &.no-scroll-next {
    &::after {
      display: none;
    }
  }

  &.no-scroll {
    .auto-grid-items {
      cursor: auto !important;
    }
  }
}

.auto-grid-items {
  width: 100%;
  display: grid;
  gap: var(--grid-gap);
  grid-template-columns: repeat(var(--auto-grow-type), minmax(max(min(var(--grid-min-width), calc((100% - (var(--grid-min-col, -1) - 1) * var(--grid-gap)) / var(--grid-min-col, -1))), calc((100% - (var(--grid-max-col, -1) - 1) * var(--grid-gap)) / var(--grid-max-col, -1))), var(--fr)));
  grid-auto-columns: minmax(max(min(var(--grid-min-width), calc((100% - (var(--grid-min-col, -1) - 1) * var(--grid-gap)) / var(--grid-min-col, -1))), calc((100% - (var(--grid-max-col, -1) - 1) * var(--grid-gap)) / var(--grid-max-col, -1))), var(--fr));
  grid-template-rows: repeat(var(--grid-rows, 1), auto);
  grid-auto-flow: dense;
  scroll-padding-inline-start: var(--scroll-padding, 0px);
  z-index: 1;

  scrollbar-width: none;

  &::-webkit-scrollbar{
    display: none !important;
  }

  &:not(.is-dragging) {
    scroll-snap-type: x mandatory;
    scroll-behavior: smooth;
    -webkit-overflow-scrolling: touch;

    > * {
      scroll-snap-stop: normal;
    }
  }

  &.is-gliding a {
    pointer-events: none;
  }

  // carousel fader overlay
  &::before,
  &::after {
    content: "";
    --scrollbar-top-space: inherit;
    --scrollbar-bottom-space: inherit;
    --fader-color: var(--background-color);
    --fader-status: 1;
    display: none;
    opacity: var(--fader-status);
    position: absolute;
    pointer-events: none;
    height: calc(100% - (var(--top-padding, 0px) + var(--bottom-padding, 0px) + var(--scrollbar-top-space, 0px) + var(--scrollbar-bottom-space, 0px)));
    z-index: 3;
    transition: opacity 200ms ease-out;
  }

  &::before {
    left: 0;
    width: min(var(--fader-width-left, 120px), 40%);
    background-image: linear-gradient(-90deg, rgba(255, 255, 255, 0) 0, var(--fader-color) 90%, var(--fader-color))
  }

  &::after {
    right: 0;
    width: min(var(--fader-width-right, 120px), 40%);
    background-image: linear-gradient(90deg, rgba(255, 255, 255, 0) 0, var(--fader-color) 90%, var(--fader-color))
  }
  > *:nth-child(even){
    margin-top: var(--stagger-item-even, 0);
  }
}

.no-scroll > .auto-grid-items {
  &::before,
  &::after {
    --fader-status: 0 !important;
    display: none !important;
  }
}

.no-scroll-prev > .auto-grid-items {
  &::before {
    --fader-status: 0 !important;
  }
}

.no-scroll-next > .auto-grid-items {
  &::after {
    --fader-status: 0 !important;
  }
}

// fader rtl
html[dir="rtl"]{
  .auto-grid-items{
    &::before {
      left: auto;
      right: 0;
      transform: scale(-1);
      width: min(var(--fader-width-right, 120px), 40%);
    }

    &::after {
      right: auto;
      left: 0;
      transform: scale(-1);
      width: min(var(--fader-width-left, 120px), 40%);
    }
  }
}

/******************************
Align to Content
*******************************/

html:not(.boxed-layout) {
  .top, .bottom, footer, .mega-menu-content {
    .fullwidth-row{
      .align-to-content {

        > .module-title{
          max-width: var(--content-max-width, 100%);
          //margin-inline: var(--align-to-content-title);
        }
        > .module-body > .module-item > .module-title{
          max-width: var(--content-max-width, 100%);
          margin-inline: var(--align-to-content-title);
        }
        > .module-body > .tab-container > .nav-tabs{
          //max-width: var(--content-max-width, 100%);
          margin-inline: calc(var(--align-to-content-title, 0px) - var(--page-tabs-scroll-padding, 0px));
        }

        .auto-grid{
          --scroll-padding: var(--content-gutter, 0px) !important
        }
        .auto-grid > .auto-grid-items{
          padding-inline-start: var(--align-to-content);
          scroll-padding-inline-start:var(--align-to-content);
        }

        .auto-carousel-controls {
          --auto-carousel-controls-max-width: min(min(var(--content-max-width, 100%) - var(--scroll-padding) * 2, var(--content-max-width, 100%) - var(--content-gutter) * 2), 100% - var(--scroll-padding) * 2) !important;
        }
      }
    }
    .fullwidth-row:not(.align-content-row){
      .align-to-content{
        > .module-title{
          margin-inline: var(--align-to-content-title);
        }
      }
    }
    .fullwidth-row.align-content-row{
      .align-to-content{
        > .module-body{
          margin-inline-start: var(--align-to-content-cancel);
        }
      }
    }
  }
}

/******************************
 Flex Grid
*******************************/

.flex-grid {
  display: flex;
  flex-wrap: wrap;
  gap: var(--flex-grid-gap, 0px);
  width: stretch;
  width: -moz-available;
  .no-flexbox-gap & {
    margin: calc(0px - var(--flex-grid-gap, 0px) / 2);

    > * {
      margin: calc(var(--flex-grid-gap, 0px) / 2);
    }
  }
  > * {
    min-width: min(var(--flex-grid-width, fit-content), 100%);
    width: var(--flex-grid-max-col, fit-content);
  }
}

.masonry-grid {
  --masonry-cols-width: auto;
  --masonry-cols-max: auto;
  --masonry-cols-gap: 0px;
  columns: var(--masonry-cols-width) var(--masonry-cols-max);
  column-gap: var(--masonry-cols-gap);
  column-fill: balance;

  > * {
    break-inside: avoid;
    &:not(:last-child){
      margin-bottom: var(--masonry-cols-gap);
    }
  }
}

/******************************
 Defaults
*******************************/

.main-products.product-grid.auto-grid,
.main-posts.post-grid.auto-grid {
  display: grid;
  grid-template-columns: repeat(var(--auto-grow-type), minmax(max(min(var(--grid-min-width), calc((100% - (var(--grid-min-col, -1) - 1) * var(--grid-gap)) / var(--grid-min-col, -1))), calc((100% - (var(--grid-max-col, -1) - 1) * var(--grid-gap)) / var(--grid-max-col, -1))), var(--fr)));
  gap: var(--grid-gap);
  overflow-x: initial;

  > .product-layout {
    height: 100%;
  }
}

.main-products.product-list.auto-grid{
  flex-direction: column;
}

.product-list.auto-grid{
  flex-direction: column;
  .product-layout{
    flex-grow: 1;
    max-width: 100%;
  }
}

/******************************
 Auto Carousel
*******************************/

.auto-carousel-controls {
  z-index: 2;
  --auto-carousel-controls-max-width: 100%;
}

.no-scroll > .auto-carousel-controls {
  visibility: hidden !important;
}


.auto-carousel-bar {
  width: -moz-available;
  width: -webkit-fill-available;
  width: stretch;

  position: relative;
  background: #eee;
  box-sizing: content-box;
  align-items: center;
  height: var(--auto-carousel-height, var(--auto-carousel-thumb-height, 10px));
  max-width: calc(var(--auto-scroll-max-width, var(--auto-carousel-controls-max-width)) - var(--width-reduction, 0px));
  max-height: 0;
  margin: var(--scrollbar-top-spacing, 0) max(0px, var(--scroll-padding, 0px)) var(--scrollbar-bottom-spacing, 0) max(0px, var(--scroll-padding, 0px));
}

.auto-carousel-thumb {
  position: absolute;
  height: var(--auto-carousel-thumb-height, 4px);
  top: 50%;
  transform: translate3d(0, -50%, 0);
  background: #aaa;
  width: 0;
  will-change: transform;
  z-index: 1;
  transition: transform 50ms linear;
  display: flex;
  align-items: center;
  justify-content: center;
  max-height: inherit;
  box-sizing: content-box;
  &::before {
    max-width: 100%;
    max-height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 1.3em;
    min-height: 1.3em;
  }
}

.auto-carousel-fill {
  --progressDirection: 1;
  position: absolute;
  background: #ddd;
  display: none;
  left: 0;
  right: 0;
  top: 50%;
  width: 100%;
  height: 100%;
  transform: translate3d(0, -50%, 0) scaleX(0);
  transform-origin: left;
  border-radius: inherit;
  transition: all 50ms linear;
  will-change: transform;
}

html[dir="rtl"] {
  .auto-carousel-fill {
    --progressDirection: -1;
    transform-origin: right;
  }
}

/******************************
 Buttons
*******************************/

.auto-carousel-buttons {
  width: -moz-available;
  width: -webkit-fill-available;
  width: stretch;
  max-width: var(--auto-carousel-controls-max-width, 100%);
  margin-inline: max(0px, var(--scroll-padding, 0px));

  position: absolute;
  pointer-events: none;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: row;
  order: -1;
  top: 50%;
  transform: translate3d(0, -50%, 0);
  gap: var(--auto-carousel-button-gap, 0px);
  --buttons-offset: calc(var(--scrollbar-top-space, 0px) / 2 * -1);


  > div {
    //overflow: hidden; // why?
    width: var(--auto-carousel-button-width, 40px);
    height: var(--auto-carousel-button-height, 40px);
    position: relative;
    pointer-events: auto;
    will-change: transform;
    transition: transform 250ms ease, opacity 0ms linear;
  }

  span {
    position: relative;
    height: 100%;
    cursor: pointer;
    background: rgba(0, 0, 0, .7);
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    transform: translate3d(0, 0, 0) scale(1);
    will-change: transform;
    transition: all 75ms ease, transform 20ms;

    &::before {
      content: '\e5c4';
      font-family: icomoon;
    }
  }
}

.auto-carousel-next {
  span::before {
    content: '\e5c8';
  }
}

.no-scroll-next {
  .auto-carousel-next {
    --scale: 1;
    opacity: .5;
    pointer-events: none;
    transition: transform 250ms ease, opacity 0ms 250ms linear;

    span {
      cursor: default;
    }

    &:active {
      &:active {
        span {
          opacity: .8;
        }
      }
    }
  }
}

.no-scroll-prev {
  .auto-carousel-prev {
    --scale: 1;
    opacity: .5;
    pointer-events: none;
    transition: transform 250ms ease, opacity 0ms 250ms linear;

    span {
      cursor: default;
    }

    &:active {
      span {
        opacity: .8;
      }
    }
  }
}
