@import 'variables.scss';

body {
  max-width: 100vw;
}

html:not(.popup) {
  body {
    overflow-y: scroll;
    overflow-x: hidden;
  }
}

.phone {
  .site-wrapper {
    overflow: hidden; // iphone only for pos sticky on ipad
  }
}

::before, ::after {
  box-sizing: border-box;
  --scrollW: inherit;
}

/******************************
 Top Counters
*******************************/

:root {
  counter-reset: top-counters;
  counter-increment: top-counters;
}

/******************************
Browser theme color
*******************************/

.mobile {
  body {
    background: var(--browser-theme-color);
  }
}

/******************************
 Main Structure
*******************************/

.global-wrapper {
  position: relative;
  max-width: 100vw;
  display: flex;
  flex-direction: column;

  .has-bottom-menu & {
    margin-bottom: calc(var(--bottom-menu-height, 55px) + var(--bottom-menu-margin, 0px)) !important;
  }

  .desktop & {
    min-height: 100vh;
  }
}


.global-wrapper {
  &::before {
    content: "";
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    inset: 0;
    pointer-events: none;
    position: absolute;
  }
}


.boxed-layout, .popup {
  .global-wrapper {
    &::before {
      display: none;
    }
  }
}

.site-wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  max-width: 100vw;
  margin-top: var(--site-top-spacing, 0px);

  > .container {
    width: 100%;
    padding: 0;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    max-width: calc(var(--container-max-width, var(--content-max-width, 100%)));
    margin-top: var(--content-top-spacing, 0px);
    margin-bottom: var(--content-bottom-spacing, 0px);

    > .row {
      display: flex;
      flex-wrap: wrap;
      flex-grow: 1;
    }
  }
}


html.route-common-home, html.popup {
  body {
    --site-top-spacing: 0px;
    --content-top-spacing: 0px;
    --content-bottom-spacing: 0px;
  }
}

.column-left,
.column-right {
  .site-wrapper > .container {
    padding-inline-start: calc(var(--content-gutter, 0px) + var(--side-cols-gap-offset, 0px) / 2);
    padding-inline-end: calc(var(--content-gutter-right, var(--content-gutter, 0px)) + var(--side-cols-gap-offset, 0px) / 2);
  }
}

.left-column-disabled.one-column,
.right-column-disabled.one-column,
.left-column-disabled.right-column-disabled.two-columns {
  --side-cols-gap: 0px;
}

.boxed-layout {
  .global-wrapper, body > header {
    margin: 0 auto;
  }

  .global-wrapper {
    &::after {
      content: "";
      width: 100%;
      height: 100%;
      position: absolute;
      z-index: -1;
      pointer-events: none;
      border-radius: inherit;
    }
  }

  header,
  .header,
  .top-bar,
  .top-bar::before {
    border-top-left-radius: inherit;
    border-top-right-radius: inherit;
  }

  footer {
    border-bottom-left-radius: inherit;
    border-bottom-right-radius: inherit;
  }
}

.row {
  margin-inline-start: 0;
  margin-inline-end: 0;

  > div {
    padding-inline-start: 0;
    padding-inline-end: 0;
  }
}

.site-wrapper > .container,
.site-wrapper > .container > .row {
  &::before, &::after {
    display: none;
  }
}

html:not(.popup) .site-wrapper > .container {
  position: relative;
}

#content {
  max-width: 100%;
  width: 100%;
  min-width: 200px;
  position: relative;
}

.left-column-disabled,
.right-column-disabled {
  #content {
    flex-grow: 1;
  }
}

.column-left {
  .site-wrapper > .container > .row {
    margin: calc(0px - var(--side-cols-gap, 0px) / 2);
  }

  #content {
    width: calc(100% - var(--col-l-width, 200px) - (var(--side-cols-gap, 0px) * 2));
    margin: calc(var(--side-cols-gap, 0px) / 2);
  }
}

.column-right {
  .site-wrapper > .container > .row {
    margin: calc(0px - var(--side-cols-gap, 0px) / 2);
  }

  #content {
    width: calc(100% - var(--col-r-width, var(--col-l-width, 200px)) - (var(--side-cols-gap, 0px) * 2));
    margin: calc(var(--side-cols-gap, 0px) / 2);
  }
}

.column-left.column-right {
  #content {
    width: calc(100% - var(--col-l-width, 200px) - var(--col-r-width, var(--col-l-width, 200px)) - (var(--side-cols-gap, 0px) * 3));
  }
}

.side-column {
  width: var(--col-l-width, 200px);
  max-width: 100%;
  z-index: 2;
}

#column-left {
  min-width: calc((var(--col-l-stack) * 1%) - var(--side-cols-gap, 0px));
  margin: calc(var(--side-cols-gap, 0px) / 2);
}

#column-right {
  width: var(--col-r-width, var(--col-l-width, 200px));
  min-width: calc((var(--col-r-stack) * 1%) - var(--side-cols-gap, 0px));
  margin: calc(var(--side-cols-gap, 0px) / 2);
}

.left-column-disabled #column-left,
.right-column-disabled #column-right {
  display: none !important;
}

html:not(.popup).left-column-disabled {
  --col-l-width: 0px;
}

html:not(.popup).right-column-disabled {
  --col-r-width: 0px;
}

//.left-column-disabled,
//.right-column-disabled {
//  body {
//    --side-cols-gap: 0px;
//  }
//}


/******************************
 Content Max Width
*******************************/

.wrapper,
.grid-cols-wrapper,
.title-wrapper {
  max-width: min(100%, var(--content-max-width, 100%));
}

.boxed-layout:not(.popup) .global-wrapper {
  max-width: min(100%, var(--content-max-width, 100%));;
}

/******************************
 Content Gutter
*******************************/

.site-wrapper > .container,
.top > .grid-rows > .grid-row > .grid-cols-wrapper,
.bottom > .grid-rows > .grid-row > .grid-cols-wrapper,
footer > .grid-rows > .grid-row > .grid-cols-wrapper,
.mobile-header > div {
  padding-inline-start: var(--content-gutter, 0px);
  padding-inline-end: var(--content-gutter-right, var(--content-gutter, 0px));
}


/******************************
 Page Titles
*******************************/

.title {
  position: relative;
  line-height: 1.2;
  margin: 0;
  padding: 0;
}

.page-title-text {
  background: inherit;
  color: inherit;
  position: relative;
  z-index: 2;
}

.site-wrapper > .page-title {
  &::after {
    display: none !important;
  }

  > .page-title-text {
    &::after {
      top: 1px;
    }
  }
}

html:not(.popup) .site-wrapper > h1:not(.page-title),
html:not(.popup) .site-wrapper > h2:not(.page-title),
html:not(.popup) .site-wrapper > h1 .page-title-text,
html:not(.popup) .site-wrapper > h2 .page-title-text {
  display: block;
  margin: 0 auto;
  max-width: var(--container-max-width, var(--content-max-width, 100%));
}

/******************************
 Title Module
*******************************/

.module-text {
  > .module-body {
    display: flex;
    flex-direction: column;
    justify-content: var(--text-module-align-v);
  }
}

.title-buttons {
  display: flex;
  flex-wrap: wrap;

  .btn {
    &::before {
      margin-inline-end: 6px;
    }

    &::after {
      margin-inline-start: 6px;
    }
  }
}

.title-module {
  display: flex;
  flex-direction: column;

  h3 {
    margin: 0;
    display: inline-block;
  }

  .title-divider {
    margin: 0 auto;

    &::before {
      line-height: 0;
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
    }
  }

  .subtitle {
    display: inline-block;

    &:empty {
      display: none;
    }
  }

  .title-label {
    width: fit-content;
  }

  .title-wrapper {
    position: relative;
    width: 100%;
    display:flex;
    flex-direction:column;


    &::before {
      position: absolute;
      width: 100%;
      top: 50%;
      text-align: center;
      transform: translateY(-50%);
    }
  }
}

.boxed-layout:not(.popup) body {
  &::before {
    content: "";
    pointer-events: none;
    top: 0;
    width: 100%;
    height: var(--height, 100%);
    max-height: var(--max-height, 100%);
    position: fixed;
    z-index: -1;
  }

  &::after {
    content: "";
    pointer-events: none;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    inset: 0;
    position: fixed;
    z-index: -2;
  }
}

.title-wrapper {
  margin: 0 auto;
}

.module-image > .module-body > {
  a {
    display: inline-block; //why inline-block?
    margin: 0;
    vertical-align: top;
    transition: all 75ms ease-out;
    text-decoration: none !important;

    width: 100%;
    height: 100%;

    &:not([href]),
    &[href='javascript:;'] {
      cursor: default;
    }

    img {
      border-radius: inherit;
      display: block;
    }

    em {
      font-style: normal;
      line-height: 1;
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;

      &::before {
        min-height: 1em;
        min-width: 1em;
        line-height: 1;
      }
    }

    &:hover {
      transform: scale(var(--scale-hover, 1))
    }

    &:active,
    &:active:hover {
      transform: scale(var(--scale-active, 1));
      transition: all 50ms;
    }
  }

  .image-caption {
    display: block;
    margin-top: .5em;
  }
}

/******************************
 Footer
*******************************/

footer {
  clear: both;
  margin-top: auto;
  z-index: 1;
}

.desktop.footer-reveal {
  .site-wrapper {
    z-index: 2;
  }

  header {
    z-index: 3;
  }

  footer {
    position: fixed;
    z-index: 0;
    width: 100%;
    bottom: 0;
  }
}

/******************************
 Breadcrumbs
*******************************/

.breadcrumbs {
  position: relative;
  display: flex;
  align-items: center;
  order: -1;
  padding-top: var(--breadcrumbs-top-padding, 0);
  padding-bottom: var(--breadcrumbs-bottom-padding, 0);
  --min-height: auto;
  min-height: var(--min-height);

  &::before {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    inset-inline-start: 50%;
    transform: translate3d(var(--transform-50-rtl, -50%), 0, 0);
    max-width: calc(var(--content-max-width, 100%) - var(--content-gutter, 0px) * 2);
  }
}

.breadcrumb {
  border-radius: 0;
  position: relative;
  background-color: transparent;
  width: 100%;
  margin: 0 auto;
  z-index: 2;
  --breadcrumb-top-padding-status: inherit;
  --breadcrumb-bottom-padding-status: inherit;
  padding: 0;
  max-width: calc(var(--content-max-width, 100%) - var(--content-gutter, 0px) * 2);

  > li {
    position: relative;
    word-break: break-all;
    margin-top: 0;

    a {
      color: inherit;
    }

    &:last-of-type {
      pointer-events: none;
      text-decoration: none;

      a {
        text-decoration: none;
      }
    }
  }

  .fa {
    font-family: inherit;
    color: inherit;
  }

  .fa-home {
    display: flex;
    align-items: center;

    &::after {
      font: inherit;
    }
  }
}

/******************************
 Empty page
*******************************/

#content h1 + p:only-of-type:last-child {
  margin: 0;
  padding: 10px;
}

/******************************
 Header Notice
*******************************/

.notice-module {
  overflow: hidden;

  .boxed-layout & {
    margin-top: calc(0px - var(--boxed-top-spacing, 0px) + var(--notice-top-spacing, 0px));
    margin-bottom: var(--notice-bottom-spacing, 0px);
  }

  .hn-content{
    p + *{
      margin-top: initial;
    }
  }

  > .module-body {
    height: auto;
    max-height: -moz-available;
    max-height: -webkit-fill-available;
    max-height: stretch;
    margin: 0 auto;
    position: relative;

    &::before,
    &::after {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      inset: 0;
      z-index: 0;
      pointer-events: none;
    }

    > div {
      position: relative;
    }

    > .hn-body {
      &::before {
        line-height: 1;
      }
    }
  }

  > .module-body, .hn-body {
    display: flex;
    align-items: center;
    justify-content: center;
    //width: 100%; //why?
  }

  .header-notice-cta {
    //margin-inline-start: 10px;
    flex-shrink: 0;
  }

  &:not(.notice-builder) {
    .hn-content {
      display: flex;
      flex-wrap: wrap;
      gap: 8px;
    }
  }

  .header-notice-close-button {
    z-index: 2;
    display: flex;
    align-items: center;
    justify-content: center;
    pointer-events: none;

    .btn.hn-close {
      pointer-events: auto;
      width: var(--button-width);
      height: var(--button-height, var(--button-width));
    }
  }

  &:not(.module-layout_notice) {
    order: -100;
    z-index: 110;
  }

  &.notice-builder {
    .hn-body, .hn-content {
      flex-grow: 1;
      height: 100%;

      &::before {
        display: none;
      }

      > .grid-rows {
        width: 100%;
        height: 100%;

        > .grid-row {
          height: 100%;

          > .grid-cols-wrapper {
            max-width: 100%;
            height: 100%;

            > .grid-cols {
              height: auto;
            }
          }
        }
      }
    }

    .hn-content {
      max-width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 0 auto;
      position: relative;
      z-index: 2;
    }
  }
}

/******************************
 Block Expand
*******************************/

.expand-block {
  position: relative;
}

.block-expand {
  padding: 2px 6px;
  font-size: 80%;
  pointer-events: auto;

  &::after {
    font-weight: inherit;
    -webkit-font-smoothing: initial;
  }
}

.block-expand-overlay {
  width: 100%;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  height: 30px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  pointer-events: none;
  background: linear-gradient(to bottom, transparent, var(--expand-overlay, var(--background-color, #fff)));
}

.expand-content.block-expanded {
  max-height: 100% !important;
  overflow: visible !important;
  padding-bottom: 40px;
  transition: all 0s !important;

  + .block-expand-overlay {
    background: none !important;
  }
}

.no-expand {
  .expand-content {
    max-height: 100% !important;
    overflow: visible !important;
  }

  .block-expand-overlay {
    display: none !important;
  }
}

/******************************
 Info Blocks
*******************************/

.module-info_blocks {
  counter-reset: top-counters;

  .info-blocks {
    counter-increment: top-counters;
  }

  .info-blocks-incremental {
    .info-block {
      &::before {
        font-family: inherit !important;
      }
    }
  }

  .info_blocks-grid:not(.auto-grid) {
    display: flex;
    flex-wrap: wrap;
  }

  .ipr-grid {
    .module-item:not(.swiper-slide):only-child {
      max-width: max-content;
      margin-inline: auto;
    }
  }

  > .module-body {
    > div {
      height: inherit;
    }
  }

  .info-blocks-image,
  .info-blocks-counter {
    .info-block {
      &::before {
        display: none;
      }
    }
  }

  .info-block-title {
    display: flex;
    align-items: center;

    &::before {
      counter-increment: top-counters 0;
      content: counter(top-counters) '.';
      margin-inline-end: 4px;
      font: inherit;
      display: none;
    }

    &::after {
      font-size: 90%;
      display: flex;
      align-items: center;
      justify-content: center;
      overflow: hidden;
      width: 1em;
      height: 1em;
      margin-inline-start: .4em;
    }
  }


  div.info-block-counter {
    line-height: 1;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    position: relative;

    min-width: var(--width);
    min-height: var(--height);

    &::after {
      content: "";
      display: inline-flex;
      align-items: center;
      justify-content: center;
      line-height: 1;
      font: inherit;
      margin: 0 -5px 0 5px;
    }
  }

  html[dir="rtl"] & {
    .info-block-counter {
      &::after {
        margin: 0 5px 0 -5px;
      }
    }
  }

  .info-block-img {
    position: relative;
    height: max-content;
    object-fit: cover;
  }
}

.info-block-button {
  margin-top: 8px;
  max-width: fit-content;

  &:empty {
    display: none;
  }
}

.info-block {
  position: relative;
  transition: all 70ms ease-out;
  height: 100%;
  display: flex;
  align-items: var(--align-v);
  justify-content: var(--align-h);
  gap: var(--gap, 8px);

  &::before {
    --width: auto;
    --height: auto;
    width: var(--width);
    height: var(--height);
    --gap: inherit;
  }

  &::before,
  &::after {
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 0 0 auto;
    line-height: 1;
    text-align: center;
    //transition: all 20ms ease-out;
  }

  &::after {
    align-self: center;
    margin-inline-start: auto;
  }

  .count-badge {
    position: absolute;
    right: 0;
    top: 0;
  }
}

.info-block-content {
  display: flex;
  flex-direction: column;
  justify-content: center;

  div {
    transition: all 70ms ease-out;
  }
}

a.info-block {
  --scale-hover: 1;

  .info-block-button {
    pointer-events: none;
  }

  &:hover {
    transform: scale(var(--scale-hover));
  }

  &:active {
    transform: scale(var(--scale-active, var(--scale-hover)));
  }

  img, &::before {
    position: relative;
    z-index: 2;
  }
}

.no-touchevents a.info-block {
  .info-block-content {
    > * {
      position: relative;
      z-index: 1;
    }

    &::before {
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      inset: 0;
      z-index: 0;
      transform: scaleX(0);
      will-change: transform;
      transition: transform 300ms cubic-bezier(0.770, 0.000, 0.175, 1.000), opacity 200ms $easeOutQuad;
      transform-origin: left;
    }
  }
}


/******************************
 Old Browser
*******************************/

.has-old-browser {
  position: fixed;
  overflow: hidden;
}

.old-browser {
  position: fixed;
  width: 100%;
  height: 100%;
  text-align: center;
  display: table;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 10000000;
}

.ob-content {
  display: table-cell;
  vertical-align: middle;
}

.ob-text {
  padding: 10px 15px;
  font-size: 20px;
  display: block;
  max-width: 750px;
  margin: 0 auto;
}

.ob-links {
  a {
    display: inline-block;
    margin: 15px;

    img {
      display: block;
      margin-bottom: 5px;
      transform: scale(.9);
      transition: all 0.075s ease-out;
    }

    &:hover img {
      transform: scale(1);
    }
  }
}

/******************************
 Scroll to Top
*******************************/

.scroll-top {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  position: fixed;
  z-index: 98;
  //right: 0;
  overflow: hidden;
  bottom: 0;
  transition: transform 400ms 350ms ease-out, opacity 150ms, background-color 150ms, bottom 400ms 300ms ease-out;
  cursor: pointer;
  margin-inline-start: var(--scroll-top-side-offset, 10px);
  margin-inline-end: var(--scroll-top-side-offset, 10px);
  margin-bottom: var(--scroll-top-bottom-offset, 10px);
  transform: translate3d(var(--translate-x, 0), calc(0px - var(--translate-y, 0px) - var(--product-page-buttons, 0px)), 0);

  .has-bottom-menu & {
    --translate-y: calc(var(--bottom-menu-display, 55px) + var(--bottom-menu-margin, 0px));
  }

  em {
    font-size: 50px;
    width: var(--scroll-top-width, 40px);
    height: var(--scroll-top-height, 40px);
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &.scroll-top-active {
    opacity: 1;
    visibility: visible;
    pointer-events: auto
  }
}

/******************************
 Loader
*******************************/

.journal-loading-overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  z-index: 9990;
  background: rgba(0, 0, 0, .3);
}

.journal-loading {
  position: absolute;
  display: flex;
  justify-content: center;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 0;

  em {
    &::before {
      content: "\e92f";
      font-family: $icons;
      line-height: 1;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -51%);
    }
  }

  + iframe {
    z-index: 10;
  }
}

/******************************
 Site Loader
*******************************/

.j-loader {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  position: fixed;
  z-index: 1000000;
  transition: transform 1s ease-in-out, opacity 6s ease;
  background: white;
}

.j-loader-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  align-items: center;

  .journal-loading {
    position: static;
    transform: none;
  }

  span {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: block;
  }
}

.j-loaded {
  transform: translate3d(-50%, -50%, 0) scale(0);
  opacity: 0;
}

