@import '../variables.scss';

.swiper {
  position: relative;
  width: 100%;
  overflow: initial;

  .swiper-pagination {
    display: flex;
    align-items: center;
    justify-content: center;
    left: 50%;
    right: auto;
    transform: translateX(-50%);

    > span {
      cursor: pointer;
      opacity: 1;
      transition: all 0.1s ease-out;
      //border-radius:0;
      + span {
        margin-inline-start: 8px;
      }
    }

    &:empty {
      padding: 0;
    }

    .swiper-has-pages & {
      display: block;
    }
  }

  .swiper-slide {
    height: auto;
    padding: 0;
    transition: opacity 200ms ease-out, transform 200ms ease-out;
  }

  .swiper-buttons {
    width: 100%;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: 2;
    pointer-events: none;

    > div {
      --width: 1.5em;
      width: var(--width);
      height: var(--height, var(--width));
      color: white;
      background: rgba(0, 0, 0, .5);

      &::before {
        content: '\e408';
        font-family: icomoon;
        line-height: 1;
        font-size: inherit;
      }

      &.swiper-button-next {
        &::before {
          content: '\e409';
        }
      }
    }
  }

  .swiper-button-next,
  .swiper-button-prev {
    pointer-events: auto;
    margin-top: auto;
    transform: translateY(-50%);
    background: transparent;
    display: flex;
    align-items: center;
    left: 0;
    justify-content: center;
    transition: transform 250ms ease, opacity 200ms linear;


    &.swiper-button-disabled {
      pointer-events: auto;
    }
  }

  .swiper-button-next {
    left: auto;
    right: 0;
  }

  .swiper:not(.swiper-has-pages) {
    .swiper-pagination,
    .swiper-button-next,
    .swiper-button-prev {
      display: none;
    }
  }

  .swiper-pagination-bullet:only-child {
    display: none !important;
  }
}

.swiper-button-disabled,
.swiper-container:not(.swiper-container-initialized) + .swiper-buttons .swiper-button-prev{
  transition:transform 250ms ease, opacity 0ms 250ms linear;
}

.carousel-mode {
  .swiper-wrapper {
    .swiper-slide{
      &:not(.swiper-slide-active){
        opacity: var(--inactive-opacity);
        transform: scale(var(--inactive-scale));
      }
      &:first-child:not(.swiper-slide-visible, .swiper-slide-prev){
        opacity: 1;
        transform: scale(1);
      }
    }
  }
}
