@import '../variables.scss';

.module-blocks {
  .block-item {
    transition: all 75ms ease;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      pointer-events: none;
      border-radius: inherit;
    }
  }
}

.block-body {
  transition: all 75ms ease;
  --display: flex;
  display: var(--display);
  flex-wrap: wrap;
  word-break: break-word;
  border-radius: inherit;

  &::after {
    content: "";
    clear: both;
    display: table;
  }

  > .module-title {
    width: -webkit-fill-available;
    width: -moz-available;
    width: stretch;
    order: -2;
  }
}

.block-wrapper {
  display: var(--display);
  flex-direction: column;
  border-radius: inherit;
  flex-grow: 1;
  position: relative;
  max-width: 100%;
  min-width: 0;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    pointer-events: none;
    border-radius: inherit;
  }

  .no-flexbox-gap & {
    margin: calc(var(--block-body-gap, 0px) / 2);
  }
}

.ie11 .block-wrapper {
  flex: auto;
}

.module-blocks{
  .module-item.block-item {
    .block-body {
      --image-split-ratio: 100%;
      gap: var(--block-body-gap, 0px);
      position: relative;

      > .block-header {
        min-width: calc(min(var(--image-split-ratio), 100%) - var(--block-body-gap, 0px) / 2);

        + .block-wrapper {
          flex-basis: calc(min(max(1%, 100% - var(--image-split-ratio)), 100%) - var(--block-body-gap, 0px) / 2)
        }
      }
    }
  }
}

.block-header {
  display: var(--display);
  flex-grow: 1;
  flex-direction: column;
  align-items: flex-start;
  position: relative;
  z-index: 1;
  transition: all 75ms ease;
  width: auto;
  flex-basis: 0;

  .no-flexbox-gap & {
    margin: calc(var(--block-body-gap, 0px) / 2);
  }

  img {
    transition: all 75ms ease;
    align-self: auto;
    display: flex;
  }

  .icon {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    transition: all 75ms ease;
    position: relative;

    &::before {
      transition: all 75ms ease;
      position: relative;
      z-index: 1;
    }

    &::after {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      border-radius: inherit;
      z-index: 0;
      pointer-events: none;
    }
  }
}

.blocks-grid {
  .blocks-grid:not(.auto-grid) {
    display: flex;
    flex-wrap: wrap;
  }
}

.block-content {
  position: relative;
  border-radius: inherit;

  &.block-image > img {
    border-radius: inherit;
  }

  > p:only-of-type {
    margin: 0;
  }
}

.block-footer {
  transition: all 75ms ease;

  .btn {
    transition: all 75ms ease;
    display: inline-flex !important;
  }
}

.block-map {
  border-radius: inherit;

  iframe {
    border-radius: inherit;
    width: 100%;
    height: 100%;
    transition: filter 175ms ease;
  }

  &::before {
    content: "";
    border-radius: inherit;
    z-index: 2;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    inset: 0;
    position: absolute;
    pointer-events: none;
    transition: opacity 250ms ease-out;
  }
}

.block-twitter {
  overflow-y: auto;
}

.module-blocks {
  height: inherit;
  max-width: 100%;

  .block-link {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 2;
  }

  .module-body {
    height: 100%;
  }

  .nav-tabs > li > a::before {
    margin-inline-end: 5px;
  }

  .block-item {
    position: relative;

    .btn {
      z-index: 2;
    }
  }
}

.module-testimonials {
  width: 100%;
  .module-item {
    display: flex;
    flex-direction: column;
  }

  .block-body {
    display: var(--display);
  }

  .block-header {
    display: block;

    img {
      border-radius: inherit;
    }

    .icon {
      font-size: 3em;
    }
  }

  .swiper-slide {
    cursor: -webkit-grab;
    cursor: grab;

    &:active {
      cursor: -webkit-grabbing;
      cursor: grabbing;
    }
  }

  .block-content {
    border-radius: 0;
  }

  .block-footer {
    width: -moz-available;
    width: -webkit-fill-available;
    width: stretch;
    z-index: 2;
  }
}

html[dir='rtl'] .module-blocks .nav-tabs > li > a::before {
  margin-inline-end: 0;
  margin-inline-start: 5px;
}

.module-faq{
  .module-item {
    counter-increment: top-counters;
    .panel-title{
      display: flex;
      align-items: center;
      gap: 16px;
      > a{
        width: 100%;
      }
      &::before{
        content: counter(top-counters);
        font-size: 20px;
      }
    }
  }
}

/******************************
 Catalog Blocks
*******************************/

.module-catalog_blocks {
  width: 100%;

  .block-content.block-catalog{
    &:empty{
      display: none;
    }
  }

  .block-manufacturers{
    flex-grow: 1;
  }

  .catalog-block-image {
    height: auto;
  }

  .module-item {
    height: 100%;
    position: relative;

    &.tab-pane.active {
      display: flex;
      flex-wrap: wrap;
    }
  }
  .block-products{
    position: relative;
    --element-padding: 0px;
  }
  .block-body {
    flex-grow: 1;
    max-width: 100%;
    --element-padding: 0px;
    + .block-products {
      width: 100%;
    }
    > .block-header{
      min-width: 0;
      flex-basis: clamp(var(--block-header-min-width, 50px), calc(var(--block-header-max-width, 30%) - var(--block-body-gap, 0px) / 2), 100%);
    }
    > .block-wrapper{
      min-width: 0;
      flex-basis: min(calc(100% - var(--block-header-max-width, 30%) - var(--block-body-gap, 0px) / 2), 100%);
    }
  }

  .block-image {
    margin: auto;
  }

  .block-content {
    height: 100%;
    max-width: min(min(100%, var(--catalog-item-initial-width, 100%)), min(100%, var(--catalog-item-min-width, 100%)));
  }

  .block-wrapper {
    &::before {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      inset: 0;
      z-index: 0;
    }
  }

  .block-header-image{
    overflow: hidden;
  }
  .block-header-data,
  .block-header-image,
  .block-head-icon {
    width: 100%;
    align-items: inherit;
    justify-content: inherit;
    img {
      width: var(--image-fill);
      height: var(--image-fill);
      object-fit: cover;
    }
  }

  .block-head-icon {
    display: flex;
    align-items: center;
    justify-content: center;

    em {
      max-width: 100%;
      --width: auto;
      width: var(--width);
      height: var(--height, var(--width));
    }
  }

  .block-category-data {
    display: flex;
    flex-direction: column;
    position: relative;
    align-items: inherit;
    justify-content: inherit;
  }

  .block-footer {
    width: -moz-available;
    width: -webkit-fill-available;
    width: stretch;
    display: flex;
    .btn span{
      white-space: nowrap;
      //overflow: hidden; why? interferes with link underline
      text-overflow: ellipsis;
      display: block;
      max-width: 100%;
      margin: 0 .2em;
    }
  }

  .nav-tabs {
    > li {
      max-width: 100%;

      > a {
        max-width: 100%;

        span {
          display: block;
          max-width: 100%;
        }

        span:not(.menu-image) {
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      }
    }
  }
}

.site-wrapper {
  .module-catalog_blocks {
    .module-item {
      &::after {
        content: "";
        width: calc(100vw - var(--scrollW, 0px));
        height: 100%;
        display: block;
        position: absolute;
        top: 0;
        left: 50%;
        transform: translate3d(-50%, 0, 0);
        z-index: -1;
        pointer-events: none;
      }
    }
  }
}
