@import '../variables.scss';

ul.nav-tabs {
  display: flex;
  flex-wrap: wrap;
  border: none;
  gap: var(--tabs-gap, 0px);
  width: stretch;
  padding-bottom: 1px;
  align-self: auto;

  padding-inline: var(--page-tabs-scroll-padding, 0px);
  margin-inline: calc(0px - var(--page-tabs-scroll-padding, 0px));
  justify-self: var(--align-self, initial);
  position: relative;
  min-height: var(--full-tabs-height, var(--tabs-height, auto));
  max-width: 100vw;
  --margin: calc(0px - var(--page-tabs-scroll-padding, 0px));

  &::before,
  &::after {
    display: none;
  }

  > li {
    display: flex;
    position: relative;
    margin-top: 0;
    transition: all 125ms ease;
    height: var(--tabs-height, auto);
    z-index: 1;

    > a {
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      line-height: 1.25;
      margin: 0;
      border: 0;
      border-radius: inherit;
      background: transparent;
      transition: all 125ms ease;

      .tab-image {
        img {
          border-radius: inherit;
        }
      }

      &::before {
        margin-inline-end: 5px;
        font-weight: normal;
        line-height: 1;
      }

      &:focus {
        background: initial;
      }
    }

    &:hover,
    &.active,
    &:focus {
      z-index: 2;

      > a {
        border: 0;
        background: transparent;

        &:hover,
        &:focus {
          background: inherit;
          border: 0;
        }
      }
    }

    &:first-child {
      border-start-start-radius: inherit;
      border-end-start-radius: inherit;
      border-left-width: 0;

      > a {
        border-start-start-radius: inherit;
        border-end-start-radius: inherit;
      }
    }

    &:last-child {
      border-start-end-radius: inherit;
      border-end-end-radius: inherit;

      > a {
        border-start-end-radius: inherit;
        border-end-end-radius: inherit;
      }
    }
  }
}

.no-flexbox-gap ul.nav-tabs {
  > li + li {
    margin-inline-end: 0;
    margin-inline-start: var(--tabs-gap, 0px);
    margin-top: 0;
  }
}

.tab-container {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: var(--align-tabs, flex-start);
}

.tab-content {
  position: relative;
  border-width: 1px;
  max-width: 100%;
  flex-grow: 1;
  min-width: 0;
  margin-block-start: var(--spacing, 0px);
  flex-basis: 100%;
  margin-inline-start: 0;
}

.tab-pane {
  padding: 0;
  background: transparent;
  border-radius: inherit;
}

.nav-tabs > li {
  z-index: auto;

  > a {
    position: relative;
    z-index: 1;
  }
}

.full-tabs-bg {
  min-height: var(--full-tabs-height, var(--tabs-height, auto));
}

.full-tabs-bg,
.tab-content::before {
  content: "";
  position: absolute;
  display: none;
  z-index: 0;
  width: $fullScroll;
  inset-inline-start: 50%;
  transform: translateX(var(--transform-50-rtl, -50%));
  top: 0;
  pointer-events: none;
}
.tab-content::before{
  height: 100%;
}

.blocks-image .product_tabs-image,
.blocks-top .product_tabs-top,
.blocks-bottom .product_tabs-bottom,
.blocks-details .product_tabs-details,
.module-catalog_blocks > .module-body > .tab-container .tab-container,
.module-blocks > .module-body > .tab-container .tab-container {
  > .full-tabs-bg,
  > .tab-content::before{
    display: none !important;
  }
}
