@import 'variables.scss';

/******************************
 Fullwidth backgrounds
*******************************/

$fullScroll: calc(100vw - var(--scrollW, 0px));

// Site content
.site-wrapper > .container {
  &::before {
    content: "";
    display: none;
    position: absolute;
    z-index: -1;
    width: 100vw;
    height: 100%;
    top: 0;
    left: 50%;
    margin-inline-start: -50vw;
    pointer-events: none;
  }
}

// Menus
.top-bar::before,
.menu-bar::after,
.header .mid-bar::before{
  content: "";
  top: 0;
  height: 100%;
  position: absolute;
  z-index: 0;
  pointer-events: none;
  --scrollW: inherit;
}

.top-bar::before,
.menu-bar::after,
.header .mid-bar::before,
.desktop-main-menu-wrapper .main-menu .mega-menu.menu-fullwidth > .j-dropdown {
  width: $fullScroll !important;
  inset-inline-start: 50%;
  transform: translate3d(var(--transform-50-rtl, -50%), 0, 0) scaleX(var(--bg-scale-x, 1));

  .boxed-layout & {
    max-width: min(100vw, var(--content-max-width, 100%));
  }
}
.header .mid-bar::before{
  background: inherit;
}
.header .mid-bar::before,
.menu-bar::after {
  transition: background-color 0.15s linear, transform 300ms ease-out;
}


.header-sticky {
  .header .mid-bar::before,
  .menu-bar::after {
    --bg-scale-x: 1;
  }
}

html[dir="rtl"] {
  --transform-50-rtl: 50%;
  --transform-100-rtl: 100%;

  .tab-content::before {
    --transform-50-rtl: 50%;
    --transform-100-rtl: 100%;
  }
}

// Cancel in boxed
.boxed-layout {
  .site-wrapper > .container::before,
  .top-bar::before,
  .menu-bar::after,
  .desktop-main-menu-wrapper .main-menu .mega-menu.menu-fullwidth > .j-dropdown,
  .full-tabs-bg,
  .tab-content::before {
    max-width: var(--content-max-width, 100%);
  }
}

