@import '../variables.scss';

/******************************
 Marquee
*******************************/

.marquee {
  width: 100%;
  margin: 0 auto;
  overflow: hidden;
  white-space: nowrap;
  overflow-x: auto;
  scrollbar-width: none !important;
  &::-webkit-scrollbar{
    display: none !important;
  }
  // fader overlay
  &::before,
  &::after {
    content: "";
    --fader-color: var(--background-color);
    display: none;
    position: absolute;
    pointer-events: none;
    top: 0;
    height: 100%;
    z-index: 1;
    border-radius: inherit;
  }

  &::before {
    left: 0;
    width: min(var(--fader-width-left, 120px), 40%);
    background-image: linear-gradient(-90deg, rgba(255, 255, 255, 0) 0, var(--fader-color) 90%, var(--fader-color))
  }

  &::after {
    right: 0;
    width: min(var(--fader-width-right, 120px), 40%);
    background-image: linear-gradient(90deg, rgba(255, 255, 255, 0) 0, var(--fader-color) 90%, var(--fader-color))
  }
}

.marquee-slider {
  --marquee-play-state: paused;
  --marquee-speed: 35s;
  --marquee-direction: marquee-left;
  --swap-direction: swap-left;
  display: inline-block;
  animation: var(--marquee-direction) var(--marquee-speed) linear infinite;
  will-change: transform;
  &:hover{
    animation-play-state: var(--marquee-play-state);
    .marquee-items-1{
      animation-play-state: var(--marquee-play-state);
    }
  }
}

.marquee-items{
  position: relative;
  display: inline-flex;
}

.marquee-item{
  display: inline-flex; //why inline-block?
  align-items: center;
  margin: 0 0 0 var(--item-gap, 0);
  text-decoration: none !important;
  transition: all 100ms ease-out;
  &::before{
    display: inline-block;
    line-height: 1;
    position: relative;
    top: 2px;
  }
  &::after{
    color: initial;
    -webkit-text-stroke-width: initial;
    line-height: 1;
    vertical-align: middle;
    margin: 0 0 0 var(--item-gap, 0);
  }
}

.marquee-items-1 {
  left: 0%;
  will-change: left;
  animation: var(--swap-direction) var(--marquee-speed) linear infinite;
}

html[dir="rtl"]{
  .marquee-items-2 {
    left: 100%;
  }
  .marquee-item{
    margin: 0 var(--item-gap, 0) 0 0;
    &::after{
      margin: 0 var(--item-gap, 0) 0 0;
    }
  }
}

/* Marque Transition Left */
@keyframes marquee-left {
  0% {
    transform: translateX(0)
  }
  100% {
    transform: translateX(-100%)
  }
}

@keyframes swap-left {
  0%, 50% {
    left: 0%;
  }
  50.01%,
  100% {
    left: 100%;
  }
}

/* Marque Transition Right */

@keyframes marquee-right {
  0% {
    transform: translateX(-100%)
  }
  100% {
    transform: translateX(0)
  }
}

@keyframes swap-right {
  0%, 50% {
    left: 100%;
  }
  50.01%,
  100% {
    left: 0%;
  }
}

@media (prefers-reduced-motion: reduce) {
  .marquee-slider, .marquee-items {
    animation-play-state: paused !important;
  }
}
