@import 'variables.scss';

/******************************
 Product Page
*******************************/

.product_tabs-default {
  max-width: 100%;

  ul.nav-tabs {
    position: relative;
    z-index: 2;
  }
}

.product-blocks {
  .block-content {
    width: -moz-available;
    width: -webkit-fill-available;
    width: stretch;
  }

  .block-body > .block-wrapper {
    display: flex;
    flex-direction: column;
  }
}

.product_blocks {
  display: flex;
  flex-wrap: wrap;
  gap: var(--product-blocks-gap, 16px);

  &:not(:last-child) {
    margin-bottom: var(--product-blocks-gap, 16px);
  }

  //display: flex; // interferes with carousel mode modules added in TAB builder
  //flex-wrap: wrap;
  //align-items: center;
  > .product-extra-popup:not(.panel, .tab-pane) {
    display: inline-flex;

    > .btn {
      width: 100%;
    }
  }
}

.blocks-content_top {
  margin-bottom: var(--product-page-split-gap, 20px);
}

.blocks-content_top,
.blocks-default {
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;

  [class*='product-extra-'] {
    flex-grow: 1;
  }
}

.product-extra {
  > div > .title {
    align-items: center;
  }

  .product-extra-short_description {
    .block-content {
      display: block;
    }
  }
}

.product-info {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  width: stretch;

  gap: var(--product-page-split-gap, 20px);

  > div {
    position: relative;
  }

  .product-left,
  .product-right {
    flex-grow: 1;
    flex-basis: 0;
  }

  .product-left {
    min-width: calc(var(--product-page-split, 50%) - (var(--product-page-split-gap, 20px) / 2));
  }

  .product-right {
    min-width: max(1%, 100% - var(--product-page-split, 50%) - var(--product-page-split-gap, 20px) / 2);
  }

  .product-details {
    display: flex;
    flex-direction: column;

    .page-title {
      order: -100;
      display: none;
    }

    > div:not(.page-title, :last-child) {
      margin-bottom: var(--sections-gap, 0px);
    }
  }
}

.product-right-wrapper {
  > .product-blocks,
  .product-blocks > div:not(:first-child) {
    margin-top: var(--sections-gap, 0px);
  }
}

@media only screen and (max-width: 470px) {
  .product-info {
    .product-left,
    .product-right {
      flex-basis: 100%;
    }
  }
}

.product-blocks {
  display: flex;
  flex-direction: column;

  [class*='product-extra'] > .panel-heading,
  [class*='product-extra'] > .module-title,
  .product-blocks-popup-btn,
  .product_tabs > ul > li[class*='product_tabs'] > a {
    &::before {
      margin-inline-end: 5px;
    }
  }
}

/******************************
 Product Image
*******************************/
.product-image {
  position: relative;
  margin: 0 auto;
  display: flex;
  flex-direction: column;

  > div {
    order: 5;
  }

  .main-image {
    overflow: hidden;
    .swiper-slide[data-gallery] img {
      cursor: zoom-in;
    }

    .swiper-controls {
      display: none;
    }

    .swiper-container {
      overflow: hidden;
      border-radius: inherit;
      width: 99.99%; //possible fix for subpixel image visibility
    }
  }

  .swiper {
    width: auto;
    width: -moz-available;
    width: -webkit-fill-available;
    width: stretch;
  }

  .swiper.main-image {
    padding: 0;
  }

  .additional-image {
    cursor: pointer;
  }

  .additional-images {
    max-width: 100vw;

    &.swiper {
      padding: 0;

      .swiper-pagination {
        display: none;
      }
    }
  }

  .additional-images:not(.swiper) {
    display: grid;
    grid-template-columns: repeat(var(--ipr, 4), 1fr);
    gap: var(--gap, 10px);
  }

  .product-labels {
    z-index: 1;
  }

  img {
    width: 100%;
    height: auto;
    transition: all 90ms ease;
  }

  &.direction-horizontal {
    &.position-bottom {
      .additional-images {
        padding-block-start: var(--additional-image-spacing, 0px);

        .swiper-buttons {
          margin-block-start: calc(0px - var(--additional-image-spacing, 0px) / 2);
        }
      }
    }

    &.position-top {
      .additional-images {
        padding-block-end: var(--additional-image-spacing, 0px);

        .swiper-buttons {
          margin-block-start: calc(0px - var(--additional-image-spacing, 0px) / 2);
        }
      }
    }
  }

  &.direction-vertical {
    flex-direction: row;

    &.position-left {
      .additional-images {
        padding-inline: 0 var(--additional-image-spacing, 0px);

        .swiper-buttons {
          margin-inline-start: calc(0px - var(--additional-image-spacing, 0px) / 2);
        }
      }
    }

    &.position-right {
      .additional-images {
        padding-inline: var(--additional-image-spacing, 0px) 0;

        .swiper-buttons {
          margin-inline-start: calc(var(--additional-image-spacing, 0px) / 2);
        }
      }
    }

    .additional-images {
      height: 0;
      overflow: visible;


      .swiper-container {
        width: -moz-available;
        width: -webkit-fill-available;
        width: stretch;
        height: 100%;
        overflow: hidden;
      }

      .swiper-wrapper {
        flex-direction: column;
      }

      .swiper-pagination {
        display: none !important;
      }

      .swiper-buttons {
        width: 100%;
        height: 100%;
        opacity: 0;
        transform: translate3d(0, 0, 0);
        left: 0;
        right: 0;
        top: 0 !important;
        margin: 0;

        > div {
          left: 50%;
          top: 0;
          transform: translateX(-50%);
          max-width: 100%;

          background: rgba(0, 0, 0, .5);

          &::before {
            transform: rotate(90deg);
            color: rgba(255, 255, 255, .75);
          }
        }

        .swiper-button-next {
          top: auto;
          bottom: 0;
        }
      }
    }
  }

  &.position-left,
  &.position-top {
    .additional-images {
      order: -1;
    }
  }
}

.product-image.direction-vertical .additional-images .swiper-buttons {
  opacity: 0;
  transition: opacity .5s 50ms ease-out;
}

.product-image.direction-vertical .additional-images .swiper-container-initialized + .swiper-buttons {
  opacity: 1;
}

