//Cloud Zoom

.zm-viewer,
.zm-handler {
  pointer-events: none;
  z-index: 99;
  //cursor: zoom-in;
}

.zm-viewer {
  opacity: 0;
  transition: opacity .25s .25s ease-in-out;
}

/******************************
 Product Stats
*******************************/

.product-info {

  .product-stats {
    position: relative;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    b {
      font-weight: normal;
      margin-inline-end: 3px;
    }

    ul {
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
      margin: 0;
      //overflow-x: auto;
    }

    li {
      margin-inline-end: 10px;
      display: flex;
      align-items: center;
      margin-top: 0;
      flex-wrap: wrap;

      &::before {
        font-size: 7px;
        margin-inline-end: 5px;
        vertical-align: middle;
      }
    }
  }

  .product-manufacturer {
    img {
      max-width: 150px;
      width: 100%;
    }

    &.brand-image {
      display: flex;
      justify-content: flex-end;

      a {
        align-self: flex-start;
      }
    }

    > a {
      display: inline-flex;
      flex-direction: column;

      img {
        margin-bottom: 3px;
        align-self: center;
      }

      > span {
        text-align: center;
        display: block;
        max-width: 130px;
      }
    }
  }

  .custom-stats {
    width: 100%;
    flex-grow: 1;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;

    b {
      margin-inline-end: 5px;
    }

    > div {
      display: flex;
      align-items: center;
      justify-content: center;

      span {
        //padding: 0 0.2em;
      }

      &:first-of-type {
        margin-inline-end: 15px;
      }
    }
  }
}

html[dir='rtl'] {
  .product-info {
    .product-stats {
      b {
        margin-inline-end: initial;
        margin-inline-start: 3px;
      }

      li {
        margin-inline-end: initial;
        margin-inline-start: 20px;

        &::before {
          margin-inline-end: initial;
          margin-inline-start: 5px;
        }
      }
    }

    .custom-stats {
      b {
        margin-inline-end: initial;
        margin-inline-start: 5px;
      }

      > div {
        &:first-of-type {
          margin-inline-end: initial;
          margin-inline-start: 10px;
        }
      }
    }
  }
}

/******************************
 Rating
*******************************/

.product-info {
  .rating {
    display: flex;
    align-items: center;
    flex-wrap: wrap;

    > span {
      display: inline-flex;

      &:last-of-type {
        margin-inline-end: 10px;
      }
    }

    div {
      a {
        display: inline-flex;
        margin: 0 2px;
      }
    }

    b {
      font-weight: normal;
    }
  }

  .rating-stars {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    margin-inline-end: 5px;
  }
}

/******************************
 Price
*******************************/

.product-info {

  .price-wrapper {
    display: flex;
    flex-direction: column;

    .product-labels-price {
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      max-width: max-content;
      order: 0;
      gap: var(--label-gap, 5px);

      .product-label {
        margin: 0;
      }
    }

    > div {
      order: 5;
      text-align: start;
    }

    .price-group {
      order: 1;
    }
  }

  .product-price-group {
    z-index: 2;
    pointer-events: none;
    display: flex;

    .product-stats {
      pointer-events: auto;
      flex-grow: 1;
    }
  }

  .price-group {
    display: flex;
    flex-wrap: wrap;
    pointer-events: auto;
    white-space: nowrap;
    gap: var(--product-page-price-gap, 0);

    .product-price,
    .product-price-new {
      font-size: 2rem;
    }

    .product-price-old {
      font-size: 2rem;
    }
  }
}

/******************************
 Default Options
*******************************/

.product-info {
  .product-options {
    .options-title + .form-group {
      margin-top: 0 !important;
    }

    .radio + .radio,
    .checkbox + .checkbox {
      margin: 0;
    }

    h3 {
      margin: 0;
    }

    .img-thumbnail {
      border-radius: 0;
      border-width: 0;
      outline: none;
      padding: 0;
      background-color: transparent;
    }

    .help-block {
      margin-bottom: 0;
      width: 100%;
      font-size: 85%;

      &:empty {
        margin: 0;
      }
    }

    .option-value {
      line-height: 1.2;
    }
  }

  .form-group:not(:last-child) {
    &::after {
      display: block !important;
      --input-group-spacing: inherit;
      margin-top: calc(var(--input-group-spacing, 0px) - var(--label-gap, 0px));
      content: "";
      width: 100%;
      border-width: 0 0 1px 0;
    }
  }
}

/******************************
 Push Options
*******************************/

.push-option {
  input {
    display: none;
    position: relative;
    margin-top: 3px;
    width: 100%;
    text-align: center;
    border: none;
    padding: 0;
    min-height: auto;
    min-width: auto;
    background: none;
    line-height: 1;
    box-shadow: none;
  }

  .checkbox, .radio {
    padding: 0 !important;
    margin: 0 !important;
  }

  .option-value {
    position: relative;
  }

  .option-wrapper {
    display: flex;
    align-items: center;
    flex-grow: 1;
    position: relative;
  }

  .option-wrapper:active {
    transform: translate3d(0, 0, 0) scale(.97);
    transition: all 50ms ease;
  }

  .option-wrapper:not(.push-option-image) .option-price {
    margin-inline-start: .3em;
  }

  .push-option-image {
    img {
      border-radius: inherit;
      object-fit: cover;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      inset: 0;
    }

    .option-value {
      margin: 1px 5px;
    }
  }

  .option-value, img {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 200ms, transform 30ms;
    align-self: center;
    margin: 0;

    &:hover {
      cursor: pointer;
    }
  }

  > div[id*='input'] {
    display: flex;
    //align-items: flex-start;
    --options-display: wrap;
    flex-wrap: var(--options-display);
    gap: var(--push-option-spacing, 7px);

    .push-option-image {
      .option-value {
        padding: 0;
      }
    }

    > div {
      width: auto;
      max-width: 100%;
    }

    label {
      padding: 0;
      position: relative;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      height: 100%;
      margin: 0;
    }
  }
}

.no-flexbox-gap {
  .push-option {
    .option-wrapper {
      margin: calc(var(--push-option-spacing, 0px) / 2);
    }

    > div:not(.text-danger) {
      margin: calc(0px - var(--push-option-spacing, 0px) / 2)
    }
  }
}

/******************************
 Add to Cart
*******************************/

.product-info {

  .product-details {

    .button-group-page {
      bottom: 0;
      left: 0;
      right: 0;
      transition: transform 350ms 300ms ease-out;
      border-radius: 0;

      .btn-cart, .btn-extra {
        .btn-text {
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
      }
    }

    > .minimum {
      margin-bottom: 0;
    }

    .buttons-wrapper {
      display: flex;
      align-items: stretch;
      flex-wrap: wrap;
      width: stretch;
      margin: 0 auto;

      .alert {
        margin-top: 10px;
        padding: 5px 10px;
        width: 100%;
      }
    }

    .wishlist-compare {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-wrap: wrap;

      > div {
        overflow: hidden;
      }
    }

    .extra-group, .wishlist-compare {
      font-size: 0;
    }

    .stepper-group {
      display: flex;
      align-items: center;

      .control-label {
        align-self: center;
        position: relative;
        background: none;
        flex-basis: auto;
      }
    }
  }
}

.product-extra-short_description #read-more-desc {
  white-space: nowrap;
}

.popup-quickview {

  .main-image {
    img {
      cursor: default;
    }
  }

  .direction-vertical .additional-images .swiper-buttons:not(.slider-navigation){
    width: 100% !important;
    > div{
      inset-inline-start: 50%;
      transform: translate3d(-50%,0,0);
    }
  }

  .extra-group {
    display: none !important;
  }

  .description {
    position: relative;
  }

  .product-info .product-details {
    .stepper-group .stepper {
      height: calc(100% - var(--quickview-buttons-gap, var(--product-page-buttons-gap, 0px)) * 2);
    }

    .buttons-wrapper {
      margin: calc(0px - var(--quickview-buttons-gap, var(--product-page-buttons-gap, 0px)));

      .stepper-group > *,
      .extra-group > a,
      .wishlist-compare > a,
      .btn-more-details {
        margin: var(--quickview-buttons-gap, var(--product-page-buttons-gap, 0px));
      }
    }

    .btn-cart {
      min-height: calc(100% - var(--quickview-buttons-gap, 0px) * 2);
    }
  }
}

// Buttons gap

.product-info .product-details .buttons-wrapper {
  margin: calc(0px - var(--product-page-buttons-gap, 0px));

  .stepper-group > *,
  .extra-group > a,
  .wishlist-compare > a,
  .btn-more-details {
    margin: var(--product-page-buttons-gap, 0px);
  }
}

.btn-more-details.btn {
  justify-content: center !important;
}

html:not(.popup) .product-info .product-details .buttons-wrapper {
  .wishlist-compare {
    gap: var(--product-page-wishlist-buttons-gap, var(--product-page-buttons-gap, 0px));
  }
}

/******************************
 Product Tabs
*******************************/

.product-left {
  .product-blocks {
    margin-top: 20px;
  }
}

#tab-specification {
  .table {
    margin-bottom: 0 !important;
  }
}

.review-links a {
  cursor: pointer;
}

#review {
  > p {
    margin: 0;
  }

  table {
    margin-bottom: 0 !important;

    &:not(:nth-last-child(2)) {
      border-bottom-width: 0 !important;
    }
  }

  .rating {
    margin-top: 5px;
  }
}

#form-review {
  .rate {
    display: flex;
    align-items: center;
    min-height: 35px;

    span {
      position: relative;
      top: -1px;
    }

    > * {
      margin: 0;

      + * {
        margin: 0 0 0 10px;
      }
    }
  }

  .help-block {
    margin-bottom: 0;
  }

  legend {
    margin-top: 15px;
  }

  h4 {
    margin-top: 10px;
    margin-bottom: 20px;
    text-transform: none;
  }
}

.tags-title {
  margin-inline-end: 5px;
}

.expand-content {
  > p:last-of-type {
    margin-bottom: 0;
  }
}
