@import '../variables.scss';

/******************************
 Sticky Header
*******************************/

.desktop-main-menu-wrapper.menu-bar {
  transition: padding 200ms linear, margin 200ms linear, height 200ms linear;
}

.desktop-header-active {
  header {
    transition: padding-bottom var(--header-transition, 250ms);

    + * {
      transition: margin-top var(--header-transition, 250ms);
    }

    .mid-bar {
      transition: height var(--header-transition, 250ms);
    }
  }

  &.sticky-full {
    header {
      position: sticky;
      top: 0;
    }
  }

  &.sticky-default {
    header {
      position: sticky;
      top: calc(0px - var(--desktop-top-bar, 35px));
    }
  }

  &.sticky-menu header {
    position: sticky;
    top: calc(0px - (var(--desktop-top-bar, 35px) + var(--desktop-header, 80px)));
  }

  &.header-sticky:not(.sticky-menu) {
    header {
      padding-bottom: calc(var(--desktop-header, 80px) - var(--desktop-sticky));

      .mid-bar {
        height: var(--desktop-sticky, var(--desktop-header, 80px));
      }

      pointer-events: none;

      .header, .mobile-header {
        pointer-events: auto;
      }
    }
  }

  /* @todo check reflow trigger */
  &.header-sticky:not(.sticky-menu):not(.over-content) header + * {
    //margin-top: calc(0px - (var(--desktop-header) - var(--desktop-sticky)));
  }
}

