@import '../variables.scss';

/******************************
 Mobile Top Bar
*******************************/

.mobile-header-active {

  .desktop-mobile-main-menu-container {
    display: none !important;
  }

  .mobile-header {
    max-width: 100vw;
    width: 100%;
    transition: background-color 100ms ease-out;

    .mobile-top-bar {
      height: var(--mobile-top-bar, 35px);
      display: flex;
      align-items: center;
      justify-content: space-between;
      position: relative;
      z-index: 2;
    }

    .mobile-top-menu-wrapper {
      //height: 100%; //why?
      display: flex;
      align-items: center;

      > .top-menu {
        width: 100%;
      }
    }
  }

  .dropdown-backdrop {
    display: none;
    pointer-events: none;
  }

  #cart {
    background: none !important;

    .cart-label {
      display: none;
    }

    a {
      background: inherit;
      border-radius: inherit;

      em {
        background: none;
      }
    }
  }

  #search {
    > .dropdown-menu {
      padding: var(--mobile-search-padding, 7px);
      padding-top: var(--mobile-search-padding-top, var(--mobile-search-padding, 7px));
    }

    .header-search {
      height: var(--mobile-search-bar, 40px);
    }
  }
  .mobile-2 {
    .mobile-search-wrapper{
      height: var(--mobile-search-bar, 50px);
    }
    #search{
      .header-search{
        height: 100%;
      }
    }
  }

  .mobile-bar ::before{
    line-height: 1.1;
  }
}

.mobile-custom-menu {
  height: 100%;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-inline: 8px;

  .links-text {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }

  .count-badge {
    position: absolute;
    transform: translate3d(calc(var(--badge-offset, 75%) + var(--x, 0px)), calc(-100% + var(--y, 0px)), 0);
    margin-top: var(--margin-top, 0px);
  }
}

.mobile-header {
  #cart-items {
    transform: translate3d(calc(var(--badge-offset, 75%) + var(--x, 0px)), calc(-100% + var(--y, 0px)), 0);
  }

  &.mobile-3 #search > .dropdown-menu {
    padding-inline-start: calc(var(--mobile-search-padding, 0px) / 2);
    padding-inline-end: calc(var(--mobile-search-padding, 0px) / 2);
  }
}

html[dir="rtl"]{
  .mobile-header{
    --badge-offset: -75%;
  }
}

.mobile-3 {
  .menu-trigger {
    min-height: 40px;
  }
}

.mobile-header{
  .mobile-secondary-menu{
    padding: 0;
  }
}

.mobile-secondary-menu,
.mobile-wrapper-top-menu{
  max-width: 100%;
  padding: 0;

  .top-menu {
    height: auto;
    overflow-x: auto;
    overflow-y: hidden;
    scrollbar-width: none;

    &::-webkit-scrollbar {
      display: none;
    }

    > .j-menu {
      position: static;
      flex-wrap: nowrap;

      > li {
        > a {
          align-items: center;
          justify-content: center;
        }

        &.dropdown {
          position: static;
          --translate-x: -100%;
          --align-scroll-start: calc((var(--element-offset, 0px)) - var(--scroll-offset, 0px) + var(--menu-item-gap, 0px) / 2);
          --align-scroll-center: calc(var(--element-offset, 0px) - var(--scroll-offset, 0px) + var(--element-width, 0px) / 2);
          --align-scroll-end: calc(var(--element-offset, 0px) - var(--scroll-offset, 0px) + var(--element-width, 0px) - (var(--menu-item-gap, 0px) / 2));
          --align-scroll: var(--align-scroll-start);

          > .j-dropdown {
            left: var(--align-scroll, 0) !important;
            transform: translate3d(var(--translate-x), var(--dropdown-from), 0) !important;

            &::before {
              --menu-item-gap: inherit;
            }
          }

          &:first-child {
            --align-scroll-start: calc((var(--element-offset, 0px)) - var(--scroll-offset, 0px));
            --align-scroll-center: calc(var(--element-offset, 0px) - var(--scroll-offset, 0px) + var(--element-width, 0px) / 2 - var(--menu-item-gap, 0px) / 4);
          }

          &:last-child {
            --align-scroll-center: calc(var(--element-offset, 0px) - var(--scroll-offset, 0px) + var(--element-width, 0px) / 2 + var(--menu-item-gap, 0px) / 4);
          }
        }
      }
    }
  }
}

.mobile-top-bar {
  .top-menu,
  .language-currency {
    height: auto;
  }
}

html[dir='rtl'] {
  .mobile-1 {
    .mobile-bar-group {
      margin-inline-start: 0;
      margin-inline-end: auto;
    }
  }
}

.mobile-main-menu-container .mobile-main-menu-wrapper .j-menu > .dropdown > a::after {
  display: none !important;
}

html[dir="rtl"] .mobile-header .language-currency.top-menu > div:last-child .dropdown.drop-menu.animating > .j-dropdown {
  right: auto;
}


.desktop-mobile-main-menu-wrapper,
.mobile-main-menu-wrapper {
  .main-menu-item {
    flex-direction: column;
  }

  .menu-trigger,
  .search-trigger,
  .mobile-custom-menu {
    position: relative;
  }
}


/******************************
 Mid Bar
*******************************/

.mobile-header-active {

  .mobile-bar {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: var(--mobile-bar, 60px);
    transition: all 100ms ease-out;
    z-index: 1;
  }

  .mobile-bar-group {
    display: flex;
    align-items: center;

    > div {
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }

  #cart a {
    cursor: pointer;
  }

  #cart-total {
    display: none !important;
  }

  .mobile-cart-wrapper {
    .dropdown-menu::before {
      display: none !important;
    }
  }

  /******************************
   Logo
  *******************************/

  .mobile-logo-wrapper {
    width: auto;
    flex-grow: 1;
    position: var(--position);
    margin: var(--margin)
  }

  /******************************
   Main Menu Trigger
  *******************************/

  .menu-trigger {
    font-size: 20px;
    cursor: pointer;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none !important;

    .menu-icon {
      display: flex;
      flex-direction: column;
      align-items: center;
    }

    button {
      background: transparent;
    }

    span {
      display: none;
    }
  }

  /******************************
   Mini Search
  *******************************/
  .mini-search {
    #search {
      position: static;
      width: 100%;
      height: 100%;

      > button {
        max-width: 100%;
        height: 100%;
        display: block;
      }

      .header-search {
        width: 100%;
      }

      > .dropdown-menu {
        input {
          border-top-left-radius: inherit !important;
          border-bottom-left-radius: inherit !important;
        }
      }
    }
  }

  .tt-menu {
    padding-top: 0;

    &::before {
      display: none;
    }
  }

  .full-search {
    #search {
      width: 100%;
      height: 100%;
    }
  }

  .mobile-bar-group {
    ::before {
      line-height: 1;
      text-align: center;
    }

    ::after {
      font-size: 11px;
      line-height: 1.2;
      text-align: center;
    }
  }

  //Mobile 1
  .mobile-1 {
    display: flex;
    flex-direction: column;

    .mobile-logo-wrapper {
      max-width: 50%;
      flex-grow: 0;
      height: 100%;
    }

    .mobile-bar-group {
      height: 100%;

      margin-inline-start: auto;
      margin-inline-end: 0;

      > div {
        height: 100%;
      }
    }

    #cart {
      width: 100%;
      height: 100%;

      > a {
        height: 100%;
        justify-content: center;
        border-radius: inherit;
      }
    }
    .mobile-custom-menu{
      order: 5;
    }
    .mini-search{
      order: 10;
    }
    .mini-cart{
      order: 20;
    }
  }

  // Mobile 2
  .mobile-2 {
    display: flex;
    flex-direction: column;

    .mobile-logo-wrapper {
      flex-grow: 1;
      max-width: none;
    }

    .mobile-bar > div {
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
    }

    .mobile-search-wrapper {
      width: 100%;
    }

    #cart {
      width: 100%;
      height: 100%;

      > a {
        width: 100%;
        height: 100%;
        justify-content: center;
      }
    }
  }

  // Mobile 3
  .mobile-3 {
    #logo {
      margin: 0 auto;
    }

    .mobile-bar {
      justify-content: space-between;
    }

    .mobile-logo-wrapper {
      height: var(--mobile-logo-wrapper, var(--mobile-bar, 50px));
    }

    .mobile-custom-menu {
      padding: 0 15px;
    }

    .mobile-search-wrapper {
      flex: 1;
    }

    .mobile-bar-group {
      justify-content: space-between;
    }
  }

  header {
    z-index: 120;
  }
}

/******************************
 iOS form elements min size
*******************************/

.ios, .ipad {
  * {
    background-attachment: initial !important;
  }
}

.ios {
  img {
    align-self: center;
  }

  input:not([type=button]):not([type=submit]), textarea {
    font-size: 16px !important;
  }

  select.form-control, select {
    padding-block: 0 !important;
    padding-inline-end: 0 !important;
  }

  select:focus, textarea:focus, input:focus {
    font-size: 16px;
  }
}

