@import "../variables";
@import "../easings";

// disable swiper 6 prev/next content, interferes with old swiper

.swiper-button-next,
.swiper-button-prev {
  background: unset;

  &::after {
    content: none;
    display: none;
  }
}


/******************************
 Slider
*******************************/
.mobile .swiper {
  overflow: hidden;
}

.swiper-container {
  overflow: hidden;
  max-width: 100vw; //additional images at 360px
}

.swiper-container:has(.module-item:only-child){
  + .swiper-buttons,
  + .swiper-pagination-wrapper{
    display: none !important;
  }
}

.module-slider,
.module-banners_grid {
  .swiper-button-next,
  .swiper-button-prev {
    background: unset;

    &::after {
      content: none;
      display: none;
    }
  }

  counter-reset: thumb;
  display: flex;
  flex-direction: column;

  .journal-loading {
    z-index: 1;
  }

  .swiper-initialized {
    .journal-loading {
      display: none;
    }
  }

  .swiper-container:not(.swiper-container-initialized) + .swiper-buttons .swiper-button-prev {
    opacity: 1;
  }

  .swiper-container:not(.swiper-initialized) + .swiper-buttons .swiper-button-prev {
    opacity: .35;
  }

  .slider-wrapper {
    position: relative;
    flex-grow: 1;
    max-height: 100%; // header notice full height

    .swiper-container {
      display: flex;
      align-items: center;
      height: 100%;

      &:not(.swiper-initialized) {
        .swiper-slide:not(.swiper-slide-active) {
          [data-swiper-parallax-x],
          [data-swiper-parallax-opacity],
          [data-swiper-parallax-scale] {
            visibility: hidden;
          }
        }
      }

      &::before {
        content: "";
        display: none;
        width: 100%;
        height: 100%;
        position: absolute;
        z-index: 2;
        pointer-events: none;
      }
    }

    .swiper-slide {
      max-width: 100%;
      transition: all 200ms ease-out;
      height: auto;
      display: flex;
      align-items: center;
      justify-content: center;
      transform: translate3d(0, 0, 0);
      -webkit-backface-visibility: hidden;
      transform-style: preserve-3d;

      &::after {
        content: "";
        pointer-events: none;
        padding-top: calc(var(--image-height, 0) / var(--image-width, 1) * 100%);
      }

      .slide-link {
        position: absolute;
        inset: 0;
        z-index: 2;
      }

      &:last-child {
        margin-inline-end: 0;
      }

      &.swiper-slide-active {
        z-index: 1;
      }
    }
  }

  /******************************
   slide layers
  *******************************/

  .slide-content {
    position: relative;
    display: flex;
    align-items: center;
    width: 100%;
    height: 100%;

    --object-position-h: center;
    --object-position-v: center;
    background-position-x: var(--object-position-h);
    background-position-y: var(--object-position-v);

    &::before {
      content: "";
      display: none;
      width: 100%;
      height: 100%;
      position: absolute;
      z-index: 1;
      pointer-events: none;
      border-radius: inherit;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      inset: 0;
      transition: all 200ms ease-out;
    }
  }

  .slide-content-custom {
    transition: background-color 75ms ease-out;
  }

  .slide-content-image,
  .slide-content-category,
  .slide-content-video,
  .slide-content-product {
    display: flex;
    justify-content: flex-end;
  }

  .slide-content-image,
  .slide-content-category,
  .slide-content-video,
  .slide-content-product,
  .slide-content- {
    > img,
    > video {
      position: absolute;
      object-fit: cover;
      object-position: var(--object-position-h) var(--object-position-v);
      width: 100%;
      height: 100%;
      border-radius: inherit;

      &:hover {
        pointer-events: none;
      }
    }
  }

  .slide-content-img {
    > img {
      // filter: grayscale(var(--grayscale, 0%)) brightness(var(--brightness, 100%)) saturate(var(--saturate, 100%)) contrast(var(--contrast, 100%)) blur(var(--blur, 0px));
    }
  }

  .slide-cols {
    position: relative;
    margin: 0 auto;
    z-index: 2;
    max-width: 100%;
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    flex-grow: 1;
  }

  .slide-link + .slide-cols {
    pointer-events: none;

    .module-subitem {
      z-index: 3;
      pointer-events: auto;
    }
  }

  .slide-col {
    position: relative;
    max-width: 100%;
    max-height: 100%;
    --element-padding: 0px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    flex-grow: 1;
    min-width: 0;
    padding: var(--slides-cols-left-padding, 0);
    flex-basis: calc((var(--slides-cols-auto-stack, 0px) - (100 * var(--stack-unit, 1%))) * 9999);

    .slide-button {
      .slide-button-group {
        margin: calc(var(--slide-layer-gap-custom, 0px) - (var(--slide-button-gap, 0px) / 2)) calc(0px - (var(--slide-button-gap, 0px) / 2)) calc(0px - (var(--slide-button-gap, 0px) / 2)) calc(0px - (var(--slide-button-gap, 0px) / 2));
        display: flex;
        flex-wrap: wrap;
      }

      .btn {
        margin: calc(var(--slide-button-gap, 0px) / 2);
        flex-grow: 1;
        transition: all 100ms ease-out;

        > span {
          white-space: nowrap;
          //overflow: hidden;
          //text-overflow: ellipsis;
        }

        &::before {
          display: flex;
          align-items: center;
          justify-content: center;
        }
      }
    }

    &::after {
      content: "";
      display: none;
      --overlay-x: 0;
      --overlay-y: 0;
      --overlay-scale: 1;
      --overlay-rotate: 0deg;
      --overlay-x-hover: var(--overlay-x);
      --overlay-y-hover: var(--overlay-y);
      --overlay-scale-hover: var(--overlay-scale);
      --overlay-rotate-hover: var(--overlay-rotate);
      --overlay-speed: 200ms;
      --overlay-ease: ease-out;
      position: absolute;
      width: 100%;
      height: 100%;
      max-width: var(--overlay-max-width, 100%);
      max-height: var(--overlay-max-height, 100%);
      transition: all var(--overlay-speed) var(--overlay-ease);
      transform: translate3d(var(--overlay-x), var(--overlay-y), 0) scale(var(--overlay-scale)) rotate(var(--overlay-rotate));
      z-index: 0;
      pointer-events: none;
    }
  }

  .slide-layers {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    gap: var(--slide-layer-gap, 0);
    width: -moz-available;
    width: -webkit-fill-available;
    width: stretch;
    height: 100%;
    position: relative;
    z-index: 1;
    transform: scale(var(--layers-scale, 100%));
    container-type: inline-size;
  }

  .module-subitem {
    max-width: 100%;
    z-index: 2;

    &.slide-products {
      width: -moz-available;
      width: -webkit-fill-available;
      width: stretch;
    }
  }

  .slide-cols {
    .module-subitem:not(.slide-icon) {
      position: relative;
      margin-top: var(--slide-layer-gap-custom, 0px);
    }
  }

  .slide-image {
    --image-scale: 1;
    transform: scale(var(--image-scale));
    transition: all 150ms ease-out;

    &:hover {
      transform: scale(var(--scale-hover, var(--image-scale)));
    }

    &:active {
      transform: scale(var(--scale-active, var(--image-scale)));
    }

    &::before {
      content: "";
      display: none;
      width: 100%;
      height: 100%;
      position: absolute;
      z-index: 1;
      pointer-events: none;
      border-radius: inherit;
    }
  }

  figure {
    position: relative;
    display: flex;
    flex-direction: column;
    overflow: hidden;

    .slide-layer-image-link {
      position: absolute;
      width: 100%;
      height: 100%;
      z-index: 3;
    }

    img {
      object-fit: cover;
      max-height: var(--max-height, 100%);
      --drop-shadow-x: 0px;
      --drop-shadow-y: 0px;
      --drop-shadow-blur: 0px;
      --drop-shadow-color: transparent;
      --drop-shadow-x-hover: var(--drop-shadow-x);
      --drop-shadow-y-hover: var(--drop-shadow-y);
      --drop-shadow-blur-hover: var(--drop-shadow-blur);
      --drop-shadow-color-hover: var(--drop-shadow-color);
      filter: drop-shadow(var(--drop-shadow-x) var(--drop-shadow-y) var(--drop-shadow-blur) var(--drop-shadow-color));
    }

    &::before{
      content: "";
      display: none;
      position: absolute;
      inset: 0;
      background: rgba(0,0,0,.5);
      pointer-events: none;
      z-index: 2;
    }
  }

  figcaption {
    background: rgba(0, 0, 0, .5);
    color: rgba(255,295,295,.9);
    position: absolute;
    z-index: 2;
    line-height: 1.4;
    font-size: 13px;
    padding:1.2em;
    width: 100%;

    span {
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: var(--truncate-lines);
      --truncate-lines: unset;
    }
  }

  .slide-text {
    max-width: var(--layer-max-width, 100%);
    display: flex;
    align-items: center;
    transition: all 75ms ease-out;
    line-height: 1.3;

    span {
      box-decoration-break: clone;

      s {
        max-width: 100%;
        margin: 0 .15em;
        font: inherit;
      }
    }

    .slide-text-layer {
      display: flex;
      align-items: center;
      &::before{
        align-self: start;
        margin-inline-end: 5px;
        font-size: 1.1em;
      }
    }

    .slide-text-item {
      display: inline;

      &::before,
      &::after {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        min-width: 1em;
        min-height: 1em;
        font-size: .8em;
        transition: all 75ms ease-out;
        margin-inline-end: .3em;
      }

      &::after {
        margin-inline-start: .3em;
      }
    }
  }

  .slide-icon {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    margin: 0 !important;
    width: 100%;
    height: 100%;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    inset: 0;
    z-index: 2;

    .can-rotate {
      position: relative;
      transition: all 75ms ease-out;
      display: flex;
      align-items: center;
      justify-content: center;
      width: min(1em, 100%);
      height: min(1em, 100%);
      max-width: 100%;
      max-height: 100%;
      padding: .5em;
      --bg-size: 80;
      --bg-size-factor: 8;
      font-size: min(var(--bg-size) * 1px, var(--bg-size) / var(--bg-size-factor) * 1vw);

      .has-icon {
        font-size: 1em;
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;

        &::before {
          line-height: 1;
        }
      }

      .has-svg {
        width: min(1em, 100%);
        height: min(1em, 100%);
        position: absolute;
        display: flex;
        align-items: center;
        justify-content: center;

        > svg {
          stroke-linejoin: round;
          //stroke-width: var(--stroke-width, 0);
          //stroke-dasharray: var(--stroke-dasharray);
          //stroke-dashoffset: var(--stroke-dasharray);
          //--stroke-linejoin: round;
          //--stroke-dasharray: 0;
          //--dash-direction: normal;
          //--dash-speed: 3000ms;
          //--dash-delay: 1000ms;
          //--dash-ease: ease-out;
          //--dash-iterations: infinite;
          //--dash-play: running;
        }

        > * {
          width: 100% !important;
          height: 100% !important;
        }
      }

      .has-text {
        position: absolute;
        line-height: 1;
        font-size: .3em;
        color: white;
        text-align: center;
        padding: .4em;
        transform: translate3d(var(--x, 0), var(--y, 0), 0);

        s {
          display: block;
          text-align: center;
          font-size: 60%;
        }
      }
    }
  }

  .module-subitem.slide-video{
    --video-layer-width: auto;
    --video-layer-height: auto;
    width: var(--video-layer-width);
    height: var(--video-layer-height);
    aspect-ratio: 16/9;
    iframe, video{
      width: 100%;
      height: 100%;
    }
  }

  /******************************
  Thumbnails
  *******************************/

  .slider-thumbs {
    position: relative;
    z-index: 2;

    .swiper-slide {
      width: calc((100% - (var(--thumbs-per-view) - 1) * var(--thumb-gap, 0px)) / var(--thumbs-per-view));
      margin-inline-end: var(--thumb-gap, 0px);
      min-width: var(--thumb-min-width, 0);
      max-width: var(--thumb-max-width, 100%);
      transition: all 120ms ease-out;
      cursor: pointer;
      padding: var(--thumb-padding, 0);

      img {
        width: 100%;
      }

      &.swiper-slide-active {
        z-index: 1;
      }

      &::before {
        content: "";
        position: absolute;
        transition: all 100ms ease-in-out;
        pointer-events: none;
        z-index: 2;
        top: var(--thumb-padding, 0);
        right: var(--thumb-padding, 0);
        bottom: var(--thumb-padding, 0);
        left: var(--thumb-padding, 0);
      }
    }

    .thumb-text {
      pointer-events: none;
      position: absolute;
      color: white;
      margin: var(--thumb-text-offset-x, 0);
      margin-bottom: var(--thumb-text-offset-y, var(--thumb-text-offset-x, 0));
      display: flex;
      align-items: center;
      max-height: 100%;
      z-index: 3;
      top: auto;
      right: auto;
      bottom: 0;
      left: 0;
      transform: translate3d(var(--thumb-padding, 0), calc(0px - var(--thumb-padding, 0)), 0);

      &::before {
        color: currentColor;
      }
    }
  }

  /******************************
   parallax
  *******************************/

  .parallax-bg {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    width: var(--parallax-bg-width, 140%);
    height: 100%;
  }

  .parallax-bg::before {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    opacity: var(--parallax-opacity, 1);
  }

  /******************************
   controls
  *******************************/

  .swiper-buttons {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate3d(-50%, 0, 0);
    z-index: 2;
    pointer-events: none;
    display: flex;
    align-items: center;
    justify-content: space-between;

    > div {
      pointer-events: auto;
      width: var(--button-width, 1.7em);
      height: var(--button-height, var(--button-width, 1.7em));
      font-size: min(3rem, 6vmin);
      left: auto;
      right: auto;
      top: auto;
      bottom: auto;
      transform: translate3d(var(--buttons-offset-x, 0), var(--buttons-offset-y, 0), 0);
      position: relative;
      margin: 0;
      transition: opacity 10ms, background-color 100ms ease;

      &:active {
        opacity: .9;
      }

      &::before {
        content: '\e408';
        font-family: icomoon;
        line-height: 1;
        font-size: inherit;
      }

      &.swiper-button-next {
        transform: translate3d(var(--button-next-offset-x, var(--buttons-offset-x, 0)), var(--button-next-offset-y, var(--buttons-offset-y, 0)), 0);

        &::before {
          content: '\e409';
        }
      }
      &.swiper-button-disabled{
        pointer-events: none;
      }
    }
  }

  .swiper-pagination {
    transform: translate3d(var(--pagination-offset-x, 0), var(--pagination-offset-y, 0), 0);
  }

  .swiper-pagination-fraction {
    color: white;
    width: auto;
    bottom: auto;
    top: auto;
    left: auto;
    right: auto;
    position: relative;

    span {
      margin: 0 2px;
    }
  }

  .swiper-scrollbar {
    position: absolute;
    left: 50%;
    -ms-touch-action: none;
    background: rgba(0, 0, 0, .1);
    bottom: 0;
    transform: translate3d(-50%, 0, 0);
    z-index: 20;
    width: 100%;
    width: -moz-available;
    width: -webkit-fill-available;
    width: stretch;
    margin: 0 auto;
    display: flex;
    align-items: center;

    .swiper-scrollbar-drag {
      box-sizing: content-box;
    }
  }

  .swiper-container-horizontal > .swiper-scrollbar {
    bottom: 10px;
  }

  .swiper-pagination-wrapper {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 50%;
    top: 0;
    transform: translate3d(-50%, 0, 0);
    z-index: 3;
    pointer-events: none;
  }

  .swiper-pagination-bullets {
    bottom: auto;
    display: flex;
    position: relative;
    justify-content: center;
    left: auto;
    right: 0;
    flex-wrap: wrap;
    width: fit-content;
    pointer-events: auto;
    cursor: default;
    gap: var(--bullet-gap, .5em);

    .swiper-pagination-bullet {
      width: var(--bullet-width, 1em);
      height: var(--bullet-height, var(--bullet-width, 1em));
      margin: 0;
      font-size: 12px;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      transition: all 100ms ease-out;
      opacity: .5;
      position: relative;
      border-radius: 0;
      overflow: hidden;
      cursor: pointer;

      .no-flexbox-gap & {
        margin: calc(var(--bullet-gap, .5em) / 2);
      }

      .swiper-timeline {
        position: absolute;
        z-index: -1;
        height: 100%;
        display: none;
        border-radius: 0;
        transition: opacity 100ms ease;
        background: var(--timer-bullet-color, var(--timer-color, #007AFF));
      }

      &:hover {
        transform: scale(var(--bullet-active-scale-hover, 1));
      }

      &.swiper-pagination-bullet-active {
        transform: scale(var(--bullet-active-scale, 1));
        opacity: 1;

        .swiper-timeline {
          display: block;
        }
      }

      &::before {
        color: white;
        position: relative;
        z-index: 12;
        counter-increment: thumb;
        content: "" counter(thumb) "";
      }
    }
  }

  .swiper-pagination-dots {
    display: flex;
    position: relative;
    flex-wrap: wrap;
    width: fit-content;
    pointer-events: auto;
    gap: var(--bullet-gap, .5em);


    .swiper-pagination-bullet {
      width: var(--bullet-dot-size, 1em);
      height: var(--bullet-dot-size, 1em);
      border-radius: 50%;
      margin: 0;
      overflow: visible;

      .no-flexbox-gap & {
        margin: calc(var(--bullet-gap, .5em) / 2);
      }

      &:not(.swiper-pagination-bullet-active) {
        .swiper-timeline-dots {
          display: none;
        }
      }
    }
  }
}

