@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.3);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/******************************
 Background Slider
*******************************/

body > .module-background_slider{
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  position: fixed;
  z-index: -2;
  ~ .global-wrapper,
  ~ .global-wrapper > .site-wrapper{
    background: none !important;
  }
}
.module-background_slider {
  &::before {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 1;
    pointer-events: none;
  }

  .slider-wrapper {
    position: relative;
    z-index: 0;
    width: 100%;
    height: 100%;
  }

  .swiper-container {
    width: 100%;
    height: 100%;
  }

  .slide-content {
    width: 100%;
    height: 100%;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  .swiper-slide {
    height: 100%;
    will-change: opacity;
    display: flex;
    align-items: center;
    justify-content: flex-end;

    &::before {
      content: "";
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      inset: 0;
      z-index: 1;
      pointer-events: none;
    }

    &::after {
      content: "";
      position: absolute;
      z-index: 2;
      pointer-events: none;
      margin: var(--content-gutter, 0);
      line-height: 1;
    }
  }
}

/******************************
Static layer
*******************************/

.module-slider {
  .static-text-wrapper {
    width: 100%;
    height: 100%;
    z-index: 2;
    top: 0;
    left: 0;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    pointer-events: none;
    padding: var(--content-gutter, 24px);

    .slider-static-text {
      pointer-events: all;
      z-index: 2;
      position: relative;

      span {
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }
  }
}

/******************************
 Slide width
*******************************/
.module-slider {
  --slide-min-height: initial;
  --slide-max-height: 100%;

  //.swiper-wrapper{
  //  transition-timing-function: ease-in-out;
  //}

  .slider-wrapper > .swiper-container > .swiper-wrapper > .swiper-slide {
    width: var(--slide-width, 100%);
    margin-inline-end: calc(var(--slide-gap, 0px));
    min-height: var(--slide-min-height);
    max-height: var(--slide-max-height);
  }

  .slide-cols {
    gap: var(--cols-gap, 0px);
  }

  .slide-col-left {
    min-width: calc(var(--slides-cols-width, 50%) - var(--cols-gap, 0px));
    z-index: 1;
  }

  .slide-col-right {
    min-width: max(1%, 100% - var(--slides-cols-width, 50%));
    padding: var(--slides-cols-right-padding, 0px);
  }
}

/******************************
 Banners grid
*******************************/

.module-banners_grid {
  --image-scale-speed: 100ms;
  --image-scale-ease: ease-out;
  --scale-hover: 1;

  .banners-grid {
    height: 100%;
  }

  .banners-wrapper {
    height: 100%;

    > .swiper-slide {
      min-height: var(--slide-min-height);
      max-height: var(--slide-max-height);
    }
  }

  > .module-body {
    &::before {
      content: "";
      position: absolute;
      pointer-events: none;
      z-index: 0;
      width: 100%;
      height: 100%;
    }
  }

  .slide-cols {
    gap: var(--cols-gap, 0px);

    &::before {
      content: "";
      align-self: stretch;
      border-width: 0.5px;
      border-style: solid;
      border-color: transparent;
      flex-basis: calc((var(--slides-cols-auto-stack, 0px) - (100 * var(--stack-unit, 1%))) * 9999);
      order: 2;
      display: none;
    }
  }

  .slide-col {
    --col-width: min(100%, max(var(--slides-cols-width), var(--col-left-fixed-width, var(--slides-cols-width))));
  }

  .slide-col-left {
    min-width: calc(var(--col-width, 50%) - 1px - (var(--cols-gap, 0px) * 2));
    z-index: 1;
    order: 0;
  }

  .slide-col-right {
    min-width: max(1%, 100% - var(--col-width, 50%));
    padding: var(--slides-cols-right-padding, 0px);
    order: 3;
  }

  .slide-content {
    min-height: inherit;
  }

  .slide-image {
    --image-layer-scale: 1;
    --image-layer-scale-speed: 100ms;
    --image-layer-scale-ease: ease-out;
    --image-layer-scale-hover: 1;
    transform: scale(var(--image-layer-scale));
    transition: transform var(--image-layer-scale-speed) var(--image-layer-scale-ease);

    img {
      transition: transform var(--image-layer-scale-speed) var(--image-layer-scale-ease);
    }
  }

  .flex-grid {
    .module-item {
      height: auto;
      max-width: 100%;
    }
  }
}

.no-touchevents {
  .module-banners_grid {
    .slide-content {
      > img {
        transition: transform var(--image-scale-speed) var(--image-scale-ease);
      }

      &:hover {
        > img {
          transform: scale(var(--image-scale-hover, 1));
        }

        .slide-image {
          transform: scale(var(--image-layer-scale-hover, 1));

          img {
            filter: drop-shadow(var(--drop-shadow-x-hover) var(--drop-shadow-y-hover) var(--drop-shadow-blur-hover) var(--drop-shadow-color-hover));
          }
        }
      }
    }

    .slide-content:hover {
      .slide-col-left,
      .slide-col-right {
        &::after {
          max-width: var(--overlay-max-width-hover, var(--overlay-max-width));
          max-height: var(--overlay-max-height-hover, var(--overlay-max-height));
          transform: translate3d(var(--overlay-x-hover), var(--overlay-y-hover), 0) scale(var(--overlay-scale-hover)) rotate(var(--overlay-rotate-hover));
        }
      }
    }
  }
}

.touchevents,
.no-touchevents {
  .module-banners_grid {
    .slide-content-image:active {
      > img {
        transform: scale(var(--image-scale-active, 1));
      }
    }

    .slide-content:active {
      .slide-image {
        transform: scale(var(--image-layer-scale-active, 1));
      }
    }
  }
}


/******************************
 Timer
*******************************/

.slider-timer {
  position: absolute;
  z-index: 11;
  width: 100%;
}

.swiper-timeline {
  position: relative;
  width: 100%;
  height: 2px;
  z-index: 11;
  border-radius: inherit;
  background: var(--timer-color, #007AFF);
  transform: translate3d(0, 0, 0) scaleX(var(--timeline-progress, 0));
  will-change: transform;
  transform-origin: left;
  transition: opacity 150ms ease;
}

.slider-thumbs .swiper-slide:not(.swiper-slide-active) .swiper-timeline {
  opacity: 0;
}

.module-slider,
.module-banners_grid {
  --stroke-width: 2px;
}

.swiper-pagination-dots {
  svg {
    width: calc(var(--bullet-dot-size, 1em) * 2);
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: absolute;
    transition: opacity 200ms ease;
  }

  circle {
    stroke: var(--timer-bullet-color, var(--timer-color, #007AFF));
    stroke-width: var(--stroke-width, 2px);
    stroke-dasharray: 100;
    stroke-dashoffset: var(--timeline-progress-circle, 0);
    transform-origin: center;
    transform: rotate(-90deg);
  }
}

/******************************
 Fade transition image scale
*******************************/

.module-slider {
  .swiper-wrapper {
    transition-timing-function: var(--ease);
  }

  --image-scale-from: 1;
  --transition-speed: 300ms;

  .slider-wrapper:not(.slider-carousel) {
    .swiper-initialized {
      .swiper-slide {
        .slide-content-image {
          > img {
            transform: scale(1);
            will-change: transform;
            transition: transform var(--image-scale-speed, var(--transition-speed)) var(--ease);
          }
        }

        &:not(.swiper-slide-visible) {
          .slide-content-image {
            > img {
              transform: scale(var(--image-scale-from));
            }
          }
        }
      }
    }
  }
}

// for lax.js
.ios {
  .module-slider {
    .slide-cols,
    .slide-content::before {
      transform: translate3d(0, 0, 0.01px);
    }
  }
}

html[dir="rtl"] {
  .module-slider,
  .module-banners_grid {
    .slide-text::before {
      margin-inline-end: 0;
      margin-inline-start: .3em;
    }
  }
}


