@import '../variables.scss';

.side-column{
  .module-filter{
    .panel-group{
      flex-direction:column;
    }
  }
}

.module-filter {
  display: flex;
  flex-direction: column;

  flex-wrap: wrap;

  .module-title{
    width: -moz-available;
    width: -webkit-fill-available;
    width: stretch;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    &::after{
      display: none !important;
    }
    .filter-title-text{
      flex-grow: 1;
    }
  }

  .panel-group{
    display:flex;
    flex-direction:column;
    flex-wrap: wrap;
  }

  .panel-body{
    overscroll-behavior: auto;
    > div{
      > label{
        display: flex;
        align-items: center;
        margin:0;
        transition: all 0.05s ease;
        cursor: pointer;
        position: relative;
        &:only-child{
          width: fit-content !important;
        }
        > *{
          position: relative;
          z-index: 1;
        }
        img{
          transition: all 75ms;
        }
        .filter-item-bg{
          pointer-events: none;
          position: absolute;
          top:0;
          right:0;
          bottom:0;
          left:0;
          z-index: 0;
          transition: all 75ms;
        }
        + label{
          margin-top: var(--item-spacing, 0px);
          &::before{
            content: "";
            border-width: 1px 0 0 0;
            position: absolute;
            top: calc(0px - var(--item-spacing, 0px) / 2);
            left: 0;
            width: -moz-available;
            width: -webkit-fill-available;
            width: stretch;
          }
        }
        input{
          margin:0 2px 0 0;
          + img{
            transition: all 0.05s ease;
          }
        }
        .links-text{
          margin: 0 5px;
          transition: all 0.05s ease;
        }
        .count-badge{
          margin: 0;
          z-index: 2;
          transform: translateX(0);
        }
      }
    }
  }

  .image-only{
    .panel-body{
      > div{
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        > label{
          display: flex;
          align-items: center;
          margin:0;
          padding:0;
          position: relative;
          + label{
            border-top: 0;
          }
          img{
            margin:0;
            width: 100%;
            height: auto;
          }
          input{
            display: none;
          }
          .links-text{
            display: none;
          }
          .count-badge{
            position: absolute;
            right: 0;
            top: 0;
          }
        }
      }
    }
  }

  .links-text{
    overflow: hidden;
    text-overflow: ellipsis;
    word-break: break-word;
  }


  //Price
  .filter-price{
    padding-bottom:20px;
  }

  .range-slider {
    min-height: 40px;
    top: -18px;
    position: relative;
  }

  .irs{
    height: auto;
  }
  .js-range-slider {
    display: none !important;
  }
  .irs-min, .irs-max, .irs-from, .irs-to, .irs-single{
    display: none;
  }
  .irs-slider{
    background: $blue;
    border: none;
    box-shadow: none;
    transition: all 0.03s ease;
    border-radius:0;
  }
  .irs-line{
    background: transparent;
    border-radius: 0;
    border: none;
  }
  .irs-bar{
    background: transparent;
    border: none;
  }

  .extra-controls{
    display: flex;
    align-items: center;
    position: relative;
    input{
      width: calc(50% - 25px);
      text-align: center;
      border:none;
      box-shadow: none;
      outline: none;
    }
    .currency-symbol{
      padding:0 4px;
    }
    .currency-left{
      ~ .currency-left{
        margin-inline-start: auto;
      }
    }
    input:first-child + .currency-right{
      margin-inline-end: auto;
    }
  }

  .module-item {
    order: 999;

    .checkbox, .radio {
      margin-top: 0;
    }
    .select select {
      width: 100%;
    }
  }

  .panel-body{
    overflow-x: hidden;
    &::before, &::after{
      display: none;
    }
  }
}

html[dir='rtl']{
  .module-filter{
    .extra-controls{
      .currency-symbol{
        ~ .currency-symbol{
          margin-inline-start: initial;
          margin-inline-end: auto;
        }
      }
    }
  }
}

.btn.mobile-filter-trigger {
  position: fixed;
  bottom: var(--mobile-filter-bottom-offset, 10px);
  margin-inline-start: var(--mobile-filter-side-offset, 0);
  left: 50%;
  transform: translate3d(-50%,0,0) scale(var(--scale-hover, 1));
  transition: all 75ms ease, margin-bottom 300ms 400ms ease-out;
  z-index: 100;
  html[dir="rtl"] &{
    margin-inline-end: var(--mobile-filter-side-offset, 0);
  }
  html:not(.mobile-filter-active) & {
    display: none !important;
  }
}


