@import 'variables.scss';

/******************************
 General
*******************************/

::before,
::after {
  position: relative;
  font-size: inherit;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: normal;
}

.fa {
  position: relative;
}

.h1, .h2, .h3, h1, h2, h3, .h4, .h5, .h6, h4, h5, h6 {
  margin: 0;
}

p {
  margin: 0;

  > br:only-child {
    display: none;
  }
}

s {
  text-decoration: none;
  font-size: 85%;
  font-style: normal;
  font-weight: normal;
  white-space: normal;
  line-height: 1;
  max-width: 125px;
  transition: all 0.075s ease-out;
}

li::before {
  min-width: 1em;
  min-height: 1em;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  line-height: 1;
}

/******************************
 Images
*******************************/

img {
  max-width: 100%;
  height: auto;
  //align-self: flex-start; //not centering image in Icons Menu //Stretches images height
}

.img-thumbnail {
  padding: 0;
  border-radius: 0;
  border: none;
  background: transparent;
}

.language-flag {
  max-width: none;
}

.lazyload,
.lazyloading {
  opacity: 0;
}

.lazyloaded {
  opacity: 1;
  transition: opacity 300ms;
}

@keyframes fa-rotate {
  0% {
    transform: perspective(120px) rotateX(0deg) rotateY(0deg);
  }
  50% {
    transform: perspective(120px) rotateX(-180.1deg) rotateY(0deg);
  }
  100% {
    transform: perspective(120px) rotateX(-180deg) rotateY(-179.9deg);
  }
}

/******************************
 Count Badge
*******************************/

.count-badge {
  font-size: 1rem;
  margin-inline-start: 5px;
  min-width: 1.73em;
  height: 1.73em;
  padding: .3em;
  display: inline-flex;
  flex: 0 0 auto;
  align-items: center;
  justify-content: center;
  line-height: 0;
  margin-top: var(--margin-top, 0px);
  transform: translate3d(var(--x, 0px), var(--y, 0px), 0);
}

.sticky-menu.header-sticky .desktop-main-menu-wrapper {
  --sticky-offset: auto;

  .main-menu-item > a > .count-badge,
  .main-menu-item > a > .menu-label,
  #cart-items {
    transition: all 100ms;
  }
}

.text-start{
  text-align: start;
}
.text-center{
  text-align: center;
}
.text-end{
  text-align: end;
}
/******************************
 Rating
*******************************/

.fa-stack {
  font-size: 15px;
  width: 1.1em;
  height: 1em;
  line-height: 1;

  em {
    font-size: 1em;

    &::before {
      font-family: $icons;
    }
  }
}

.fa-star-o::before {
  content: "\e900";
}

.fa-star::before {
  content: "\e902";
}

.fa-star,
.fa-star + .fa-star-o {
  color: gold;
}

.fa-stack-1x, .fa-stack-2x {
  position: absolute;
  left: 0;
  width: 100%;
  text-align: center;
}

/******************************
 Product Labels
*******************************/

.product-labels {
  overflow: hidden;
  width: 100%;
  height: 100%;
  position: absolute;
  inset: 0;
  pointer-events: none;
  display: flex;
  flex-direction: column;
}

.product-label {
  position: relative;
  pointer-events: none;
  max-width: 100%;
  //overflow: hidden; // why? clips shadows
  order: 5;
  min-width: 0;

  strong {
    position: relative;
    min-width: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    transition: opacity .2s;
    font-weight: normal;
    font-size: 85%;
    line-height: 1;
    color: white;
    background: rgba(0, 0, 0, .85);
    padding: 0.45em 0.55em;
    will-change: transform;

    &::before{
      line-height: 1;
    }

    s {
      font-size: inherit;
      display: inline-block;
      white-space: nowrap;
      margin: 0 .2em;
      max-width: none;
      position: relative;
    }
  }
}


.product-labels-outside {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: var(--label-gap, 5px);

  span.product-label.product-label-default {
    flex-grow: var(--label-grow, 0);
    margin: 0;

    strong {
      width: 100%;
      padding: 0;

      &:hover {
        transform: scale(var(--scale-hover));
      }

      &:active {
        transform: scale(var(--scale-active, var(--scale-hover)));
        transition: all 50ms ease;
      }

      a:not([href]) {
        cursor: auto;
        color: inherit;
        font: inherit;
        padding: 0.45em 0.55em;
        text-decoration: none;
      }

      a[href] {
        pointer-events: auto;
        padding: 0.45em 0.55em;
        color: inherit;
        font: inherit;
        cursor: pointer;
      }
    }
  }
}

.no-flexbox-gap {
  .product-labels-outside {
    .product-label {
      margin: calc(var(--label-gap, 0) / 2) !important;
    }
  }
}

.product-label-diagonal {
  width: 150%;
  max-width: none;
  text-align: center;
  position: absolute;
  margin: calc(20px + var(--label-diagonal-offset, 0px)) !important;
  padding: 0;
  top: 0;
  left: 0;
  right: initial;
  bottom: initial;
  transform: translate3d(-50%, -50%, 0) rotate(-45deg);

  strong {
    display: block;
    background: transparent;
  }
}

html[dir='rtl'] {

  .product-label-diagonal {
    top:0;
    left:initial;
    right:0;
    bottom:initial;
    transform: translate3d(50%, -50%, 0) rotate(45deg);
  }

  .table-responsive > .table-bordered > thead > tr > th:first-child, .table-responsive > .table-bordered > tbody > tr > th:first-child, .table-responsive > .table-bordered > tfoot > tr > th:first-child, .table-responsive > .table-bordered > thead > tr > td:first-child, .table-responsive > .table-bordered > tbody > tr > td:first-child, .table-responsive > .table-bordered > tfoot > tr > td:first-child {
    border-right: 0;
    border-left: 1px;
  }

  .table-responsive > .table-bordered > thead > tr > th:last-child, .table-responsive > .table-bordered > tbody > tr > th:last-child, .table-responsive > .table-bordered > tfoot > tr > th:last-child, .table-responsive > .table-bordered > thead > tr > td:last-child, .table-responsive > .table-bordered > tbody > tr > td:last-child, .table-responsive > .table-bordered > tfoot > tr > td:last-child {
    border-left: 0;
    border-right: 0;
  }

  .product-label-default {
    text-align: left;
  }

  //.product-label-diagonal {
  //  left: auto;
  //  right: 0;
  //  text-align: center;
  //  transform: translate3d(calc(0px - (-130px)), 8px, 0) rotate(45deg);
  //
  //  &.label-2 {
  //    transform: translate3d(calc(0px - (-112px)), 26px, 0) rotate(45deg);
  //  }
  //
  //  &.label-3 {
  //    transform: translate3d(calc(0px - (-94px)), 43px, 0) rotate(45deg);
  //  }
  //}
}

/******************************
 Countdown
*******************************/

.countdown {
  position: relative;
  color: white;
  background-color: rgba(0, 0, 0, .4);
  transition: all 0.2s;
  visibility: visible;
  opacity: 1;
  width: -moz-available;
  width: -webkit-fill-available;
  width: stretch;
  display: flex;
  align-items: center;
  text-align: center;
  padding: 10px;
  font-size: 85%;
  max-width: 100%;
  min-width: 0;
  container-type: inline-size;

  > div {
    width:calc(var(--custom-item-size, 25%) - var(--element-margin-inline-start, 0px));
    max-width: calc(25% - var(--element-margin-inline-start, 0px));
    min-width: 0;
    border-width: 0;
    border-inline-start-width: 1px;
    display: flex;
    flex-direction: column;
    line-height: 1;
    font-size: calc(12px + 1.2cqw);
    font-weight: bold;

    &:first-of-type {
      border-inline-start-width: 0;
    }

    span {
      display: block;
      font-size: clamp(10px, 0.5em, 18px);
      font-weight: normal;
      position: relative;
      text-align: center;
      margin-top: .2em;
    }
  }

  .product-thumb:hover & {
    opacity: 1;
  }
}

.touchevents {
  .countdown {
    opacity: 1;
  }
}

.module-countdown {
  > .module-body {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: var(--gap, 4px);

    .module-text {
      display: flex;
      flex-grow: 1;
      flex-basis: clamp(var(--text-min-width, 100px), calc(var(--text-max-width, 50%) - var(--gap, 0px) / 2), 100%);
      min-width: 0;
      > div{
        flex-grow: 1;
      }
      &::before{
        line-height: 1;
        //align-self: flex-start;
      }
    }

    .countdown {
      flex-basis: min(calc(100% - var(--text-max-width, 50%) - var(--gap, 0px) / 2), 100%);
      flex-grow: 1;
      > div{
        padding: .5em;
        min-width: 2.5em;
      }
    }
  }
}

/******************************
 Table
*******************************/

table {
  border-collapse: inherit;

  thead {
    td {
      background: none;
    }
  }

  td {
    padding: 10px 15px;

    &.td-model {
      white-space: nowrap;
    }

    &.td-name {
      white-space: normal;
    }
    &.td-qty{
      .input-group{
        margin: 0 auto;
      }
    }
  }
}

.table-bordered > tbody > tr > td {
  border-top-width: 0;
  border-right-width: 0;
}

.table-responsive {
  margin-bottom: 0;
  border-width: 0;

  &.table-scroll::before {
    content: "";
    display: block;
    padding: 2px 6px;
    text-align: center;
    left: 0;
    position: sticky;

    html[dir="rtl"] & {
      right: 0;
    }
  }
}


.table-responsive {
  border: 0;

  > table {
    border: none !important;
  }
}

.table, table, .table-responsive > .table {
  margin-bottom: 0;
  border-width: 0;
}

.table-hover > tbody > tr:hover {
  background-color: transparent;
}

.table-striped > tbody > tr:nth-of-type(odd) {
  background-color: transparent;
}

html[dir='rtl'] {
  .table {
    max-width: initial;
  }
}

//Border Color NOT BORDERED

.table > tbody > tr > td,
.table > tbody > tr > th,
.table > tfoot > tr > td,
.table > tfoot > tr > th,
.table > thead > tr > td,
.table > thead > tr > th {
  padding: 8px 14px;
  border-top-color: transparent;
  vertical-align: middle;
}

//Border Color BORDERED

.table-bordered > tbody > tr > td,
.table-bordered > tbody > tr > th,
.table-bordered > tfoot > tr > td,
.table-bordered > tfoot > tr > th,
.table-bordered > thead > tr > td,
.table-bordered > thead > tr > th {
  border-color: transparent;
}

.table-bordered {
  border-color: transparent;
}

.table {
  border-color: transparent;

  > tbody + tbody {
    border-top: 0;
  }
}

table, .table-bordered {
  td:first-child, th:first-child {
    border-left-width: 0;
  }

  td:last-child, th:last-child {
    border-right-width: 0;
  }

  thead > tr > td {
    border-bottom-width: 1px;
    border-right-width: 0;
  }

  tbody tr:last-child {
    td, th {
      border-bottom-width: 0;
    }
  }

  tfoot > tr > td {
    border-bottom-width: 0;
    border-right-width: 0;
  }
}

table > thead > tr > td {
  border-bottom-width: 1px;
}

table > thead:first-of-type > tr > td {
  border-top-width: 0 !important;
}

.table > tbody:first-child > tr:first-of-type > td {
  border-top-width: 0 !important;
}

@media screen and (max-width: 767px) {
  .table-responsive > .table > tbody > tr > td,
  .table-responsive > .table > tbody > tr > th,
  .table-responsive > .table > tfoot > tr > td,
  .table-responsive > .table > tfoot > tr > th,
  .table-responsive > .table > thead > tr > td,
  .table-responsive > .table > thead > tr > th {
    //white-space: normal;
  }
}


.product-compare {
  #content {
    tbody td {
      text-align: center;
    }
  }
}

.bootstrap-datetimepicker-widget td,
.bootstrap-datetimepicker-widget th {
  border-width: 0;
}

/******************************
 Tooltips
*******************************/

.tooltip {
  z-index: 1399;
  pointer-events: none;
  opacity: 1;
  background-color: transparent !important;

  &.in {
    opacity: 1;
    pointer-events: none;
  }
}

.tooltip-inner {
  color: white;
  background-color: var(--background-color, #333);
  border-radius: 5px;
  padding: .2em .6em;
}

.tooltip.top .tooltip-arrow {
  border-top-color: var(--background-color, #333);
}

.tooltip.right .tooltip-arrow {
  border-right-color: var(--background-color, #333);
}

.tooltip.bottom .tooltip-arrow {
  border-bottom-color: var(--background-color, #333);
}

.tooltip.left .tooltip-arrow {
  border-left-color: var(--background-color, #333);
}

//Animate
.tooltip.fade {
  > * {
    opacity: 0;
    visibility: hidden;
    transition: all 0.15s ease-in-out;
  }

  &.in > * {
    opacity: 1;
    visibility: visible;
    transform: translate3d(0, 0, 0);
  }
}

//Top
.tooltip {
  &.top > * {
    transform: translate3d(0, 7px, 0);
  }

  &.right > * {
    transform: translate3d(-7px, 0, 0);
  }

  &.bottom > * {
    transform: translate3d(0, -7px, 0);
  }

  &.left > * {
    transform: translate3d(7px, 0, 0);
  }
}

/******************************
 Alerts
*******************************/

.alert {
  margin-bottom: 0;
  border: 0;
  padding: 10px 25px 10px 15px;
  border-radius: 0;
  position: relative;

  a {
    color: inherit;
  }

  i {
    margin: 5px;
  }

  .close {
    position: absolute;
    top: 50%;
    transform: translateY(-55%);
    right: 10px;
    opacity: 1;
  }
}

/******************************
 Tags
*******************************/

.tags {
  display: flex;
  align-items: center;
  flex-wrap: wrap;

  a {
    font-size: 90%;
    text-decoration: none;
    font-weight: normal;
    transition: all 0.075s ease-out;
  }
}

/******************************
 Responsive Video
*******************************/

.video-responsive {
  break-inside: avoid;
  width: 100%;
  position: relative;

  &:only-child {
    margin-bottom: 0;
  }
}

.video-frame {
  position: relative;
  padding-bottom: 56.25%;
  width: 100%;

  > iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

iframe {
  max-width: 100%;
  border: none;
}

/******************************
 Facebook
*******************************/

.fb-page {
  overflow-x: auto;
  max-width: 100%;
  display: block !important;
}

hr {
  margin-top: 10px;
  margin-bottom: 10px;
  position: relative;
  overflow: visible;

  &::before {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    line-height: 0;
    width: 1em;
    height: 1em;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

/******************************
 Drop Cap
*******************************/

.drop-cap {
  position: relative;
  display: block;
  float: left;
  line-height: 1;
}

.amp {
  display: inline-block;
  position: relative;
  line-height: 1;
}

/******************************
 Blockquote
*******************************/

blockquote {
  border: none;
  padding: 0;
  margin: 0;
  font-size: inherit;

  &::before {
    float: left;
    line-height: 1;
    font-size: 2.5em;
    font-style: normal;
  }

  &::after {
    content: "";
    display: table;
    width: 100%;
    clear: both;
  }
}

/******************************
 No transitions
*******************************/

.no-transitions * {
  transition: all 0s !important;
}

/******************************
 Share Reactions
*******************************/

.sharethis-inline-reaction-buttons {
  display: inline-block !important;

  .st-btn {
    font-size: 18px !important;
    padding: 0 7px !important;
    width: auto !important;

    &.st-selected {
      transform: scale(1) !important;
    }

    > svg {
      width: 30px !important;
      height: 30px !important;
    }
  }
}

/******************************
Link Resets
*******************************/


.cart-heading,
.info-block,
.nav-tabs li a,
.btn,
.name a,
.catalog-title,
.subitem a,
.panel-title a,
.refine-item a,
.compare-btn,
.search-result a,
.j-menu > li > a,
.dropdown-toggle > a,
.menu-item > a,
.account-list a,
.tags a,
.module-blog_categories a,
.module-blog_comments a,
.blog-feed,
.mobile-custom-menu,
.slider-static-text,
.x {
  text-decoration: none;

  &:hover {
    text-decoration: none;

    .no-touchevents & {
      text-decoration: none;
    }
  }
}


// background lazyload
.lazyload-bg:not(.lazyload),
.lazyload-bg.lazyloaded {
  background-image: var(--lazy-background-image);
}

.atss[class*='addthis']{
  z-index: 1020;
}
