@import 'variables.scss';

/******************************
 Login Page
*******************************/

.login-box {
  display: flex;
  flex-wrap: wrap;
  > div {
    flex-grow: 1;
    flex-basis: 300px;
    &:first-child{
      p:last-of-type{
        margin-bottom: 20px;
      }
    }
  }
  .login-box{
    .form-group{
      flex-wrap: wrap;
    }
  }
  &::before,
  &::after {
    display: none;
  }
  .well {
    height: 100%;
    border: none;
    border-radius: 0;
    padding: 0;
    margin: 0;
    box-shadow: none;
    background: transparent;
    display: flex;
    flex-direction: column;
    > .btn {
      margin-top: auto;
      align-self: flex-start;
    }
    p:first-of-type {
      display: none;
    }
    .buttons {
      margin-top: auto;
    }
  }
}

.fa.fa-cloud-download::before {
  content: "\eb4e";
  font-family: 'icomoon';
}

#account-login .well{
  //margin:0; // why? interferes with account page login container styles margin
}


a.forgot-password {
  display: block;
  margin: 10px 0;
  max-width: max-content;
}

.register-page{
  > div {
    width: 100%;
  }
  > p{
    margin-bottom: 20px;
  }
  .fieldset + fieldset{
    margin-top: 30px;
  }
}

/******************************
 My Account
*******************************/

.route-account-account,
.route-affiliate-account {
  #content {
    display: flex;
    flex-direction: column;
    > div {
      margin-bottom: 32px;
    }
  }
}

.route-affiliate-login {
  #content {
    > p + p {
      margin-bottom: 24px;
    }
  }
}

[class*="route-account-"]{
  #content > p:only-of-type{
    margin-bottom: 24px;
  }
}

.account-list {
  display:flex;
  flex-wrap: wrap;
  > li {
    width: 100%;
    margin-bottom: 5px;
    display: flex;
    > a {
      display: inline-flex;
      align-items: center;
      line-height: 1.3;
      transition: all 100ms ease-out;
      &::before {
        margin: 0 5px 5px 0;
        transition: inherit;
        line-height: 1;
        html[dir="rtl"] &{
          margin: 0 0 5px 5px;
        }
      }
      &:hover{
        transform: scale(var(--scale-hover));
      }
      &:active{
        transform: scale(var(--scale-active, var(--scale-hover)));
      }
    }
  }
}

.route-account-return-add{
  fieldset{
    margin-top: 30px;
  }
}

.route-account-order-info{
  table{
    margin-bottom: 20px;
  }
  h2{
    margin-top: 20px;
  }
}

.route-account-return-info{
  h1 + table,
  .table-responsive{
    margin-bottom: 20px;
  }
}

#account, #address, #account-address form > div:not(.buttons){
  display:flex;
  flex-direction:column;
}

