@import 'variables.scss';

/******************************
 Buttons
*******************************/

a, button {
  touch-action: manipulation;
}

a:focus, a:hover {
  text-decoration: none;
  outline: none;
}

button {
  border: none;
  outline: none;
  margin: 0;
  padding: 0;
}

button,
.btn,
.btn-default {
  &:focus,
  &.active:focus,
  &:active:focus {
    outline: unset;
  }
}

.btn{
  outline-color: var(--background-color, initial);
  &:focus,
  &.focus,
  &.active:focus,
  &:active:focus,
  &:focus-within,
  &:hover{
    color: var(--focus-color);
    outline-color: var(--background-color, initial);
  }
}

button,
.btn,
.btn-default {
  &.active,
  &:active {
    box-shadow: none;
  }
}

.links-menu a[href='javascript:;']{
  pointer-events: none;
}

.btn.disabled,
.btn[disabled]{
  pointer-events: none;
  position: relative;
  //opacity: 1;

  &::before,
  > span {
    opacity: .2;
  }
  &::after{
    position: absolute; // loading spinner
  }
}

.button-group-page{
  .btn.disabled,
  .btn[disabled] {
    &::before,
    > span {
      opacity: 0;
    }
  }
}

.btn.btn-full {
  width: 100%;
  width: -moz-available;
  width: -webkit-fill-available;
  width: stretch;
}

.btn {
  border: none;
  border-radius: 0;
  padding: 0.4em;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  position: relative;
  line-height: 1;
  font-size: initial;
  min-width: 0;
  transition: all 0.1s ease-out;
  .fa{
    aspect-ratio: 1;
    display: inline-flex;
    align-items: center;
    justify-content: center;
  }

  &::before,
  &::after {
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    min-width: 1em;
    min-height: 1em;
    padding: 0;
    line-height: 1;
  }

  > span:first-of-type {
    white-space: nowrap;
  }

  > .links-text:empty {
    display: none;
  }
  .count-badge {
    margin: 0;
  }

  &.block-expand{
    &::after{
      position: relative;
    }
  }
}

.fa-upload {
  margin-inline-end: 5px;
}

:focus {
  outline: none;
}

/******************************
 Page Buttons
*******************************/

.buttons {
  transition: all 0.1s ease-out;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  background: none;
  gap: var(--page-buttons-gap, 10px);

  .no-flexbox-gap & {
    margin-inline-start: calc(0px - var(--page-buttons-gap, 10px) / 2);
    margin-inline-end: calc(0px - var(--page-buttons-gap, 10px) / 2);
    margin-bottom: calc(0px - var(--page-buttons-gap, 10px) / 2);

    > div {
      padding: calc(var(--page-buttons-gap, 10px) / 2);
    }
  }

  label{
    margin-bottom: 0;
  }

  > div {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    min-width: min-content;
    width: 100%;
    flex-basis: calc((var(--page-buttons-width, 420px) - 100%) * 999);

    > .btn {
      min-width: min-content;
      width: 100%;
    }
  }

  .pull-left {
    align-items: flex-end;
  }

  .pull-right {
    &:only-child {
      flex-grow: 1;

      .btn {
        width: auto;
      }
    }

    a:not(.btn) {
      margin-inline-start: 5px;

      html[dir="rtl"] & {
        margin-inline-end: 5px;
      }
    }

    input {
      margin: 0;
    }

    .btn::before {
      order: 10;
    }
  }

  &::before,
  &::after {
    display: none;
  }
}

@media only screen and (min-width: 470px) {
  .buttons.dual-agree {
    .pull-right {
      flex-grow: 1;
      justify-content: flex-end;

      .btn {
        width: max-content;
      }
    }
  }
}

/******************************
 Button scale on hover
*******************************/

.btn{
  &:hover{
    transform: scale(var(--scale-hover));
  }
  &:active{
    transform: scale(var(--scale-active, var(--scale-hover)));
    transition: all 50ms ease;
  }
}

/******************************
 Button ripple
*******************************/
.ripple {
  --ripple-background: white;
  --ripple-opacity: 0.3;
  --ripple-duration: 600ms;
  --ripple-easing: linear;
}

.btn.ripple {
  overflow: hidden;

  small {
    position: absolute;
    display: block;
    background: var(--ripple-background);
    border-radius: 50%;
    pointer-events: none;
    overflow: hidden;
    z-index: 0;
    will-change: transform, opacity;

    //  position and size
    top: calc(var(--y) * 1px);
    left: calc(var(--x) * 1px);
    width: calc(var(--d) * 1px);
    height: calc(var(--d) * 1px);

    //  animated properties
    opacity: calc(var(--o, 1) * var(--ripple-opacity));
    transition: calc(var(--t, 0) * var(--ripple-duration)) var(--ripple-easing);
    transform: translate(-50%, -50%) scale(var(--s, 1));
    transform-origin: center;

    &::before,
    &::after {
      display: none;
    }
  }
}
