@import '../variables.scss';

.module-catalog{
  .flex-grid,
  .ipr-grid:not(.swiper-wrapper){
    display: flex;
    flex-wrap: wrap;
  }
  &::after{
    content: "";
    position: absolute;
    top: 0; right: 0; bottom: 0; left: 0;
    inset: 0;
    pointer-events: none;
  }
  > div{
    position: relative;
    z-index: 1;
  }
}

.module-catalog,
.module-catalog_blocks{
  .masonry-grid{
    position: relative;
    --masonry-cols-width: 200px;
    --masonry-cols-gap: 20px;
  }

  .item-all-link{
    cursor: pointer !important;
    pointer-events: auto !important;
    margin-inline-start: auto;
    align-self: center;
    display: inline-flex;
    align-items: center;
    justify-content: flex-end !important;
    text-decoration: none;
    color: unset;
    padding-inline: 0 6px !important;
    &:active::after{
      transform: scale(.93);
    }
    &:hover{
      color: unset;
      text-decoration: none;
    }
  }
  h3.block-title .item-all-link {
    position: absolute;
    inset-inline-end: 0;
  }
}

.catalog-title,
.subitems{
  &:empty{
    display: none;
  }
}

.catalog-blocks{
  > div{
    counter-reset: top-counters;
  }
}

.catalog-title{
  display: flex;
  align-items: flex-end;
  z-index: 2;
}

.catalog-image{
  width: 100%;
  text-decoration: none;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
  flex-basis: clamp(var(--image-min-width, 50px), calc(var(--image-max-width, 50%) - var(--catalog-gap, 0px) / 2), 100%);
  img {
    width: 100%;
    align-self: flex-start;
    object-fit: cover;
  }
  transition: all 60ms ease-out;

  &:hover{
    transform: scale(var(--scale-hover, 1))
  }
  &:active{
    transform: scale(var(--scale-active, 1));
    transition: all 30ms ease;
  }
  &:empty{
    display: none !important;
  }
  &::before{
    content: "";
    line-height: 1;
    position: absolute;
    z-index: 2;
    transition: all 100ms ease-out;
    max-width: 100%;
    max-height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    --icon-width: auto;
    width: var(--icon-width);
    height: var(--icon-height, var(--icon-width));
  }
  &::after{
    content: "";
    position: absolute;
    top: 0; right: 0; bottom: 0; left: 0;
    inset: 0;
    z-index: 1;
    transition: all 100ms ease-out;
  }
}

.item-content{
  transition: all 0.1s ease-out;
  display:flex;
  flex-direction:column;
  height: 100%;
  position: relative;

  &::before{
    content:"";
    position: absolute;
    inset: 0;
    pointer-events: none;
    border-radius: inherit;
  }
}

.item-assets {
  display: flex;
  flex-wrap: wrap;
  height: auto;
  align-content: flex-start;
  position: relative;
  --image-max-width: 40%;
  gap: var(--catalog-gap, 0px);
  max-height: none;
}

.subitems{
  flex-basis: min(calc(100% - var(--image-max-width, 50%) - var(--catalog-gap, 0px) / 2), 100%);
  flex-grow: 1;
  order:1;
  display:flex;
  flex-direction:column;
  position: relative;
  flex-wrap: nowrap;
  min-width: 60px;
  //gap: var(--catalog-link-gap, 0);
  --overflow-x: initial;
  --overflow-y: initial;
  max-height: var(--max-height, 100%);
  overflow-x: var(--overflow-x);
  overflow-y: var(--overflow-y);
  counter-reset: top-counters;
  &:empty{
    display: none;
  }
  .view-more a{
    display: flex;
    align-items: center;
    font-size: 95%
  }
}

.subitem {
  line-height: 1.2;
  display: flex;
  align-items: center;
  max-width: 100%;
  padding: calc(var(--catalog-link-gap, 0px) / 2) 0;
  counter-increment: top-counters;

  a{
    width: max-content;
    transition: all 0.075s ease-out;
    s {
      display: inline-block;
      padding: 3px 4px;
      margin-inline: 4px;
    }
  }
  &::before {
    float: left;
  }
  &:not(.view-more)::after{
    order: -1;
    display: none;
    align-items: center;
    justify-content: center;
    margin-inline-end: 4px;
    min-width: 1em;
    content: counter(top-counters);
  }
}

.no-flexbox-gap{
  .item-assets{
    margin: calc(0px - var(--catalog-gap, 0px) / 2);
  }
  .catalog-image,
  .subitems{
    margin: calc(var(--catalog-gap, 0px) / 2);
  }
  .subitem + .subitem{
    padding-top: var(--catalog-link-gap, 0);
    padding-inline-start: var(--catalog-link-gap, 0);
  }
}
html[dir="rtl"].no-flexbox-gap{
  .no-flexbox-gap{
    .subitem + .subitem{
      padding-top: var(--catalog-link-gap, 0);
      padding-inline-start: 0;
      padding-inline-end: var(--catalog-link-gap, 0);
    }
  }
}
