@import '../variables.scss';

/******************************
 Full Search
*******************************/

.header-search {
  height: 100%;

  input {
    //width: -moz-available;
    //width: -webkit-fill-available;
    //width: stretch; // why?
    width: 100%;
    height: 100%;
    border-radius: 0;
    border: 0;
    box-shadow: none;
    background: white !important;
    padding: 0 12px;

    &:focus {
      box-shadow: none;
      outline: none;
    }
  }

  > span {
    flex-grow: 1;
    width: 100%;
    height: 100%;
    position: static !important;

    .tt-hint {
      display: none;
    }
  }

  > button {
    &::before {
      content: "\f002";
      font-family: $icons;
    }
  }

  > * {
    order: 5;
  }
}

.full-search #search {
  max-height: 100%;

  > .j-dropdown {
    display: block;
  }
}

.mobile-header.mobile-1 {
  #search {
    &:not(.open) {
      .j-dropdown {
        pointer-events: none;
      }
    }

    &.animating::before {
      content: '';
      width: 100%;
      height: 100vh;
      position: fixed;
      left: 0;
      z-index: -1;
      --mobile-1-height: inherit;
      --mobile-top-bar: inherit;
      top: calc(var(--mobile-1-height, 0px) + var(--top-offset, 0px));
    }
  }
}

.mobile-sticky {
  .mobile-header.mobile-1 {
    #search.animating::before {
      top: calc(var(--mobile-1-height, 0px) - var(--mobile-top-bar, 35px) + var(--top-offset, 0px));
    }
  }
}

/******************************
 Mini Search
*******************************/

.mini-search {
  height: 100%;

  .search-trigger {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    transition: all 0.075s ease-out;
    background-color: transparent;
    --width: auto;
    width: var(--width);
  }

  #search {
    align-items: center;
    justify-content: center;
    height: 100%;

    > .dropdown-menu {
      left: auto;
      right: 0;
      //min-width: initial; //why? interferes with compact dropdown
      width: -moz-available;
      width: -webkit-fill-available;
      width: stretch;

      &::before {
        left: 100%;
        transform: translate3d(calc(-100% - 5px + var(--triangle-left-offset, 0px)), 0, 0);
      }
    }
  }

  .menu-icon {
    display: flex;
    flex-direction: column;
    line-height: 1;

    &::after {
      font-size: 11px;
      transform: translate3d(var(--offset-x, 0), var(--offset-y, 0), 0);
    }
  }

  .header-search {
    display: flex;
    max-width: 100%;

    input {
      min-width: 150px;
    }
  }

  .search-button {
    min-width: 40px;
    background-color: inherit;
  }

  .menu-icon {
    transition: all 100ms ease-out;
  }

  &:hover {
    .menu-icon {
      transform: scale(var(--scale-hover, 1));
    }
  }

  &:active {
    .menu-icon {
      transform: scale(var(--scale-active, 1));
    }
  }

  .search-categories-button {
    //font-size: 13px; why?
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.mobile-header-active {
  .mobile-2, .mobile-3 {
    .search-trigger {
      display: none;
    }
  }

  .mini-search #search > .dropdown-menu {
    max-width: 100%;
    left: 0;

    &::before {
      --element-offset: inherit;
      --element-width: inherit;
      left: calc(var(--element-offset, 0px) + var(--element-width, 0px) / 2);
      transform: translate3d(calc(-100% - 2px + var(--triangle-left-offset, 0px)), 0, 0);
    }
  }
}

.mobile-search-wrapper.full-search {
  .header-search input {
    width: 100%;
  }
}

.search-page-open {
  .header-compact .top-bar {
    z-index: 1;
  }
  .tt-menu{
    //padding-inline: var(--search-container-padding, 0px);
  }
}

.search-page.desktop-header-active .mini-search > #search > .j-dropdown::before {
  display: none !important;
}


/******************************
 Full Search
*******************************/

.full-search {
  #search {
    height: 40px;

    > .dropdown-menu {
      z-index: 1;
      position: relative;
      visibility: visible;
      opacity: 1;
      transform: translate3d(0, 0, 0) scale(1);
      padding: 0;
      margin: 0;
      min-width: initial;
      width: 100%;
      height: 100%;
      top: auto;

      &::before {
        display: none !important;
      }
    }
  }

  .header-search {
    display: flex;
    align-items: center;

    > span {
      flex-grow: 1;
    }
  }
}

/******************************
 Search Button
*******************************/

.search-button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  min-width: 40px;
  height: 100%;
  border-radius: 0;
  font-size: 20px;
  border-top-right-radius: inherit;
  border-bottom-right-radius: inherit;
}

/******************************
 Categories Dropdown
*******************************/

.search-categories-button {
  padding: 0 10px;
  position: relative;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: var(--max-width, 100%);
  border-top-left-radius: inherit;
  border-bottom-left-radius: inherit;
  display: flex;
  align-items: center;
  cursor: default;
  height: 100%;

  a {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    color: inherit !important;
  }

  &::after {
    position: relative;
    margin-inline-start: 7px;
    content: "";
    font-size: 11px;
    font-family: $icons;
  }
}

.search-categories {
  height: 100%;
  border-top-left-radius: inherit;
  border-bottom-left-radius: inherit;
  display: flex;
  align-items: center;
  transition: all 0.075s ease-out;

  .j-dropdown {
    div {
      overflow: hidden;
    }

    ul {
      max-height: 350px;
      width: 200px;
      overflow-y: auto;
      overflow-x: hidden;
      border-radius: inherit;
      background: white;
      -webkit-overflow-scrolling: touch;

      > li {
        font-size: 13px;
        width: 100%;
        white-space: normal;
        overflow: hidden;
        text-overflow: ellipsis;
        cursor: pointer;
        position: relative;

        a {
          cursor: pointer;
          padding-inline-start: 15px;
        }

        &.category-level-1 a::before {
          display: none;
        }

        &.category-level-4 a {
          padding-inline-start: 35px;
        }

        &.category-level-3 a {
          padding-inline-start: 25px;
        }

        &.category-level-2 a {
          padding-inline-start: 15px;
        }
      }
    }
  }
}

/******************************
 AutoSuggest
*******************************/

.search-result {
  &:hover {
    z-index: 1;
  }
}

.search-result.no-results > a {
  padding: 16px;
}

.search-results-empty {
  padding: 10px 15px;
}

.tt-menu {
  //display: block !important;
  width: 100%;
  //top: auto !important;
  .mobile-header & {
    overflow: hidden;
  }

  > div {
    max-height: 350px;
    overflow-y: auto;
    background-color: white;
    margin-inline-start: auto;
    display: grid;
    gap: var(--item-gap, 0);
    grid-template-columns: repeat(auto-fit, minmax(min(100%, var(--item-width, 100%)), 1fr));

    > div > a {
      transition: all 0.075s ease-out;
    }
  }
}

.tt-menu:not(.tt-empty) {
  &::before {
    content: "";
    width: 0;
    height: 0;
    --triangle-size: 10px;
    border: var(--triangle-size, 10px) solid transparent;
    border-bottom-color: white;
    pointer-events: none;
    margin: calc(-10px + var(--triangle-top-distance, 0px)) calc(var(--triangle-size, 0px) / 2) 0 calc(var(--triangle-size, 0px) / 2);
    display: block;
  }
}

.search-result {
  position: relative;

  > a {
    padding: 8px;
    display: flex;
    border-width: 1px 0 0 0;
    border-style: none;
    font-size: 14px;
    height: 100%;

    img {
      object-fit: contain;
    }

    > span {
      flex: 1;
      padding: 0 5px 5px 10px;
      display: flex;
      flex-direction: column;
    }
  }

  &:not(.view-more) {
    > a {
      &::after {
        content: "";
        position: absolute;
        right: 24px;
        top: 50%;
        transform: translate3d(0, -50%, 0);
      }
    }
  }

  &:first-child {
    > a {
      border-top-width: 0;
    }
  }

  .button-group {
    align-self: center;
    margin-inline-start: auto;
    //display: flex;
    display: none;
  }

  .product-name {
    line-height: 1.2;
    margin-bottom: 4px;

    + span {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      gap: 5px;
      line-height: 1;
    }
  }

  .price-new {
    position: relative;
    order: -1;
  }

  .price-old {
    font-size: 90%;
    border-radius: 3px;
  }

  &.view-more {
    grid-column: 1 / -1;

    a {
      padding: 8px 15px;
      align-items: center;

      &::after {
        margin-inline-start: 5px;
      }
    }
  }
}

.mobile-header-active {
  .mobile-2 {
    .mobile-search-group {
      padding: 0;

      .mobile-search-wrapper {
        padding: var(--mobile-search-padding, 5px);
        padding-top: var(--mobile-search-padding-top, var(--mobile-search-padding, 5px));
        padding-bottom: var(--mobile-search-padding-bottom, var(--mobile-search-padding, 5px));
      }
    }

    #search > .dropdown-menu {
      padding: 0;
    }
  }
}

