@import 'variables.scss';

/******************************
 Quick Checkout
*******************************/

.quick-checkout-wrapper {
  > div {
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;

    > div {
      flex-grow: 1;
      max-width: 100%;
    }

    .right {
      display: flex;
      flex-direction: column;

      .checkout-section {
        &:last-child {
          order: 1000;
          margin-bottom: 0;
        }
      }
    }
  }

  .section-body {
    display: flex;
    flex-direction: column;

    .form-group {
      order: 100;
    }

    .radio, .checkbox {
      align-items: initial;
    }
  }

  .payment-address,
  .shipping-address {
    .section-body {
      > div:not(.checkbox) {
        display: flex;
        flex-direction: column;
      }
    }
  }

  .well {
    margin-bottom: 0;
    box-shadow: none;
    border: none;
    padding: 0;
    background: transparent;
  }

  .checkout-section {
    .login-options {
      margin-bottom: 0;
      order: -1;

      .radio {
        width: auto;
        margin-inline-end: 10px;

        html[dir="rtl"] & {
          margin-inline-end: 0;
        }
      }
    }

    .login-form {
      margin-top: 10px;

      > div:first-of-type {
        margin-top: 10px;
      }

      input[type="password"] + div {
        flex-basis: 100%;
        margin-top: 8px;
      }
    }

    .radio, .checkbox {
      label {
        input[type='radio'],
        input[type='checkbox'] {
          margin-inline-end: 5px;

          html[dir="rtl"] & {
            margin-inline-start: 5px;
          }
        }
      }
    }
  }

  .shipping-payment {
    display: flex;
    flex-wrap: wrap;
    gap: var(--section-shipping-gap, 0px);

    > div {
      width: 100%;
      flex-basis: calc((var(--section-shipping-stack, 700px) - 100%) * 9999);
      flex-grow: 1;
    }

    .section-payment {
      flex-grow: 1;
    }

    .section-body {
      > div {
        display: flex;
        align-items: center;

        &::before {
          line-height: 1;
          min-width: 34px;
          font-size: 24px;
        }
      }

      .alert {
        display: block;

        &::before {
          display: none;
        }
      }
    }

    .shippings {
      p {
        margin-bottom: 0;
      }
    }
  }

  .section-cvr {
    .form-group .control-label {
      + div {
        justify-content: flex-end;
      }
    }
  }

  .cart-section {
    table {
      margin-bottom: 0 !important;
    }

    .table-responsive + table {
      border-top-width: 0 !important;
      margin-top: 20px;

      tfoot {
        td + td {
          width: 20%;
        }
      }
    }
    .input-group,
    .input-group-btn{
      --input-group-gap: 8px;
    }
  }

  .confirm-section {
    textarea {
      margin-bottom: 10px;
    }
  }

  .login-active .checkout-section:not(.section-login) {
    opacity: .4;
    pointer-events: none;
  }

  // Spinner
  .quick-checkout-spinner {
    display: flex;
    justify-content: center;
  }

  // Payment details
  .payment-pagseguro,
  .payment-skrill,
  .payment-cod,
  .payment-payza,
  .payment-paypal,
  .payment-pp_standard,
  .payment-pp_express,
  .payment-coinbase,
  .payment-stripe,
  .payment-instamojo,
  .payment-twocheckout {
    display: none !important;
  }

  #payment {
    > legend {
      display: none;
    }
  }

  .quick-checkout-payment {
    position: relative;

    .control-label[for="input-cc-save"] {
      width: auto;
      max-width: max-content;

      + div {
        min-width: initial;
        width: auto;
      }
    }
  }

  // Payment buttons
  .quick-checkout-payment .buttons,
  .payment-pp_braintree #submit-new {
    display: none !important;
  }

  .captcha {
    margin-top: 15px;
  }

  .captcha .form-group label {
    max-width: 100%;
  }

  .captcha .form-group img {
    margin-inline-start: 10px;
    margin-top: 1px;
  }
}


/******************************
 Default Checkout
*******************************/

.route-checkout-checkout {
  #collapse-payment-method,
  #collapse-shipping-method {
    .radio + p {
      margin-top: 10px;
    }
  }

  .table-responsive {
    table tr td {
      min-width: 90px;

      &.td-product {
        min-width: 150px;
      }
    }

    + h2 {
      margin-top: 24px;
    }
  }

  #payment {
    .col-sm-3 {
      width: auto;
      padding-inline-end: 0;
      float: left;
    }
  }
}

.phone.route-checkout-checkout {
  #payment {
    > div > div > div {
      max-width: 100%;
    }
  }
}

//Paypal Iframe
.table-responsive > iframe {
  margin: 0 !important;
  padding-top: 20px;
  width: 100%;
}

#checkout-checkout {
  .login-box {
    .radio {
      margin-bottom: 1.2rem;
    }
  }

  .register-page {
    display: flex;
    flex-wrap: wrap;

    margin-bottom: 20px;

    > div {
      padding: 10px;
      flex: 1 1 300px;
    }
  }
}

.quick-checkout-wrapper {
  .confirm-section {
    .checkbox {
      display: block !important;
      margin: 0;
    }

    label {
      display: inline-block;

      input {
        top: 2px;
        float: left;
      }
    }
  }
}

@media only screen and (max-width: 760px) {
  .confirm-section {
    .radio label, .checkbox label {
      display: block;
    }
  }
}

.popup-checkout-payment {
  .popup-container {
    width: 400px;
  }

  .popup-content {
    display: flex;
    flex-direction: column;
  }

  .buttons {
    margin-top: auto;
    display: block;
  }

  .popup-close {
    margin: 0;
  }
}

/******************************
 OC4
*******************************/

.oc4.route-checkout-checkout {

  .checkout-sections {
    display: flex;
    //align-items: flex-start;
    flex-wrap: wrap;
    max-width: 100%;
    gap: var(--checkout-sections-gap, 30px);

    &::before,
    &::after {
      display: none;
    }
  }

  .checkout-section {
    flex-grow: 1;
    min-width: min(100%, 300px);
    .buttons{
      width: 100%;
    }
  }

  .checkout-section-start{
    width:calc(var(--checkout-sections-split, 50%) - var(--checkout-sections-gap, 30px) / 2);
  }

  .checkout-section-end{
    align-self: flex-start;
    width:calc(100% - var(--checkout-sections-split, 50%) - var(--checkout-sections-gap, 30px) / 2);
    > div{
      + div{
        margin-top: var(--checkout-sections-gap, 24px);
      }
    }
    .input-group{
      select{
        flex-grow: 1;
      }
    }
  }

  #shipping-address{
    + div{
      margin-top: 24px;
    }
  }

  #shipping-existing{
    margin-top: 12px;
    select{
      width: 100%;
    }
  }

  .checkout-cart-item{
    display: flex;
    span{
      &:first-child{
        small{
          margin-inline-start: 2px;
        }
      }
      small{
        opacity: .8;
      }
      + span{
        margin-inline-start: 5px;
      }
    }
  }

  #checkout-payment{
    margin-top: var(--checkout-sections-gap, 30px);
  }
  #button-confirm{
    width: 100%;
  }
}
