@import '../variables.scss';

/******************************
 Header
*******************************/


html{
  --desktop-top-bar: 35px;
  --slider-full-height: 100vh;
}

.header-desktop-slim,
.header-desktop-compact{
  &.has-menu-bar{
    --desktop-main-menu-slider: var(--desktop-main-menu, 42px);
  }
}

.header-desktop-classic,
.header-desktop-mega {
  --desktop-main-menu-slider: var(--desktop-main-menu, 42px);
}

.desktop-header-active{
  --slide-auto-reduction: calc(var(--desktop-top-bar, 0px) + var(--desktop-header, 0px) + var(--desktop-main-menu-slider, 0px) + var(--header-notice-height, 0px));
  &.over-content{
  --slide-auto-reduction: var(--header-notice-height, 0px);
  }
}
.mobile-header-active{
  --slide-auto-reduction: calc(var(--mobile-bar, 0px) + var(--mobile-top-bar, 0px) + var(--mobile-search-bar, 0px) + var(--bottom-menu-height, 0px) + var(--header-notice-height, 0px));
  &.header-mobile-1{
    --slide-auto-reduction: calc(var(--mobile-bar, 0px) + var(--mobile-top-bar, 0px) + var(--bottom-menu-height, 0px) + var(--header-notice-height, 0px));
  }
}

.ios{
  --slider-full-height: 100svh;
}

header {
  z-index: 100;
  position: relative;
}

.header {
  height: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  margin: 0 auto;
  position: relative;
  transition: background-color 0.07s linear;
  //overflow-x: clip;

  .top-bar {
    z-index: 3;
    height: var(--desktop-top-bar, 35px);
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: static;

    > div {
      z-index: 1;
    }

    &::before {
      height: var(--desktop-top-bar, 35px);
    }
  }

  .top-menu {
    position: relative;
  }

  .mid-bar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    height: var(--desktop-header, 80px);
    z-index: 2;
  }

  > .desktop-main-menu-wrapper {
    z-index: 1;
  }
}

.desktop-main-menu-wrapper{
  display: flex;
  &.menu-bar {
    height: calc(var(--desktop-main-menu, 42px) + var(--bar-padding, 0px) * 2);
    .main-menu-1{
      z-index: 1;
    }
  }
  &::before{
    content: "";
    position: absolute;
    inset: 0;
    z-index: 0;
    border-radius: inherit;
    pointer-events: none;
  }
  &::after{
    z-index: -1;
  }
}


/******************************
 Logo
*******************************/
.desktop-logo-wrapper{
  border-start-start-radius: inherit;
  border-end-start-radius: inherit;
}
#logo {
  height: 100%;
  max-width: var(--logo-max-width, 100%);
  transition: max-width 150ms ease-out;
  border-start-start-radius: inherit;
  border-end-start-radius: inherit;
  a {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.075s ease-out;
    transform: none;
    border-start-start-radius: inherit;
    border-end-start-radius: inherit;
    > img {
      max-width: 100%;
      max-height: 100%;
      position: relative;
      align-self: center;
      transition: transform 150ms ease-out;
      width: auto !important;
      height: auto !important;
      --scale-hover: 1;
      &[src*="svg"]{
        width: 100% !important;
      }
    }
    &:hover {
      > img {
        transform: scale(var(--scale-hover, 1));
      }
    }

    &:active {
      > img {
        transition: transform 50ms;
        transform: scale(var(--scale-active, 1));
      }
    }
  }

  h1 {
    height: 100%;
    margin: 0;
    padding: 0;
  }
}

/******************************
 DESKTOP HEADER STYLES
*******************************/

.desktop-header-active {
  //Classic
  .header-classic {
    flex-direction: column;

    > div {
      width: 100%;
    }

    .mid-bar {
      justify-content: start;
      > div {
        height: 100%;
        order: 5;
        display: flex;
        align-items: center;
      }

      .top-menu {
        height: auto;

        > .j-menu {
          flex-wrap: nowrap;
        }
      }

      .secondary-menu {
        .top-menu {
          margin: 0;
        }
      }
    }

    #logo {
      width: 100%;

      a {
        width: 100%;
      }
    }

    .classic-cart-wrapper,
    .desktop-cart-wrapper {
      justify-content: flex-end;
    }

    .desktop-search-wrapper {
      min-width: 160px;
    }

    .header-cart-group {
      justify-content: flex-end;
    }

    .full-search #search {
      width: 100%;
      z-index: 2;
    }

    #cart {
      flex-grow: 1;
    }
  }

  .header-sm {
    .top-menu > ul {
      margin: 0;
      flex-wrap: nowrap;
    }
  }

  //Mega
  .header-mega {
    flex-direction: column;

    .menu-bar{
      .main-menu-1{
        flex-grow: 0;
      }
    }

    > div {
      width: 100%;
    }

    .mid-bar {
      > div {
        height: 100%;
        display: flex;
        align-items: center;
      }

      .desktop-cart-wrapper {
        height: auto;
      }

      .secondary-menu {
        flex-grow: 1;

        .top-menu {
          height: auto;
        }
      }
    }

    #logo {
      margin: 0 auto;
      width: 100%;

      a {
        width: 100%;
        height: 100%;
      }
    }

    .desktop-search-wrapper {
      flex-grow: 1;
      justify-content: flex-end;
      display: flex;
      height: 100%;

      > div {
        width: 100%;
      }
    }

    .desktop-logo-wrapper {
      margin-inline-end: auto;
      min-width: max-content;
    }

    .desktop-cart-wrapper {
      justify-content: flex-end;
      order: 5;

      #cart, .cart-heading {
        height: 100%;
      }
    }

    .desktop-main-menu-wrapper {
      display: flex;
      align-items: center;
      justify-content: space-between;

      #main-menu-2 {
        order: 4;
      }
    }

    #main-menu {
      order: -1;
    }

    .full-search {
      align-items: center;
    }
  }

  //Compact
  .header-compact {
    > div {
      width: 100%;
    }

    .mid-bar {
      .desktop-main-menu-wrapper {
        //position: static; why?

        &::after {
          display: none;
        }

        .main-menu {
          display: inline-flex;
        }
      }
    }

    .desktop-logo-wrapper {
      min-width: max-content;
      height: 100%;
      pointer-events: none;
      display: flex;
      #logo{
        min-width: 100%;
        a{
          pointer-events: auto;
        }
      }
    }

    .desktop-main-menu-wrapper {
      .main-menu {
        flex-wrap: nowrap;

        > .j-menu > .menu-item {
          > a {
            position: relative;
            z-index: 2;
          }

          > .dropdown-menu {
            z-index: 3;
          }
        }
      }
    }

    .header-cart-group {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      margin-inline-start: auto;
      height: 100%;
    }

    .top-menu-group {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      flex-wrap: wrap;
      padding: 0 10px;
    }
  }

  //Slim
  .header-slim {

    .header-slim-wrapper {
      display:flex;
      flex-direction:column;
    }

    .mid-bar + .desktop-main-menu-wrapper {
      min-height: var(--desktop-main-menu, 100%);
    }

    .header-cart-group {
      display: flex;
      align-items: center;
      justify-content: flex-end;
    }

    .full-search {
      display: flex;
      align-items: center;
      flex-grow: 1;
      height: 100%;

      #search {
        width: 100%;
      }
    }
  }
}

.header-lg .desktop-main-menu-wrapper.menu-bar {
  #main-menu + .desktop-cart-wrapper {
    margin-inline-start: auto;
  }
  padding-top: var(--bar-padding, 0);
  padding-bottom: var(--bar-padding, 0);
}

