/******************************
 Slim New
*******************************/
.desktop-header-active{
  .header-slim{
    .header-slim-new {
      //background: none !important;

      .header-slim-wrapper {
        display: grid;
        grid-template-columns: var(--desktop-logo-wrapper, 250px) 1fr;
        grid-template-rows: var(--desktop-top-bar, 35px) 1fr;
      }

      .desktop-logo-wrapper {
        grid-row: 1/3;
        z-index: 4;
      }

      .desktop-main-menu-wrapper {
        grid-column: 1/-1;
      }

      .top-bar {
        align-self: start;
        z-index: 3;
      }

      .mid-bar {
        align-self: start;
        min-height: 100%;
      }

      .desktop-logo-wrapper {
        #logo {
          display: flex;
          align-items: center;
          justify-content: center;
          position: relative;
          overflow: hidden;
          width: 100%;

          a {
            max-height: calc(var(--desktop-header, 80px) + var(--desktop-top-bar, 35px));
            transition: all 120ms linear;
            margin-top: 0;

            img {
              width: auto;
            }
          }
        }
      }

      &::before {
        content: "";
        width: 100vw;
        left: 50%;
        margin-inline-start: -50vw;
        height: 100%;
        position: absolute;
        z-index: 0;
      }

      .boxed-layout & {
        &::before {
          margin: 0;
          left: 0;
          width: 100%;
          border-top-left-radius: inherit;
          border-top-right-radius: inherit;
        }
      }
    }
  }
  &.header-sticky.sticky-default .header-slim-new {
    .desktop-logo-wrapper {
      #logo {
        a {
          max-height: var(--desktop-sticky, var(--desktop-header, 80px));
          margin-top: var(--desktop-top-bar, 35px);
          transition: all 120ms linear;
        }
      }
    }
  }
}


/******************************
 Boxed
*******************************/

.boxed-layout {
  .header .mid-bar {
    border-top-left-radius: inherit;
  }

  .desktop-logo-wrapper {
    border-top-left-radius: inherit;

    #logo {
      border-top-left-radius: inherit;
    }
  }

  .mobile-header {
    border-top-left-radius: inherit;
    border-top-right-radius: inherit;

    .mobile-top-bar {
      border-top-left-radius: inherit;
      border-top-right-radius: inherit;
    }
  }
}

/******************************
 Mobile Header Desktop
*******************************/

.mobile-header,
.open-close,
.mobile-bar-group {
  display: none;
}

.mobile-header-active {
  .mobile-header {
    display: block;
  }

  .header {
    display: none;
  }
}




