@import '../variables.scss';

.module-banners {
  .banners-grid:not(.auto-grid){
    display: flex;
    flex-wrap: wrap;
    height: 100%;
  }
  .auto-grid{
    --grid-width-default: 1fr;
    --grid-gap: 0;
  }
  *{
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
  }
  .module-item {
    display:flex;
    flex-direction:column;
    > a {
      display: flex;
      align-items: center;
      justify-content: center;
      position: relative;
      width: 100%;
      flex-grow: 1;
      transition: background-color 0.18s ease-out;
      -webkit-backface-visibility: hidden;
      backface-visibility: hidden;
      overflow: hidden;
      z-index: 2;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        transition: all 0.2s ease;
      }
      &::before {
        content: "";
        width: 100%;
        height: 100%;
        position: absolute;
        pointer-events: none;
        transition: all 0.15s ease-out;
        z-index: 2;
        border-radius: inherit;
      }
      &::after {
        content: "";
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        transition: all 0.2s ease-out;
        font-size: 35px;
        color: white;
        z-index: 4;
      }
    }
    transition: transform 90ms ease-out;
    &:hover img{
      transform: scale(var(--scale-hover, 1));
    }
    &:active{
      transition: transform 60ms linear;
      transform: scale(var(--scale-active, 1));
    }
  }
  .banner-caption{
    transition: all 0.1s ease-out, margin 0.15s ease-out;
  }
  .banner-caption-2{
    transition: all 0.2s ease-out;
  }
  .banner-text {
    position: absolute;
    left: 0;
    right: 0;
    color: white;
    line-height: 1.3;
    z-index: 3;
    span{
      position: relative;
      display: flex;
      align-items: center;
      justify-content: var(--align, center);
      transition: all 0.2s ease-out;
      s{
        max-width: 100%;
      }
      &::after{
        transition: all 100ms ease-out;
      }
    }
  }
  .banner-caption-3{
    span{
      display: block;
    }
  }
}

.module-banners_grid{
  .swiper-slide{
    &::before {
      content: "";
      float: left;
      pointer-events: none;
      padding-top: calc(var(--image-height, 0) / var(--image-width, 1) * 100%);
    }
    &::after{
      content: "";
      display: table;
      clear: both;
    }

    .slide-link {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      z-index: 2;
    }

    .slide-content{
      &::before{
        content: "";
        display: block;
        z-index: 1; //why 4?
      }
    }
  }
  .banners-grid.auto-grid{
    min-height: 100%;
    > .banners-wrapper{
      //height: auto; why?
    }
  }
}
