@import '../variables';

/******************************
 Journal Menu
*******************************/

.j-menu {
  all: unset;
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 100%; // for dropdowns offset
  flex-wrap: wrap;
  list-style: none;
  margin: 0;
  padding: 0;

  .mobile-header-active .main-menu &,
  .desktop-mobile-main-menu-wrapper & {
    flex-direction: column;
    align-items: flex-start;
    flex-wrap: nowrap;
    height: auto;

    .menu-item:not(.icons-menu-item) {
      width: 100%;
    }
  }

  > li {
    all: unset;
    margin: 0;
    border: 0;

    &.dropdown {
      position: relative;
    }

    &:not(:first-child) {
      border-width: 0 0 0 1px;
      border-style: none;
    }

    &:first-child {
      padding-inline-start: 0;
    }

    &:last-child {
      padding-inline-end: 0;
    }

    > a {
      width: 100%;
      height: 100%;
      max-height: 100%;
      padding: 5px;
      display: flex;
      align-items: center;
      min-width: 30px;
      cursor: pointer;
      transition: color 0.1s ease-out, background-color 0.07s ease-out, transform 80ms ease;
      position: relative;
      --scale-hover: auto;
      --menu-badge-y: -50%;

      &:hover {
        transform: scale(var(--scale-hover));
      }

      &:active {
        transform: scale(var(--scale-active, var(--scale-hover)));
        transition: transform 60ms;
      }

      .links-text {
        transition: inherit;
      }

      &::before {
        line-height: 1;
        font-size: 16px;
        min-width: 1em;
        min-height: 1em;
        color: inherit;
      }
    }

    &:first-child {
      > a {
        border-width: 0;
      }
    }

    &.icon-only {
      > a {
        min-width: 10px;
      }
    }
    > a:not([href]),
    > a[href='javascript:;']{
      cursor: default;
    }
  }

  .links-text {
    display: inline-block;
    color: inherit;

    s, em {
      font-style: initial;
      display: inline-block;
      margin: 0 .3em;
    }

    em {
      font-size: 1.1em;
      vertical-align: middle;
      margin: 0 .2em;
    }
  }

  > li:first-child {
    > a {
      .links-text em {
        margin-inline-start: 0;
      }
    }
  }
}

.ios {
  .j-menu > li > a {
    cursor: pointer !important;
  }
}

.menu-image {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: max-content;

  img {
    border-radius: inherit;
    max-width: min-content;
  }
}

/******************************
 Top Menu
*******************************/

.top-bar .wrapper {
  justify-content: space-between;
}

.top-menu {
  height: 100%;

  > ul {
    position: relative;

    > li {
      padding: 0 calc(var(--menu-item-gap, 0px) / 2);
      height: inherit;
      a {
        align-items: center;
      }
      > a {
        justify-content: center;
      }
    }
  }

  .dropdown {
    a .links-text {
      white-space: nowrap;
    }
    > .j-dropdown > .j-menu{
      overflow: hidden;
    }
  }
}


// Multilevel Indicator
.dropdown > a::after,
.language-currency .dropdown-toggle::after {
  content: "";
  border-width: 4px;
  border-style: solid;
  border-color: transparent;
  border-left-color: currentColor;
  border-right-color: transparent !important;
  border-top-color: transparent !important;
  border-bottom-color: transparent !important;
  margin-inline-start: 5px;
  margin-inline-end: 0;
  transform: rotate(90deg) scale(1) translate3d(25%, 0, 0);
}

.j-dropdown .dropdown > a::after {
  margin-inline-start: auto;
  transform: rotate(0deg) scale(1) translate3d(25%, 0, 0);
}


.dropdown {
  &:not(.mega-menu) {
    > .j-dropdown {
      width: min-content;
    }
  }

  > a {
    position: relative;

    .count-badge {
      margin: 0 5px;
    }
  }

  .j-menu {
    > li > a {
      &::before {
        overflow: hidden;
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }
  }
}

.desktop-main-menu-wrapper .main-menu .menu-item.dropdown .j-menu > li:first-child:not(.dropdown){
  border-start-end-radius: inherit;
  border-start-start-radius: inherit;
  > a{
    border-start-start-radius: inherit;
    border-start-end-radius: inherit;
  }
}
.desktop-main-menu-wrapper .main-menu .menu-item.dropdown .j-menu > li:last-child:not(.dropdown){
  border-end-end-radius: inherit;
  border-end-start-radius: inherit;
  > a{
    border-end-end-radius: inherit;
    border-end-start-radius: inherit;
  }
}
.desktop-main-menu-wrapper .main-menu .menu-item.dropdown .j-menu > li:first-child.dropdown{
  border-start-start-radius: inherit;
  > a{
    border-start-start-radius: inherit;
  }
}
.desktop-main-menu-wrapper .main-menu .menu-item.dropdown .j-menu > li:last-child.dropdown{
  border-end-start-radius: inherit;
  border-end-end-radius: 0;
  > a{
    border-end-start-radius: inherit;
    border-end-end-radius: 0;
  }
}

/******************************
 Dropdown
*******************************/

.desktop-mobile-main-menu-wrapper > .main-menu,
.mobile-main-menu-wrapper > .main-menu,
.site-wrapper .accordion-menu,
.site-wrapper .flyout-menu{
  .j-menu > li:not(:only-child) {
    padding: calc(var(--menu-item-gap, 0px) / 2) 0;

    &:not(:first-child) {
      border-width: 1px 0 0 0;
    }
    &:first-child {
      //padding-top: 0; why?
    }
    &:last-child {
      //padding-bottom: 0;
    }
  }
}

.dropdown-menu {
  font-size: inherit;
}

.j-dropdown,
.flyout-menu {
  .j-menu > li:not(:first-child) {
    border-width: 1px 0 0 0;
  }
}

.j-dropdown {
  margin: 0;
  padding: 0;
  box-shadow: none;
  border-width: 0;
  border-radius: 0;
  width: 100%;
  background-color: transparent;
  transform-origin: 50% 0;
  transform: translate3d(0, -10px, 0);
  opacity: 0;
  display: none;
  flex-direction: column;
  min-width: min-content;

  .j-dropdown {
    min-width: min-content;
  }

  --dropdown-from: -10px;

  .mobile-header-active #main-menu & {
    position: relative;
    left: 0;
    right: 0;
    transform: none;
    width: 100%;
  }

  .mobile-header-active & {
    .menu-item {
      width: 100%;
    }
  }

  .j-menu {
    padding: var(--dropdown-padding, 0);

    > li {
      --menu-item-gap: 0px;
      width: 100%;
      padding: calc(var(--menu-item-gap, 0px) / 2) 0;


      &:first-child {
        padding-top: 0 !important;
      }

      &:last-child {
        padding-bottom: 0 !important;
      }

      .mobile-header-active & {
        padding: calc(var(--menu-item-gap, 0px) / 2) 0;
      }

      //&:not(.flyout-menu-item){
      //  &:first-child {
      //    > .j-dropdown{
      //      top: calc(0px - (var(--dropdown-padding, 0px) / 2));
      //    }
      //  }
      //}
      .j-dropdown {
        top: 0;

        &::before {
          display: none !important;
        }
      }
    }
  }

  .open > & {
    display: flex;
  }

  .animating > & {
    transform: none;
    opacity: 1;
    transition: transform 0.1s ease-out, opacity 0.1s ease-out, visibility 0.1s ease-out;
  }
}

/******************************
 Dropdown Triangle
*******************************/

.dropdown {
  --triangle-size: 10px;
  --dropdown-triangle: calc(var(--triangle-size, 10px) + var(--triangle-top-distance, 0px));
}

.j-dropdown {
  &::before {
    content: "";
    --triangle-size: inherit;
    --dropdown-triangle: inherit;
    --triangle-left: var(--triangle-left-offset, 0px);
    --triangle-right: var(--triangle-right-offset, 0px);
    width: 0;
    height: 0;
    border: var(--triangle-size, 10px) solid transparent;
    border-bottom-color: white;
    pointer-events: none;
    margin: calc(-20px + var(--dropdown-triangle, 0px)) calc(var(--triangle-size, 0px) / 2 + var(--triangle-right, 0px)) 0 calc(var(--triangle-size, 0px) / 2 + var(--triangle-left, 0px));
    display: block;
  }
}

.flyout-menu-item {
  > .j-dropdown {
    > .mega-menu-content {
      > .grid-rows {
        border-radius: inherit;
        overflow: hidden;
      }
    }

    &::before {
      display: none !important;
    }
  }
}

.mega-menu {
  > .j-dropdown {
    &::before {
      opacity: 0;
    }
  }
}

.menu-bar{
  .main-menu{
    &::before,
    &::after {
      content: "";
      display: none;
      opacity: 1;
      position: absolute;
      pointer-events: none;
      height: calc(var(--desktop-main-menu, 40px) + var(--bar-padding, 0px) * 2);
      transition: opacity 200ms ease-out;
      --fader-color: hsla(var(--j-color-scheme-background-primary-h), var(--j-color-scheme-background-primary-s), calc(var(--j-color-scheme-background-primary-l) - 0%), calc(var(--j-color-scheme-background-primary-a) * 1));
      width: 80px;
      z-index: 2;
    }

    &::before {
      inset-inline-start: 0;
      background-image: linear-gradient(-90deg, rgba(255, 255, 255, 0) 0, var(--fader-color, white) 90%, var(--fader-color, white));
      border-start-start-radius: inherit;
      border-end-start-radius: inherit;
    }

    &::after {
      inset-inline-end: 0;
      background-image: linear-gradient(90deg, rgba(255, 255, 255, 0) 0, var(--fader-color, white) 90%, var(--fader-color, white));
      border-start-end-radius: inherit;
      border-end-end-radius: inherit;
    }

    &:not([style]),
    &[style*="--scroll-offset: 0px;"]{
      &::before{
        display: none;
      }
    }
    &.no-scroll-end{
      &::after{
        display: none;
      }
    }
    border-radius: inherit;
  }
  > div{
    border-radius: inherit;
  }
}

/******************************
 Menu Label
*******************************/

.main-menu-item {
  .dropdown > a {
    --dropdown-indicator: -8px;
  }
}

.menu-label {
  position: absolute;
  top: 50%;
  left: 100%;
  font-size: .75em;
  font-weight: normal;
  line-height: 1;
  padding: 2px 3px;
  --label-triangle-size: 5px;
  transform: translate3d(calc(-100% + var(--offset-x, 0px)), calc(-150% + var(--sticky-offset-y, var(--offset-y, 0px))), 0) scale(var(--label-scale, 1));
  opacity: var(--label-opacity, 1);
  margin: 0;
  //transition: background-color 100ms ease, opacity 100ms ease, color 100ms ease, transform 300ms cubic-bezier(0.175, 0.885, 0.320, 1.275) var(--label-delay, 0ms);
  //transition: background-color 100ms ease, opacity 100ms ease, color 100ms ease, left 0s;
  transition: all 0s;
  transform-origin: bottom;
  will-change: transform;

  &::before {
    content: "";
    --display: none;
    display: var(--display);
    color: var(--background-color);
    position: absolute;
    border: var(--label-triangle-size, 0px) solid transparent;
    top: 50%;
    left: 0;
    transform: translate3d(-100%, -50%, 0);
    transition: inherit;
    pointer-events: none;
  }

  &::after {
    content: "";
    --display: none;
    display: var(--display);
    color: var(--background-color);
    position: absolute;
    border: var(--label-triangle-size, 0px) solid transparent;
    border-top-color: currentColor;
    top: 100%;
    left: 50%;
    transition: inherit;
    transform: translate3d(-50%, 0, 0);
    pointer-events: none;
  }
}

.accordion-menu,
.flyout-menu,
.j-dropdown {
  .menu-label {
    position: static !important;
    transform: none !important;
    margin-inline-start: calc(var(--label-margin, 0px) + var(--label-triangle-size, 0px)) !important;

    &::before {
      display: var(--display);
      border-right-color: currentColor !important;
    }

    &::after {
      display: none;
    }
  }
}

html[dir="rtl"] {

  .menu-label {
    left: 0px;
    transform: translate3d(calc(0px - var(--offset-x, 0px)), calc(-150% + var(--sticky-offset-y, var(--offset-y, 0px))), 0) scale(var(--label-scale, 1));

    &::before {
      top: 50%;
      left: auto;
      right: 0;
      transform: translate3d(100%, -50%, 0);
    }
  }

  .accordion-menu,
  .flyout-menu,
  .j-dropdown {
    .menu-label {

      &::before {
        border-left-color: currentColor !important;
        border-right-color: transparent !important;
      }
    }
  }
}

// Labels smaller z-index
@for $i from 1 through 50 {
  .main-menu-item.has-label-#{$i} {
    z-index: calc(50 - #{$i});
  }
}

/******************************
 Flyout Menu
*******************************/
.flyout-menu {
  display: flex;
  flex-direction: column;
  position: relative;

  > ul > li {
    width: 100%;

    &:first-child {
      border-top-left-radius: inherit;
      border-top-right-radius: inherit;

      &.dropdown:hover {
        border-top-right-radius: 0;
      }

      > a {
        border-radius: inherit;
      }
    }

    &:last-child {
      border-bottom-left-radius: inherit;
      border-bottom-right-radius: inherit;

      &.dropdown:hover {
        border-bottom-right-radius: 0;
      }

      > a {
        border-radius: inherit;
      }
    }
  }

  .count-badge + .open-menu + .menu-label {
    margin-inline-start: 0;
  }

  .j-dropdown {
    top: 0;
    left: 100%;
    padding-top: 0;
    width: auto;
    transform: translate3d(0, 0, 0);
    transition: all 0s ease-out;
  }

  .mega-menu {
    .j-dropdown {
      --flyout-mega-menu: 800px;
      padding-inline-start: var(--flyout-mega-menu-inset, 0px) !important;
      padding-block-start: var(--flyout-mega-menu-inset, 0px);
      width: min(80vw - var(--flyout-mega-menu-inset, 0px), var(--flyout-mega-menu) - var(--flyout-mega-menu-inset, 0px));
      //height: 100%; //why?
    }
  }

  .dropdown {
    > a::after {
      margin-inline-start: auto;
      transform: rotate(0deg) scale(1) translate3d(0, 0, 0);
    }
  }
}

.global-wrapper{
  .flyout-menu > .j-menu > .flyout-menu-item.mega-menu > .j-dropdown > .mega-menu-content {
    max-height: calc(100vh - var(--element-top-offset, 0px));
    overflow-y: auto;
  }
  .flyout-menu .flyout-menu-item.multi-level .j-dropdown{
    padding-inline-start: var(--flyout-sub-level-offset, 0);
  }
  .main-menu-item{
    &.dropdown,
    &.multi-level{
      .j-dropdown .j-dropdown{
        padding-inline-start: var(--sub-level-offset, 0);
      }
    }
  }
}


.mega-menu > {
  .j-dropdown {
    min-width: auto;
  }
}

.mega-fullwidth:not(.menu-fullwidth) > .j-dropdown {
  max-width: 100%;
}

.flyout > .j-dropdown::before {
  z-index: 1;
}

/******************************
 Accordion Menu
*******************************/

.open-menu {
  display: none;
}

.accordion-menu {
  .open-menu {
    width: 20px;
    height: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    position: absolute;
    right: 8px;
    top: 50%;
    transform: translateY(-50%);
    transition: color 0.1s ease-out, background-color 0.1s ease-out;
    z-index: 2;

    &[aria-expanded="true"] em::before {
      content: "\f068";
    }
  }

  .j-menu > .menu-item {
    box-shadow: none !important;
    height: auto;
    padding: calc(var(--menu-item-gap, 0px) / 2) 0;

    > a {
      position: relative;

      &::after {
        display: none;
      }

      + div {
        border-width: 1px 0 0 0;
        margin-top: calc(var(--menu-item-gap) / 2);
      }
    }

    &.flyout > a {
      + .in {
        border-top-width: 0;
      }
    }
    &.is-divider{
      padding: 0;
      border-width: 0;
      border-bottom-width: 1px;
      border-style: none;
      margin: calc(var(--menu-item-gap, 0px) / 2) 0;
      > a{
        display: none;
      }
    }
  }

  .j-menu {
    height: auto;
    overflow: hidden;

    > li {
      width: 100%;

      > a {
        .links-text {
          white-space: normal;

          &:empty {
            width: 100%;
          }
        }

        &::before {
          display: flex;
          align-items: center;
          justify-content: center;
        }
      }

      + li {
        margin-inline-start: 0 !important;
        margin-inline-end: 0 !important;
      }
    }

    .j-menu {
      box-shadow: none;
    }
  }

  .menu-item.dropdown:not(.mega-menu):not(#search) .j-menu > li,
  .menu-item.dropdown:not(.mega-menu):not(#search) .j-menu > li > a {
    --acordion-menu-item-height: auto;
    min-height: var(--acordion-menu-item-height);
  }
}

/******************************
 Icon text decoration overrride
*******************************/

ul.j-menu > li.menu-item > a {
  text-decoration: none !important;
}
