@import '../variables.scss';

/******************************
 Cart
*******************************/
#cart {
  transition: all 100ms ease-out, transform 50ms ease;
  height: 100%;
  display: flex;
  align-items: center;
  > a {
    position: relative;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: transparent;
    transition: all 100ms ease-out;
    max-height: 100%;
    height: var(--size, 40px);
    border-radius: inherit;
    text-decoration: none !important;
    > span{
      color: inherit;
    }
    &:hover {
      color: inherit;
      transform: scale(var(--scale-hover, 1));
    }
    &:active{
      transform: scale(var(--scale-active, 1));
    }
    > em {
      font-family: inherit;
      display:flex;
      flex-direction:column;
      align-items: center;
      justify-content: center;
      width: var(--size, 40px);
      height: 100%;
      border-top-right-radius: inherit;
      border-bottom-right-radius: inherit;
      .cart-label{
        position: relative;
        font-size: 80%;
        padding:0 5px;
        transition: all 100ms ease-out;
      }
      &::before {
        transition: all 100ms ease-out;
        color: inherit;
      }
    }
    &::after {
      display: none !important;
    }
  }
}

.desktop-main-menu-wrapper .desktop-cart-wrapper{
  display: inline-flex;
  align-items: center;
  #cart{
    height: auto;
  }
}

.mobile{
  #cart .cart-label{
    display: none;
  }
}


#cart-items {
  z-index: 1;
  margin: var(--margin-top, 0) 0 0 0;
  position: absolute;
  transform: translate3d(calc(75% + var(--x, 0px)), calc(-100% + var(--y, 0px)), 0);
}

#cart-total{
  white-space: nowrap;
  display:flex;
  flex-direction:column;
  padding-inline: 12px;
  .cart-title{
    display: block;
  }
}

/******************************
 Cart Content
*******************************/

#cart-content{
  &::before {
    left: 100%;
    transform: translateX(-150%);
    --triangle-right: var(--triangle-right-offset, 0px);
    --triangle-left: var(--triangle-left-offset, 0px)
  }
}
html[dir="rtl"]{
  #cart-content{
    &::before {
      left: auto;
      right: 100%;
      transform: translateX(150%);
      --triangle-right: var(--triangle-right-offset, 0px);
      --triangle-left: var(--triangle-left-offset, 0px)
    }
  }
}

.cart-content {
  right: 0;
  left: auto;
  padding-top: 0;
  --cart-table-spacing: 8px;
  font-size: 13px;
  .cart-empty {
    padding: 20px;
    margin: var(--cart-table-spacing);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    &::before{
      line-height: 1;
    }
  }
  > ul {
    display: flex;
    flex-direction: column;
    background-color: white;
    list-style: none;
    margin:0;
    padding:0;
    overflow: hidden;
    .cart-products{
      border-top-left-radius: inherit;
      border-top-right-radius: inherit;
    }
    .cart-totals {
      position: relative;
      border-bottom-left-radius: inherit;
      border-bottom-right-radius: inherit;
      background-color: $f4;
    }
  }
  .cart-buttons {
    padding: calc(var(--cart-buttons-gap, 0) / 2);
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    border-width: 1px 0 0 0;
    border-bottom-right-radius: inherit;
    border-bottom-left-radius: inherit;
    a{
      min-width: min-content;
      margin:calc(var(--cart-buttons-gap, 0) / 2);
    }
  }
  img {
    max-width: 100%;
    height: auto;
  }
  table {
    //margin-top: calc(var(--cart-table-spacing-v, var(--cart-table-spacing)) * -1);
    margin-bottom: 0 !important;
    border-style: none !important;
  }
  .cart-products {
    overflow-x: hidden;
    table{
      border-bottom-width: 0 !important;
      border-top-left-radius: inherit;
      border-top-right-radius: inherit;
    }
    tbody {
      > tr {
        &:nth-of-type(odd) {
          background-color: transparent;
        }
        &:first-of-type {
          > td {
            border-top: 0 !important;
          }
        }
        &:last-of-type > td {
          //padding-bottom: 0 !important;
          //border-bottom-width: 1px;
        }
        > td {
          padding: var(--cart-table-spacing-v, var(--cart-table-spacing)) var(--cart-table-spacing) !important;
          border-width: 1px 0 0 0;
          background-color: transparent;
        }
      }
      .td-image {
        a {
          min-width: 50px;
          display: block;
        }
      }
      .td-name{
        vertical-align: middle;
        a{
          display: block;
          + br{
            display: none;
          }
        }
        span{
          display: inline-block;
          margin-top: 3px;
        }
        small {
          display: inline-flex;
          align-items: center;
          &::before {
            content: "";
            margin: 0 3px;
          }
        }
      }
      .td-qty {
        white-space: nowrap;
        padding: 0 !important;
      }
      .td-remove {
        padding:7px 5px;
        min-width: 32px;
        //padding-inline-end: 3px !important;
        button {
          color: #999;
          position: relative;
          background-color: transparent;
          padding: 2px 0 0 0;
          i {
            float: left;
          }
          &:hover {
            color: #EA2349;
          }
        }
      }
    }
  }
  .cart-totals {
    border-width: 1px 0 0 0;
    z-index: 2;
    tbody {
      td {
        border-width: 1px 0 0 0;
        padding:5px;
      }
    }
    .td-total-text {
      width: 25%;
      white-space: nowrap;
    }
  }
  .table-bordered{
    border-width: 0;
  }
}

.desktop-header-active{
  .mobile-cart-content-container{
    .cart-content ul{
      height: 100%;
      box-shadow: none;
      border-radius: 0;
      .cart-products{
        max-height: 100%;
      }
      .cart-totals{
        margin-top: auto;
      }
    }
  }
}
