@import '../variables.scss';

/******************************
 Language & Currency
*******************************/

.top-bar .top-menu {
  &.language-currency > div,
  &.language-currency > div > div,
  &.language-currency > div > div form,
  &.language-currency > div > div .dropdown button{
    height: 100%;
  }
}

.language-currency > div > div form{
  align-items: center;
  display: flex;
}

.top-menu.language-currency .dropdown.dropdown .j-menu>li>a:before{
  display: none; //hide menu style global icon
}

.language-currency {
  display: flex;
  align-items: center;
  height: 100%;
  pointer-events: none;

  .language-title,
  .currency-title,
  .currency-code{
    padding: 1px;
  }

  > div:first-of-type:not(:only-of-type) {
    margin-inline-end: var(--language-currency-spacing, 0);
  }

  .dropdown {
    pointer-events: auto;
  }

  .dropdown-toggle {
    padding: 5px;
    display: flex;
    align-items: center;
  }

  .symbol,
  .currency-symbol{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-inline-end: 5px;
    img {
      align-self: center;
    }
  }

  button {
    background-color: transparent;
    margin: 0;
    width: 100%;
    white-space: nowrap;
    padding: 7px 10px;
  }

  a {
    white-space: nowrap;
    cursor: pointer !important;
  }

  .dropdown > button > span {
    display: flex;
    align-items: center;
  }

  .dropdown-menu {
    min-width: min-content;
    > ul {
      > li {
        border-width: 1px 0 0 0;

        &:first-child {
          border-top-width: 0;
        }

        &::before {
          display: none;
        }
      }
    }
  }
  //Compact
  .header-compact &{
    pointer-events: none;

    button {
      pointer-events: auto;
    }
  }
  // Mega
  .header-mega &{
    pointer-events: none;

    button {
      pointer-events: auto;
    }
  }
}

#language, #currency {
  display: flex;
  align-items: center;

  form > .dropdown {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  button {
    flex-direction: row;
  }
}

.language-flag-title .symbol,
.language-flag {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-height: 18px;
  margin-inline-end: 4px;
  background-repeat: no-repeat;
  background-position: center center;
}
.language,
.language-flag{
  img {
    max-width: none;
    align-self: center;
  }
}

.currency-code,
.currency-code-dropdown {
  display: none;
}

.currency-select,
.language-select {
  display: flex;
  align-items: center;
}
